TimesheetUtils.validateParams = function (params) {
  var errParams = []
  if (params.pivotTableType && Object.keys(PivotTableType).indexOf(params.pivotTableType) < 0) {
    errParams.push('Unknown PivotTableType: ' + params.pivotTableType)
  }
  if (params.pivotTableType && ['TimeEntry', 'Calendar', 'PieChart'].indexOf(params.pivotTableType) >= 0) {
    errParams.push('Not supported PivotTableType: ' + params.pivotTableType)
  }
  if (params.filterOrProjectId) {
    var filterOrProjects = TimesheetUtils.wrapIntoArray(params.filterOrProjectId)
    for (var i = 0; i < filterOrProjects.length; i++) {
      if (filterOrProjects[i].indexOf('project_') !== 0 && filterOrProjects[i].indexOf('filter_') !== 0) {
        errParams.push('Unknown filterOrProjectId: ' + filterOrProjects[i])
      }
    }
  }
  if (params.numOfWeeks && parseInt(params.numOfWeeks) !== +params.numOfWeeks) {
    errParams.push('numOfWeeks should be Integer')
  }
  if (params.reportingDay && (parseInt(params.reportingDay) !== +params.reportingDay || params.reportingDay > 6 || params.reportingDay < -1)) {
    errParams.push('reportingDay should be -1..6')
  }
  if (params.offset && parseInt(params.offset) !== +params.offset) {
    errParams.push('offset should be Integer')
  }
  var startDate = params.startDate || params.start
  if (startDate && (!/^\d{4}-\d{2}-\d{2}$/.test(startDate) || isNaN(Date.parse(startDate)))) {
    errParams.push('startDate should be date in YYYY-MM-DD format')
  }
  var endDate = params.endDate || params.end
  if (endDate && (!/^\d{4}-\d{2}-\d{2}$/.test(endDate) || isNaN(Date.parse(endDate)))) {
    errParams.push('endDate should be date in YYYY-MM-DD format')
  }
  if (params.sum && ['day', 'week', 'month'].indexOf(params.sum) < 0) {
    errParams.push('sum should be in ["day", "week", "month"]')
  }
  if (params.view && ['day', 'week', 'month'].indexOf(params.view) < 0) {
    errParams.push('view should be in ["day", "week", "month"]')
  }
  return errParams.length ? errParams : null
}

TimesheetUtils.clearObject = function (obj) {
  var keys = Object.keys(obj)
  for (var i = 0; i < keys.length; i++) {
    delete obj[keys[i]]
  }
}

TimesheetUtils.showPeriodLocked = function (allowToLogFromDate) {
  AP.flag.create({
    title: 'Configuration',
    body: 'Log Work allowed from ' + moment(allowToLogFromDate).format(TimesheetUtils.dateFormat),
    type: 'error',
    close: 'auto'
  })
}

TimesheetUtils.getFirstLogWorkStarted = function (started, timeSpent, jiraConfig, configOptions) {
  started = moment(started).toDate()
  var timeSpents = TimesheetUtils.getTimeSpents(timeSpent, jiraConfig)
  for (var i = 1; i < timeSpents.length; i++) {
    do {
      started = TimesheetUtils.addDays(started, -1)
    } while (i === timeSpents.length || TimesheetUtils.isWeekend(started, configOptions))
  }
  return started
}

TimesheetUtils.mapLogWorkToStarted = function (started, timeSpent, jiraConfig, configOptions) {
  started = moment(started).startOf('day').toDate()
  const map = {}
  var timeSpents = TimesheetUtils.getTimeSpents(timeSpent, jiraConfig)
  var isWeekend = false
  for (var i = 0; i < timeSpents.length; i++) {
    do {
      if (!isWeekend) {
        map[started] = TimesheetUtils.getTimeSpentMs(timeSpents[i], jiraConfig) / 1000
      }
      started = TimesheetUtils.addDays(started, -1)
      isWeekend = TimesheetUtils.isWeekend(started, configOptions)
    } while (isWeekend)
  }
  return map
}

TimesheetUtils.isIssueEpic = function (issue) {
  return ['에픽', 'Эпик', 'Eposz', 'Episk', 'Epika', 'Epik', 'Épico', '长篇故事', 'エピック', 'Eepos', 'Epos', 'Epic']
    .indexOf(issue.fields.issuetype.name) >= 0
}

TimesheetUtils.moreFieldsForExport = function (routeParams, exportColumns, allFields) {
  var addIfNotContains = function (arr, item) {
    if (exportColumns.indexOf(item.id) === -1 && arr.indexOf(item) === -1) {
      arr.push(item)
    }
  }
  var findField = function (fieldId) {
    return TimesheetUtils.findByProperty('id', fieldId, allFields) ||
      { id: fieldId, name: fieldId }
  }
  var moreFields = []
  var field
  if (angular.isArray(routeParams.moreFields)) {
    for (var i = 0; i < routeParams.moreFields.length; i++) {
      var moreField = routeParams.moreFields[i]
      field = findField(moreField)
      addIfNotContains(moreFields, field)
    }
  } else if (routeParams.moreFields) {
    field = findField(routeParams.moreFields)
    addIfNotContains(moreFields, field)
  }
  if (routeParams.groupByField) {
    field = findField(routeParams.groupByField)
    addIfNotContains(moreFields, field)
  }
  if (routeParams.groupByField && routeParams.categorizeByField) {
    field = findField(routeParams.categorizeByField)
    addIfNotContains(moreFields, field)
  }
  return moreFields
}

