var app = angular.module('timesheetApp', ['appBase', 'xeditable', 'focus-if'])

app.run(['editableOptions', 'editableThemes', function (editableOptions, editableThemes) {
  editableOptions.theme = 'aui'
  editableThemes.aui = {
    formTpl: '<form class="aui"></form>',
    noformTpl: '<span class="editable-wrap"></span>',
    controlsTpl: '<div class="inline-edit-fields" tabindex="1"><div class="field-group"></div></div>',
    inputTpl: '',
    errorTpl: '<div class="editable-error" ng-show="$error" ng-bind="$error"></div>',
    buttonsTpl: '<span class="editable-buttons"></span>',
    submitTpl: '<button class="aui-button submit" accesskey="s" type="submit" title="Press Alt+s to submit this form">' +
                        '<span class="aui-icon aui-icon-small aui-iconfont-success">Save</span></button>',
    cancelTpl: '<button class="aui-button cancel" type="cancel" ng-click="$form.$cancel()" title="Press Alt+` to cancel">' +
                        '<span class="aui-icon aui-icon-small aui-iconfont-close-dialog">Cancel</span></button>',

    buttonsClass: '',
    inputClass: 'text long-field',
    postrender: function () {
      // apply `form-control` class to std inputs
      switch (this.directiveName) {
        case 'editableText':
        case 'editableSelect':
        case 'editableTextarea':
        case 'editableEmail':
        case 'editableTel':
        case 'editableNumber':
        case 'editableUrl':
        case 'editableSearch':
        case 'editableDate':
        case 'editableDatetime':
        case 'editableTime':
        case 'editableMonth':
        case 'editableWeek':
          this.inputEl.addClass('form-control')
          if (this.theme.inputClass) {
            // don`t apply `input-sm` and `input-lg` to select multiple
            // should be fixed in bs itself!
            if (this.inputEl.attr('multiple') &&
                (this.theme.inputClass === 'input-sm' || this.theme.inputClass === 'input-lg')) {
              break
            }
            this.inputEl.addClass(this.theme.inputClass)
          }
          break
      }

      // apply buttonsClass (bs3 specific!)
      if (this.buttonsEl && this.theme.buttonsClass) {
        this.buttonsEl.find('button').addClass(this.theme.buttonsClass)
      }
    }
  }
}])

/*
possible request parameters:
 pivotTableType (like http://localhost:5000/#!/?pivotTableType=Timsheet)
 - IssueWorkedTimeByUser
 - TimeTracking
 - Timesheet
 groupByField (like http://localhost:5000/#!/?groupByField=Status)
 requestId (like http://localhost:5000/#!/?rquestId=10000)
*/

app.config(['$routeProvider', '$windowProvider', function ($routeProvider, $windowProvider) {
  var $window = $windowProvider.$get()
  $routeProvider
    .when('/', {
      controller: $window.mainController || 'TimesheetController',
      templateUrl: $window.mainTemplate || '/templates/main.html',
      resolve: {
        loggedInUser: ['loggedInUser', function (loggedInUser) {
          return loggedInUser
        }],
        projectKey: ['projectKey', function (projectKey) {
          return projectKey
        }],
        jql: ['jql', function (jql) {
          return jql
        }],
        externalFilter: ['externalFilter', function (externalFilter) {
          return externalFilter
        }],
        timesheetParams: ['timesheetParams', function (timesheetParams) {
          return timesheetParams
        }],
        flightRecorder: ['flightRecorder', function (flightRecorder) {
          return flightRecorder
        }],
        translations: ['translations', function (translations) {
          return translations
        }],
        userConfiguration: ['loggedInUser', 'configurationService', function (loggedInUser, configurationService) {
          return loggedInUser.then(configurationService.getUserConfiguration.bind(configurationService))
        }]
      }
    })
    .otherwise({ redirectTo: '/' })
}])

app.run(['$templateRequest', '$window', function ($templateRequest, $window) {
  if ($window.b) { // not tests
    var b = $window.b
    var templates = [
      '/templates/header.html' + b,
      '/templates/main.html' + b,
      '/templates/slider.html' + b,
      '/templates/menu.html' + b,
      '/templates/dialogs/updateWorklog.html',
      '/templates/dialogs/remainingEstimate.html',
      '/templates/dialogs/deleteWorklog.html',
      '/templates/dialogs/userPreferences.html' + b,
      '/templates/dialogs/hourlyRates.html' + b,
      '/templates/popups/addWorklog.html' + b,
      '/templates/popups/editWorklog.html' + b
    ]
    if ($window.isTimesheet) {
      templates.push('/templates/popups/subscribe.html' + b)
    }
    templates.forEach(function (template) {
      // set the 2nd param to true to ignore the template request error
      $templateRequest(template, true)
    })
  }
}])

app.factory('translations', ['$i18n', '$q', '$rootScope', function ($i18n, $q, $rootScope) {
  $i18n.loadTranslation($i18n.language) // load!
  var deferred = $q.defer()
  $rootScope.$on('i18n.file.loaded', function (e, lang, section, data) {
    deferred.resolve(data)
  })
  return deferred.promise
}])

// TimesheetController waits for translations resolved, so directives do
app.factory('translationsSync', ['translations', function (translations) {
  var result = {}
  translations.then(function (data) {
    angular.extend(result, data)
  })
  return result
}])
