app.controller('UserPreferencesController', ['$scope', '$route', '$routeParams', '$location', '$modalInstance', '$q', '$sce',
  '$timeout', '$window', 'configurationService', '$http', 'loggedInUser', 'translations', 'changed',
  function ($scope, $route, $routeParams, $location, $modalInstance, $q, $sce,
    $timeout, $window, configurationService, $http, loggedInUser, translations, changed) {
    init()

    function showSaveSuccess (varName) {
      showSaveMessage(varName + 'SaveSuccess')
    }

    /* eslint-disable no-unused-vars */
    function showSaveError (varName) {
      showSaveMessage(varName + 'SaveError')
    }
    /* eslint-enable no-unused-vars */

    function showSaveMessage (varName) {
      changed.changed = true
      $scope[varName] = true
      $timeout(function () { $scope[varName] = false }, 2000)
    }

    function init () {
      $scope.isTimesheet = window.isTimesheet

      $scope.cancel = function () {
        $modalInstance.dismiss()
      }

      $scope.getDefault = function (index, from) {
        if (index && angular.isArray(from)) {
          var item = TimesheetUtils.findByProperty('id', index, from)
          if (item) {
            return item.name || item.label || ''
          }
        }
        return index || ''
      }

      $scope.saveWeekendType = function () {
        var weekendType = angular.copy($scope.weekendType)
        if (weekendType.val === 'default') { // do not store default value
          weekendType.val = null
        }
        configurationService.saveUserPreferences(weekendType, loggedInUser).then(function (config) {
          if (!config.val) {
            config.val = 'default'
          }
          $scope.weekendType = config
          showSaveSuccess('weekendType')
        })
      }

      $scope.saveWorkingTimeInStatus = function () {
        if ($scope.workingTimeInStatus.val && !$scope.workingTimeInStatus.val.from && !$scope.workingTimeInStatus.val.to) {
          $scope.workingTimeInStatus.val = null
        }
        configurationService.saveUserPreferences($scope.workingTimeInStatus, loggedInUser).then(function (config) {
          $scope.workingTimeInStatus = config
          showSaveSuccess('workingTimeInStatus')
        })
      }

      $scope.saveDurationType = function () {
        if ($scope.durationType.val === 'default') { // do not store default value
          $scope.durationType.val = null
        }
        configurationService.saveUserPreferences($scope.durationType, loggedInUser).then(function (config) {
          $scope.durationType = config
          if (!$scope.durationType.val) {
            $scope.durationType.val = 'default'
          }
          showSaveSuccess('durationType')
        })
      }

      $scope.saveDurationTypeForExport = function () {
        if ($scope.durationTypeForExport.val === 'default') { // do not store default value
          $scope.durationTypeForExport.val = null
        }
        configurationService.saveUserPreferences($scope.durationTypeForExport, loggedInUser).then(function (config) {
          $scope.durationTypeForExport = config
          if (!$scope.durationTypeForExport.val) {
            $scope.durationTypeForExport.val = 'default'
          }
          showSaveSuccess('durationTypeForExport')
        })
      }

      $scope.saveMaxFractionDigits = function () {
        configurationService.saveUserPreferences($scope.maxFractionDigits, loggedInUser).then(function (config) {
          $scope.maxFractionDigits = config
          showSaveSuccess('maxFractionDigits')
        })
      }

      $scope.saveTimeInStatusCategories = function () {
        $scope.timeInStatusCategories.val = $scope.timeInStatusCategories.val ? $scope.timeInStatusCategories.val.split(',') : undefined
        configurationService.saveUserPreferences($scope.timeInStatusCategories, loggedInUser).then(function (config) {
          $scope.timeInStatusCategories = config
          showSaveSuccess('timeInStatusCategories')
        })
      }

      $scope.saveTimeTrackingColumns = function () {
        $scope.timeTrackingColumns.val = $scope.timeTrackingColumns.val ? $scope.timeTrackingColumns.val.split(',') : undefined
        configurationService.saveUserPreferences($scope.timeTrackingColumns, loggedInUser).then(function (config) {
          $scope.timeTrackingColumns = config
          showSaveSuccess('timeTrackingColumns')
        })
      }

      $scope.saveTimeBalanceColumns = function () {
        $scope.timeBalanceColumns.val = $scope.timeBalanceColumns.val ? $scope.timeBalanceColumns.val.split(',') : undefined
        configurationService.saveUserPreferences($scope.timeBalanceColumns, loggedInUser).then(function (config) {
          $scope.timeBalanceColumns = config
          showSaveSuccess('timeBalanceColumns')
        })
      }

      $scope.saveShowDetailsCount = function () {
        configurationService.saveUserPreferences($scope.showDetailsCount, loggedInUser).then(function (config) {
          $scope.showDetailsCount = config
          showSaveSuccess('showDetailsCount')
        })
      }

      $scope.saveShowCategoryRow = function () {
        configurationService.saveUserPreferences($scope.showCategoryRow, loggedInUser).then(function (config) {
          $scope.showCategoryRow = config
          showSaveSuccess('showCategoryRow')
        })
      }

      $scope.saveShowTotalInFront = function () {
        configurationService.saveUserPreferences($scope.showTotalInFront, loggedInUser).then(function (config) {
          $scope.showTotalInFront = config
          showSaveSuccess('showTotalInFront')
        })
      }

      $scope.saveOnErrorIgnore = function () {
        configurationService.saveUserPreferences($scope.onErrorIgnore, loggedInUser).then(function (config) {
          $scope.onErrorIgnore = config
          showSaveSuccess('onErrorIgnore')
        })
      }

      $scope.saveDateFields = function () {
        $scope.dateFields.val = $scope.dateFields.val ? $scope.dateFields.val.split(',') : undefined
        configurationService.saveUserPreferences($scope.dateFields, loggedInUser).then(function (config) {
          $scope.dateFields = config
          showSaveSuccess('dateFields')
        })
      }

      $scope.saveExportColumns = function () {
        $scope.exportColumns.val = $scope.exportColumns.val ? $scope.exportColumns.val.split(',') : undefined
        configurationService.saveUserPreferences($scope.exportColumns, loggedInUser).then(function (config) {
          $scope.exportColumns = config
          showSaveSuccess('exportColumns')
        })
      }

      $scope.saveReportingDay = function () {
        configurationService.saveUserPreferences($scope.reportingDay, loggedInUser).then(function (config) {
          $scope.reportingDay = config
          showSaveSuccess('reportingDay')
        })
      }

      $scope.saveStartedTimeInStatus = function () {
        configurationService.saveUserPreferences($scope.startedTimeInStatus, loggedInUser).then(function (config) {
          $scope.startedTimeInStatus = config
          showSaveSuccess('startedTimeInStatus')
        })
      }

      $scope.saveInProgressIssuesJql = function () {
        configurationService.saveUserPreferences($scope.inProgressIssuesJql, loggedInUser).then(function (config) {
          $scope.inProgressIssuesJql = config
          showSaveSuccess('inProgressIssuesJql')
        })
      }

      $scope.saveTempoToken = function () {
        configurationService.saveUserPreferences($scope.tempoToken, loggedInUser).then(function (config) {
          $scope.tempoToken = config
          showSaveSuccess('tempoToken')
        })
      }

      $scope.sendMobileLink = function () {
        $http.post('/api/1/mobile/get_link')
          .then(function () {
            AP.flag.create({
              title: 'Mobile link',
              body: 'Message has been queued',
              type: 'success',
              close: 'auto'
            })
          }, function (err) {
            AP.flag.create({
              title: 'Mobile link',
              body: err.data || err || 'Something went wrong',
              type: 'error',
              close: 'auto'
            })
          })
      }

      $scope.emailAddress = loggedInUser.emailAddress
      var $config = configurationService.getConfiguration()
      var $userPreferences = configurationService.getUserPreferences(loggedInUser)
      var $statusCategories = configurationService.getAllStatusCategories()

      $q.all([$config, $userPreferences, $statusCategories]).then(function (args) {
        var config = args[0]
        var userPreferences = args[1]
        var statusCategories = args[2]

        var defaultText = ' [' + translations['Default'] + ']'

        if (config.workingTimeInStatus.val && config.workingTimeInStatus.val.from && config.workingTimeInStatus.val.to) {
          config.workingTimeInStatus.defaultFrom = '' + config.workingTimeInStatus.val.from + defaultText
          config.workingTimeInStatus.defaultTo = '' + config.workingTimeInStatus.val.to + defaultText
        }
        if (config.maxFractionDigits.val) {
          config.maxFractionDigits.default = '' + config.maxFractionDigits.val + defaultText
        }
        if (config.inProgressIssuesJql.val) {
          config.inProgressIssuesJql.default = '' + config.inProgressIssuesJql.val + defaultText
        }
        $scope.defaults = config

        $scope.weekendType = userPreferences.weekendType
        $scope.workingTimeInStatus = userPreferences.workingTimeInStatus
        $scope.durationType = userPreferences.durationType
        $scope.durationTypeForExport = userPreferences.durationTypeForExport
        $scope.maxFractionDigits = userPreferences.maxFractionDigits
        $scope.timeInStatusCategories = userPreferences.timeInStatusCategories || config.timeInStatusCategories
        $scope.timeTrackingColumns = userPreferences.timeTrackingColumns || config.timeTrackingColumns
        $scope.timeBalanceColumns = userPreferences.timeBalanceColumns || config.timeBalanceColumns
        $scope.exportColumns = userPreferences.exportColumns || config.exportColumns
        $scope.dateFields = userPreferences.dateFields || config.dateFields
        $scope.reportingDay = userPreferences.reportingDay
        $scope.startedTimeInStatus = userPreferences.startedTimeInStatus
        $scope.inProgressIssuesJql = userPreferences.inProgressIssuesJql
        $scope.showDetailsCount = userPreferences.showDetailsCount
        $scope.showCategoryRow = userPreferences.showCategoryRow
        $scope.showTotalInFront = userPreferences.showTotalInFront
        $scope.onErrorIgnore = userPreferences.onErrorIgnore
        $scope.tempoToken = userPreferences.tempoToken
        if (!$scope.onErrorIgnore) {
          $scope.onErrorIgnore = config.onErrorIgnore
        }

        $scope.timeInStatusCategoriesData = new TimesheetSelectOptions(TimesheetGeneralOption, $scope.timeInStatusCategories.val)
        for (var i in statusCategories) {
          var statusCategory = statusCategories[i]
          $scope.timeInStatusCategoriesData.addOption(statusCategory.name, statusCategory.id)
        }

        $scope.timeTrackingColumnsData = new TimesheetSelectOptions(TimesheetGeneralOption, $scope.timeTrackingColumns.val)
        var timeTrackingColumns = configurationService.getAllTimeTrackingColumns()
        for (var i in timeTrackingColumns) {
          $scope.timeTrackingColumnsData.addOption(translations[timeTrackingColumns[i]], timeTrackingColumns[i])
        }

        $scope.timeBalanceColumnsData = new TimesheetSelectOptions(TimesheetGeneralOption, $scope.timeBalanceColumns.val)
        var timeBalanceColumns = configurationService.getAllTimeBalanceColumns()
        for (var i in timeBalanceColumns) {
          $scope.timeBalanceColumnsData.addOption(translations[timeBalanceColumns[i]], timeBalanceColumns[i])
        }

        $scope.exportColumnsData = new TimesheetSelectOptions(TimesheetGeneralOption, $scope.exportColumns.val)
        var exportColumns = configurationService.getAllExportColumns()
        for (var i in exportColumns) {
          $scope.exportColumnsData.addOption(translations[exportColumns[i]], exportColumns[i])
        }

        $scope.dateFieldsData = new TimesheetSelectOptions(TimesheetGeneralOption, $scope.dateFields.val)
        var dateFields = configurationService.getAllDateFields()
        for (var i in dateFields) {
          $scope.dateFieldsData.addOption(translations[dateFields[i]], dateFields[i])
        }

        $scope.showDetailsCountTypes = []
        $scope.showDetailsCountTypes.push({ id: undefined, label: (config.showDetailsCount.val ? translations['Yes'] : translations['No']) + defaultText })
        $scope.showDetailsCountTypes.push({ id: true, label: translations['Yes'] })
        $scope.showDetailsCountTypes.push({ id: false, label: translations['No'] })

        $scope.showCategoryRowTypes = []
        $scope.showCategoryRowTypes.push({ id: undefined, label: (config.showCategoryRow.val ? translations['Yes'] : translations['No']) + defaultText })
        $scope.showCategoryRowTypes.push({ id: true, label: translations['Yes'] })
        $scope.showCategoryRowTypes.push({ id: false, label: translations['No'] })

        $scope.showTotalInFrontTypes = []
        $scope.showTotalInFrontTypes.push({ id: undefined, label: (config.showTotalInFront.val ? translations['Yes'] : translations['No']) + defaultText })
        $scope.showTotalInFrontTypes.push({ id: true, label: translations['Yes'] })
        $scope.showTotalInFrontTypes.push({ id: false, label: translations['No'] })

        $scope.reportingDayOptions = new TimesheetSelectOptions(TimesheetGeneralOption, $scope.reportingDay.val)
        $scope.reportingDayOptions.addOption('', undefined)
        $scope.reportingDayOptions.addOption(translations['Today'], -1)
        var day = moment()
        for (var i = 0; i < 7; i++) {
          var d = (moment.localeData().firstDayOfWeek() + i) % 7 // in the order of locale week
          day.day(d)
          $scope.reportingDayOptions.addOption(moment.localeData().weekdays(day), d)
        }
        if (config.reportingDay.val) {
          $scope.reportingDayOptions.addOption('' + $scope.getDefault(config.reportingDay.val, $scope.reportingDayOptions.options) + defaultText, 'default', true)
        }
        $scope.startedTimeInStatusTypes = []
        $scope.startedTimeInStatusTypes.push({ id: undefined, label: (config.startedTimeInStatus.val ? translations['Yes'] : translations['No']) + defaultText })
        $scope.startedTimeInStatusTypes.push({ id: true, label: translations['Yes'] })
        $scope.startedTimeInStatusTypes.push({ id: false, label: translations['No'] })

        $scope.allWeekendTypes = []
        $scope.allWeekendTypes.push({ id: '60', label: 'Sat-Sun' }) // default
        $scope.allWeekendTypes.push({ id: '56', label: 'Fri-Sat' }) // Islamic
        $scope.allWeekendTypes.push({ id: '45', label: 'Thu-Fri' }) // Arabic
        $scope.allWeekendTypes.push({ id: '0', label: 'Sun' }) // Indian
        $scope.allWeekendTypes.push({ id: '5', label: 'Fri' }) // Solar
        $scope.allWeekendTypes.push({ id: '560', label: 'Fri-Sun' }) // 4 10 hours
        if (config.weekendType.val) {
          $scope.allWeekendTypes.unshift({ id: 'default', label: '' + $scope.getDefault(config.weekendType.val, $scope.allWeekendTypes) + defaultText })
        }
        $scope.allDurationTypes = []
        $scope.allDurationTypes.push({ id: 'm', label: 'Minutes' })
        $scope.allDurationTypes.push({ id: 'h', label: 'Hours' })
        $scope.allDurationTypes.push({ id: 'd', label: 'Days' })
        $scope.allDurationTypes.push({ id: 'pretty', label: 'Pretty Duration' })
        if (config.durationType.val) {
          $scope.allDurationTypes.unshift({ id: 'default', label: '' + $scope.getDefault(config.durationType.val, $scope.allDurationTypes) + defaultText })
        }
        $scope.allDurationTypesForExport = []
        $scope.allDurationTypesForExport.push({ id: 'm', label: 'Minutes' })
        $scope.allDurationTypesForExport.push({ id: 'h', label: 'Hours' })
        $scope.allDurationTypesForExport.push({ id: 'd', label: 'Days' })
        if (config.durationTypeForExport.val) {
          $scope.allDurationTypesForExport.unshift({ id: 'default', label: '' + $scope.getDefault(config.durationTypeForExport.val, $scope.allDurationTypesForExport) + defaultText })
        }
        $scope.problemReporter = $window.mailerUrl != null
      })
    }
  }
])
