app.controller('WorklogDialogController', ['$scope', '$q', 'pivottableService',
  'configurationService', '$modalInstance', 'issueKey', 'worklog', 'pivotTableType', 'loggedInUser', 'jiraConfig',
  function ($scope, $q, pivottableService, configurationService,
    $modalInstance, issueKey, worklog, pivotTableType, currentUser, jiraConfig) {
    $scope.cancel = function () {
      $modalInstance.dismiss()
    }

    $scope.closeDialog = function (worklog) {
      $scope.loading = true
      worklog.deferred = $q.defer()
      $modalInstance.close(worklog)
      worklog.deferred.promise.then(function (savedWorklog) {
        if ($scope.standaloneDialog) {
          AP.events.emitPublic('LoggedTime', savedWorklog)
          AP.dialog.close()
        }
      }).catch(function () {
        delete $scope.loading
      })
    }

    $scope.toggleShowMore = function () {
      $scope.showMore = !$scope.showMore
    }

    $scope.issuePickerOptions = { selected: '', issues: [] }

    $scope.isTimesheet = window.isTimesheet
    $scope.loggedInUser = currentUser
    $scope.worklog = worklog
    $scope.standaloneDialog = worklog.standaloneDialog
    $scope.dialogType = worklog.initialWorkLabels ? worklog.id ? 'update' : 'add' : 'delete'
    $scope.pivotTableType = pivotTableType
    $scope.jiraConfig = jiraConfig
    $scope.issueKey = issueKey
    $scope.showMore = $scope.standaloneDialog || !$scope.issueKey

    $scope.calculateWorklogDate = function (startOfDay) {
      var newStart = moment()
      if ($scope.worklogDatetime.date != null) {
        newStart = moment($scope.worklogDatetime.date).startOf('day')
        if ($scope.worklogDatetime.time != null) {
          $scope.worklogDatetime.time.timePartsToMoment()
          newStart.add($scope.worklogDatetime.time.momentTime)
        }
      } else if ($scope.worklog.started != null) {
        newStart = moment($scope.worklog.started)
      }
      return startOfDay ? newStart.startOf('day').toDate() : newStart.toDate()
    }

    var populateDatetime = function (datetimeObj, dateString, timezone) {
      var momentDate = moment(TimesheetUtils.convertDate(dateString, timezone))
      datetimeObj.date = momentDate.toDate()
      datetimeObj.time.momentTime = momentDate
      datetimeObj.time.momentToTimeParts()
    }

    var setEstimatePlaceholder = function () {
      $scope.estimatePlaceholder = DrawHelper.getFormattedTime(
        Math.max(0, ($scope.timeestimate || 0) -
                    TimesheetUtils.getTimeSpentMs(worklog.timeSpent || 0, jiraConfig) / 1000 +
                    ($scope.worklogTimeSpentSeconds || 0)) +
                    ($scope.dialogType === 'delete' ? $scope.worklogTimeSpentSeconds : 0),
        jiraConfig, null, 'jiraStyle')
    }

    $scope.save = function () {
      if (TimesheetUtils.getFirstLogWorkStarted($scope.calculateWorklogDate(), worklog.timeSpent, jiraConfig, $scope.configOptions) < $scope.allowToLogFromDate) {
        TimesheetUtils.showPeriodLocked($scope.allowToLogFromDate)
        return
      }
      $scope.worklog.date = $scope.calculateWorklogDate()
      $scope.worklog.started = TimesheetUtils.formatDate($scope.worklog.date)
      $scope.worklog.timeSpentSeconds = TimesheetUtils.getTimeSpentMs(worklog.timeSpent, jiraConfig) / 1000
      worklog.estimateType = worklog.estimateValue ? 'new' : 'auto'
      $scope.closeDialog($scope.worklog)
    }

    $scope.delete = function () {
      worklog.estimateType = worklog.estimateValue ? 'new' : 'auto'
      $scope.closeDialog($scope.worklog)
    }

    angular.extend(worklog, {
      estimateType: 'auto',
      estimateValue: ''
    })

    configurationService.getUserConfiguration(currentUser).then(function (config) {
      if (config.disableLogWork.val) {
        $scope.cancel()
      }
      $scope.workLabelsList = config.workLabels.val
      if (worklog.initialWorkLabels) {
        var labels = Object.keys(worklog.initialWorkLabels)
        $scope.workLabelsList =
                  $scope.workLabelsList
                    ? TimesheetUtils.uniqueArray($scope.workLabelsList.concat(labels))
                    : labels
      }
      $scope.workDescriptionRequired = config.workDescriptionRequired.val
      $scope.visibilityGroup = config.worklogVisibilityGroup.val
      $scope.visibilityRole = config.worklogVisibilityRole.val
      $scope.allowToLogFromDate = config.allowToLogFromDate.val
      $scope.workingTimeInStatus = config.workingTimeInStatus.val
      $scope.configOptions = {
        weekendType: config.weekendType.val,
        restrictWorkPerDay: config.restrictWorkPerDay.val
      }
      pivottableService.clearWorklogCache(config)
    })

    $scope.worklogDatetime = {
      date: '',
      time: {
        momentTime: null,
        timeParts: [],
        momentToTimeParts: function () {
          if (this.momentTime == null) return
          var regExp = /^(.*?)(\d+):(\d+)(.*?)$/
          var match = regExp.exec(this.momentTime.format('LT'))
          if (!match) {
            match = regExp.exec(this.momentTime.locale('en').format('LT'))
          }
          var items = []
          var format24 = match[1] === '' && match[4] === ''
          if (match[1] !== '') {
            items.push({
              value: match[1],
              step: 12
            })
          }
          items.push({
            value: match[2],
            step: 1,
            min: format24 ? 0 : 1,
            max: format24 ? 23 : 12,
            suffix: ':'
          })
          items.push({
            value: match[3],
            step: 5,
            min: 0,
            max: 59
          })
          if (match[4] !== '') {
            items.push({
              value: match[4],
              step: 12
            })
          }
          this.timeParts = items
        },
        timePartsToMoment: function () {
          if (this.timeParts != null) {
            var time = ''
            for (var i = 0; i < this.timeParts.length; i++) {
              time += this.timeParts[i].value
              if (this.timeParts[i].suffix != null) {
                time += this.timeParts[i].suffix
              }
            }
            this.momentTime = moment(time, 'LT').isValid() ? moment(time, 'LT') : moment(time, 'LT', 'en')
          }
        }
      }
    }

    $scope.groups = currentUser.groups

    $scope.$watch('worklog.issueKey', function (newValue) {
      if (newValue) {
        if (!$scope.worklog.user && $scope.dialogType !== 'delete') {
          var projectKey = newValue.split('-')[0]
          pivottableService.isProjectAdmin(projectKey).then(function (isAdmin) { // FIXME: ACJIRA-424
            if (isAdmin) { // avoid error in console
              configurationService.getCurrentMemberProjectRoles(projectKey).then(function (roles) {
                $scope.roles = roles
              })
            } else {
              $scope.roles = null
            }
          })
        }
        pivottableService.findIssueByKey(newValue, { _fields: 'timespent,timeestimate,timeoriginalestimate' }).then(function (issue) {
          $scope.timeestimate = issue.fields.timeestimate
          $scope.originalEstimate = DrawHelper.getFormattedTime(issue.fields.timeoriginalestimate, jiraConfig)
          $scope.timeestimatePretty = DrawHelper.getFormattedTime($scope.timeestimate, jiraConfig, null, 'jiraStyle')
          $scope.timespentPretty = DrawHelper.getFormattedTime(issue.fields.timespent, jiraConfig)
          setEstimatePlaceholder()
        })
      }
    })

    $scope.changeDate = function () {
      $scope.worklogDatetime.dateChanged = true
    }

    $scope.$watch('worklog.timeSpent', function () {
      setEstimatePlaceholder()
      if (!$scope.worklog.id && TimesheetUtils.sameDay(moment(worklog.started).toDate(), new Date()) &&
          !$scope.worklogDatetime.time.changed && !$scope.worklogDatetime.dateChanged) {
        var startOfWorkingDay = Math.min(TimesheetUtils.getCurrentTimeMs() / 1000, ($scope.workingTimeInStatus.from || 9) * 60 * 60)
        var secondsWorked = TimesheetUtils.getTimeSpentMs(worklog.timeSpent || 0, jiraConfig) / 1000
        populateDatetime($scope.worklogDatetime,
          moment(worklog.started)
            .startOf('day')
            .add(Math.max(startOfWorkingDay, TimesheetUtils.getCurrentTimeMs() / 1000 - secondsWorked), 'seconds')
            .toJSON(), null)
      }
    })
    $scope.$watch('worklog.estimateValue', setEstimatePlaceholder)

    if (worklog.id != null) {
      pivottableService.getWorklog(worklog.issueKey, worklog.id).then(function (worklog) {
        worklog.visibilityGroup = worklog.visibility && worklog.visibility.type === 'group'
          ? worklog.visibility.value : ''
        worklog.visibilityRole = worklog.visibility && worklog.visibility.type === 'role'
          ? worklog.visibility.value : ''
        angular.extend($scope.worklog, worklog)
        var timeZone = worklog.author ? worklog.author.timeZone : null
        populateDatetime($scope.worklogDatetime, worklog.started, timeZone)
        $scope.worklogTimeSpentSeconds = worklog.timeSpentSeconds || 0
        if ($scope.calculateWorklogDate() < $scope.allowToLogFromDate) {
          TimesheetUtils.showPeriodLocked($scope.allowToLogFromDate)
          $scope.cancel()
        }
      })
    } else {
      $scope.$watch('visibilityGroup', function (newValue) {
        $scope.worklog.visibilityGroup = null
        if (newValue) {
          var visibilityGroups = newValue.split(',')
          for (var i = 0; i < $scope.groups.items.length; i++) {
            var group = $scope.groups.items[i].name
            if (visibilityGroups.indexOf(group) >= 0) {
              $scope.worklog.visibilityGroup = group
              break
            }
          }
        }
      })
      var unwatchRoles
      $scope.$watch('visibilityRole', function (newValue) {
        if (newValue) {
          var visibilityRoles = newValue.split(',')
          if (unwatchRoles) {
            unwatchRoles()
          }
          unwatchRoles = $scope.$watch('roles', function (roles) {
            $scope.worklog.visibilityRole = null
            if (roles) {
              for (var role in roles) {
                if (visibilityRoles.indexOf(role) >= 0) {
                  $scope.worklog.visibilityRole = role
                  break
                }
              }
            }
          })
        }
      })
      populateDatetime($scope.worklogDatetime, worklog.started, null)
      $scope.worklogDatetime.time.changed = worklog.timeSpentSeconds != null
    }
    // FIXME: $scope.jqlQuery = params.jqlQuery; // addworklog only
    if ($scope.standaloneDialog) {
      AP.events.on('dialog.submit', $scope.save)
      AP.dialog.disableCloseOnSubmit()
    }
  }
])
