function PivotEntry (rowKey, columnKey, value) {
  this.rowKey = rowKey
  this.columnKey = columnKey
  this.columnKeys = [columnKey]
  this.value = value
  this.values = {}
  columnKey && columnKey.keyValue && (this.values[columnKey.keyValue] = value)
}
PivotEntry.prototype.addColumn = function (columnKey, value) {
  this.columnKeys.push(columnKey)
  this.values[columnKey.keyValue] = value
}
function WorkedTimePivotEntry (rowKey, columnKey, worklog) {
  PivotEntry.apply(this, [rowKey, columnKey, worklog ? worklog.timeSpentSeconds : 0])
  this.worklog = worklog
}
WorkedTimePivotEntry.prototype = new PivotEntry()
function EmptyPivotEntry (rowKey) {
  this.rowKey = rowKey
  this.columnKey = {}
  this.columnKeys = []
  this.values = {}
}

if (typeof module === 'object' && module.exports) {
  module.exports.PivotEntry = PivotEntry
  module.exports.WorkedTimePivotEntry = WorkedTimePivotEntry
  module.exports.EmptyPivotEntry = EmptyPivotEntry
}
