if (typeof module === 'object' && module.exports) {
  var PivotKey = require('./PivotKey')
}

function WorkedTimePivotStrategy (rowKeyClass, columnKeyClass, options) {
  this.rowKeyClass = rowKeyClass
  this.columnKeyClass = columnKeyClass
  this.options = WorkedTimePivotStrategy.getStrategyOptions(options)
  this.configOptions = options.configOptions
}

WorkedTimePivotStrategy.getStrategyOptions = function (options) {
  var result = {
    startDate: options.startDate ? moment(options.startDate).toDate() : null,
    endDate: options.endDate ? moment(options.endDate).toDate() : null
  }
  if (options.worklogAuthors) {
    result.worklogAuthors = options.worklogAuthors
  } else if (options.user) { // PieChart
    result.worklogAuthors = [options.user]
  }
  if (options.restrictedProjects) {
    result.restrictedProjects = options.restrictedProjects
  }
  result.loggedInUser = options.loggedInUser
  result.groupByField = options.groupByField
  result.groupByFieldObject = options.groupByFieldObject
  result.categorizeByField = options.categorizeByField
  result.categorizeByFieldObject = options.categorizeByFieldObject
  result.orderByField = options.orderByField
  result.pivotByStatus = options.pivotTableType === 'IssueWorkedTimeByStatus' // spread timespent over statuses
  result.includeEmpty = options.includeEmpty
  result.userInfos = options.userInfos
  return result
}

WorkedTimePivotStrategy.prototype.getEntries = function (issue) {
  var entries = []
  var nonEmptySubtasks = []
  var issueWorklog = issue.worklog || issue.fields.worklog
  for (var i = 0; i < issueWorklog.worklogs.length; i++) {
    var worklog = issueWorklog.worklogs[i]
    var rowKey = new this.rowKeyClass(issue, worklog, this.options)
    var inRange = true
    var workDate = worklog.workDate = TimesheetUtils.convertDate(worklog.started,
      !this.configOptions.preserveStartedTime && worklog.author ? worklog.author.timeZone : this.options.loggedInUser.timeZone)
    if (this.options.startDate) {
      inRange = workDate >= this.options.startDate
    }
    if (this.options.endDate) {
      var endDateIncluded = TimesheetUtils.addDays(this.options.endDate, 1) // endDate should be in range
      inRange = inRange && (workDate < endDateIncluded)
    }
    var inGroups = (!this.options.worklogAuthors || // filtering by author
            (worklog.author && this.options.worklogAuthors.indexOf(worklog.author.accountId) >= 0))
    var restricted = this.options.restrictedProjects ? this.options.restrictedProjects.indexOf(worklog._issue ? worklog._issue.fields.project.key : issue.fields.project.key) >= 0 : false
    restricted = restricted && (!worklog.author || (worklog.author.accountId !== this.options.loggedInUser.accountId))
    if (inRange && inGroups && !restricted) {
      var entry
      if (this.options.pivotByStatus) {
        var columnKeys = PivotKey.getStatusKeys(worklog, issue, this.configOptions)
        entry = new PivotEntry(rowKey, columnKeys[0].key, worklog.timeSpentSeconds)
        entry.values[columnKeys[0].key.keyValue] = columnKeys[0].timespent // hack totals
        entry.worklog = worklog
        for (var j = 1; j < columnKeys.length; j++) {
          entry.addColumn(columnKeys[j].key, columnKeys[j].timespent)
        }
      } else {
        var columnKey = new this.columnKeyClass(worklog, issue, this.options)
        if (this.options.includeEmpty && /* sumSubTasks */ issue._subTasks) {
          rowKey._issue = issue // trigger summing numeric fields
          if (worklog._issue && nonEmptySubtasks.indexOf(worklog._issue.key) < 0) {
            nonEmptySubtasks.push(worklog._issue.key)
            // sum numeric fields child issues with worklogs
            for (var issueField in worklog._issue.fields) {
              var field = worklog._issue.fields[issueField]
              if (field && typeof field === 'number') {
                issue.fields[issueField] = (issue.fields[issueField] || 0) + field
              }
            }
          }
        }
        entry = new WorkedTimePivotEntry(rowKey, columnKey, worklog)
      }
      entries.push(entry)
    }
  }
  if (!this.options.pivotByStatus && this.options.includeEmpty && ['workeduser', 'workedusergroup', 'workeddate', 'workedweek', 'workcreated', 'commentfirstword', 'worklabel'].indexOf(this.options.groupByField) < 0) {
    var rowKey = new this.rowKeyClass(issue, null, this.options)
    var addEntry = function (issue, value) {
      rowKey = TimesheetUtils.clone(rowKey)
      rowKey._issue = issue
      var entry = new EmptyPivotEntry(rowKey)
      entry.value = value
      entries.push(entry)
    }
    if (!entries.length) {
      rowKey._issue = issue // trigger summing numeric fields
      addEntry(issue, this.options.groupByField ? {} : undefined)
    }
    if (issue._subTasks && issue._subTasks.length > 0) {
      issue._subTasks.forEach(function (issue) {
        if (nonEmptySubtasks.indexOf(issue.key) < 0) {
          addEntry(issue, {})
        }
      })
    }
  }
  return entries
}

function LabelPivotStrategy (rowKeyClass, columnKeyClass, options) {
  this.rowKeyClass = rowKeyClass
  this.columnKeyClass = columnKeyClass
  this.options = LabelPivotStrategy.getStrategyOptions(options)
  this.configOptions = options.configOptions
}

LabelPivotStrategy.getStrategyOptions = function (options) {
  var result = {
    startDate: options.startDate ? moment(options.startDate).toDate() : null,
    endDate: options.endDate ? moment(options.endDate).toDate() : null
  }
  if (options.worklogAuthors) {
    result.worklogAuthors = options.worklogAuthors
  } else if (options.user) {
    result.worklogAuthors = [options.user]
  }
  if (options.restrictedProjects) {
    result.restrictedProjects = options.restrictedProjects
  }
  result.loggedInUser = options.loggedInUser
  result.groupByField = options.groupByField
  result.groupByFieldObject = options.groupByFieldObject
  result.categorizeByField = options.categorizeByField
  result.categorizeByFieldObject = options.categorizeByFieldObject
  result.orderByField = options.orderByField
  result.includeEmpty = options.includeEmpty
  result.userInfos = options.userInfos
  return result
}

LabelPivotStrategy.prototype.getEntries = function (issue) {
  var entries = []
  var issueWorklog = issue.worklog || issue.fields.worklog
  for (var i = 0; i < issueWorklog.worklogs.length; i++) {
    var worklog = issueWorklog.worklogs[i]
    var rowKey = new this.rowKeyClass(issue, worklog, this.options)
    var inRange = true
    var workDate = worklog.workDate = TimesheetUtils.convertDate(worklog.started,
      !this.configOptions.preserveStartedTime && worklog.author ? worklog.author.timeZone : this.options.loggedInUser.timeZone)
    if (this.options.startDate) {
      inRange = workDate >= this.options.startDate
    }
    if (this.options.endDate) {
      var endDateIncluded = TimesheetUtils.addDays(this.options.endDate, 1) // endDate should be in range
      inRange = inRange && (workDate < endDateIncluded)
    }
    var inGroups = (!this.options.worklogAuthors || // filtering by author
            (worklog.author && this.options.worklogAuthors.indexOf(worklog.author.accountId) >= 0))
    var restricted = this.options.restrictedProjects ? this.options.restrictedProjects.indexOf(worklog._issue ? worklog._issue.fields.project.key : issue.fields.project.key) >= 0 : false
    restricted = restricted && (!worklog.author || (worklog.author.accountId !== this.options.loggedInUser.accountId))
    if (inRange && inGroups && !restricted) {
      var entry
      var columnKeys = PivotKey.getLabelKeys(worklog, issue, this.configOptions)
      entry = new PivotEntry(rowKey, columnKeys[0].key, worklog.timeSpentSeconds)
      entry.values[columnKeys[0].key.keyValue] = columnKeys[0].timespent // hack totals
      entry.worklog = worklog
      for (var j = 1; j < columnKeys.length; j++) {
        entry.addColumn(columnKeys[j].key, columnKeys[j].timespent)
      }
      entries.push(entry)
    }
  }
  return entries
}

function TimeEntryStrategy (rowKeyClass, options) {
  this.rowKeyClass = rowKeyClass
  this.options = {
    startDate: options.startDate,
    endDate: options.endDate,
    worklogAuthors: [options.user],
    includeEmpty: options.includeEmpty,
    orderByField: options.orderByField,
    userInfos: options.userInfos
  }
  this.configOptions = options.configOptions
}
TimeEntryStrategy.prototype.addEntry = function (entries, rowKey, worklog, workDate, timeSpentSeconds) {
  var startedColumnKey = new PivotKey.Generic('TimeEntry', '1started', 'time')
  var timespentColumnKey = new PivotKey.Generic('TimeEntry', '2timespent')
  var actionColumnKey = new PivotKey.Generic('TimeEntry', '3action')
  actionColumnKey.showStart = TimesheetUtils.sameDay(this.options.startDate, new Date())
  var entry = new PivotEntry(rowKey, startedColumnKey, workDate)
  entry.worklog = worklog
  entry.addColumn(timespentColumnKey, timeSpentSeconds)
  entry.addColumn(actionColumnKey, entry)
  entries.push(entry)
}
TimeEntryStrategy.prototype.getEntries = function (issue) {
  var entries = []
  var issueWorklog = issue.worklog || issue.fields.worklog
  for (var i = 0; i < issueWorklog.worklogs.length; i++) {
    var worklog = issueWorklog.worklogs[i]
    var rowKey = new this.rowKeyClass(issue, worklog, this.options)
    var inRange = true
    var workDate = worklog.workDate = TimesheetUtils.convertDate(worklog.started, worklog.author ? worklog.author.timeZone : null)
    if (this.options.startDate) {
      inRange = workDate >= this.options.startDate
    }
    if (this.options.endDate) {
      var endDateIncluded = TimesheetUtils.addDays(this.options.endDate, 1) // endDate should be in range
      inRange = inRange && (workDate < endDateIncluded)
    }
    var inGroups = (!this.options.worklogAuthors || // filtering by author
            (worklog.author && this.options.worklogAuthors.indexOf(worklog.author.accountId) >= 0))
    if (inRange && inGroups) {
      this.addEntry(entries, rowKey, worklog, workDate, worklog.timeSpentSeconds)
    }
  }
  if ((this.options.includeEmpty || issue._inProgressIssue) && !entries.length) {
    var rowKey = new this.rowKeyClass(issue, null, this.options)
    this.addEntry(entries, rowKey)
  }
  return entries
}

