IssueTimeStrategy.prototype.getColumnsFunction = function () {
  var result = {}
  var startedColumnKey = new PivotKey.Generic('TimeEntry', '1started', 'time')
  var timespentColumnKey = new PivotKey.Generic('TimeEntry', '2timespent')
  result[startedColumnKey.keyValue] = new PivotColumn(startedColumnKey)
  result[timespentColumnKey.keyValue] = new PivotColumn(timespentColumnKey)
  return result
}

function getElapsedTimeEntriesFn (getStatuses) {
  return function getElapsedTimeEntries (issue) {
    var self = this
    var getIssueEntries = function (issue, parent) {
      var entries = []
      var timeZone = !self.configOptions.preserveStartedTime && issue.fields.creator && issue.fields.creator.timeZone
      var rowKey = new self.rowKeyClass(parent, null, self.options)
      var start = moment(self.options.startDate).unix()
      var created = moment(TimesheetUtils.convertDate(issue.fields.created, timeZone)).unix()
      var startDate = new Date(Math.max(start, created) * 1000)
      var end = TimesheetUtils.addDays(self.options.endDate, 1).getTime()
      if (self.configOptions.workingTimeInStatus.from && self.configOptions.workingTimeInStatus.to) {
        end -= 1 // end of the day, not start of next day
      }
      var endDate = new Date(Math.min(end, Date.now()))
      var worklog = {
        workDate: startDate,
        timeSpentSeconds: PivotKey.getTimeInStatus(startDate, endDate, self.configOptions)
      }
      var columnKeys = PivotKey.getStatusKeysEx(worklog, issue, self.configOptions, getStatuses(issue, self.configOptions))
      var fromDate = startDate.getTime()
      for (var i = 0; i < columnKeys.length; i++) {
        var entry = new PivotEntry(rowKey, columnKeys[i].key, columnKeys[i].timespent)
        var from = moment(columnKeys[i].key.fromDate || fromDate).format(TimesheetUtils.dateTimeFormat) // from previous status or report start date
        var nextFromDate = columnKeys[i].key.statusDate // next from = previous status date
        var toDate = new Date(Math.min(nextFromDate, endDate.getTime())) // till status or report end date
        var to = moment(toDate).format(TimesheetUtils.dateTimeFormat)
        entry.worklog = {
          _issue: issue.key !== parent.key ? issue : null,
          comment: from + ' - ' + to + (columnKeys[i].key.assignee ? ' (' + columnKeys[i].key.assignee + ')' : ''),
          workDate: fromDate,
          toDate: toDate,
          author: entry.columnKey.author,
          timeSpentSeconds: entry.value
        }
        entries.push(entry)
        fromDate = nextFromDate
      }
      return entries
    }

    var entries = getIssueEntries(issue, issue)
    if (issue._subTasks && issue._subTasks.length > 0) {
      issue._subTasks.forEach(function (child) {
        entries = entries.concat(getIssueEntries(child, issue))
      })
    }
    return entries
  }
}

function PassedTimePivotStrategy (rowKeyClass, columnKeyClass, options) {
  this.rowKeyClass = rowKeyClass
  this.columnKeyClass = columnKeyClass
  this.options = WorkedTimePivotStrategy.getStrategyOptions(options)
  this.configOptions = options.configOptions
  if (options.timeInStatusCategories) {
    this.configOptions.timeInStatusCategories = options.timeInStatusCategories.map(function (category) {
      return '' + category
    })
  }
  delete this.configOptions.startedTimeInStatus
}

PassedTimePivotStrategy.prototype.getEntries = getElapsedTimeEntriesFn(PivotKey.getStatuses)

function ResolutionTimePivotStrategy (rowKeyClass, columnKeyClass, options) {
  this.rowKeyClass = rowKeyClass
  this.columnKeyClass = columnKeyClass
  this.options = WorkedTimePivotStrategy.getStrategyOptions(options)
  this.configOptions = options.configOptions
  delete this.configOptions.startedTimeInStatus
}

ResolutionTimePivotStrategy.prototype.getEntries = getElapsedTimeEntriesFn(PivotKey.getResolutions)

function TimeTrackingStrategy (rowKeyClass, columnKeyClass, options) {
  this.rowKeyClass = rowKeyClass
  this.columnKeyClass = columnKeyClass
  this.options = {
    timeTrackingColumns: options.timeTrackingColumns,
    groupByField: options.groupByField,
    groupByFieldObject: options.groupByFieldObject,
    categorizeByField: options.categorizeByField,
    categorizeByFieldObject: options.categorizeByFieldObject,
    orderByField: options.orderByField,
    userInfos: options.userInfos
  }
  this.configOptions = options.configOptions
}
TimeTrackingStrategy.prototype.getEntries = function (issue) {
  var self = this
  var rowKey = new this.rowKeyClass(issue, null, this.options)
  var plannedColumnKey = new PivotKey.Generic('PlannedVsActual', '1timeoriginalestimate')
  var estimateColumnKey = new PivotKey.Generic('PlannedVsActual', '12estimate')
  var remainColumnKey = new PivotKey.Generic('PlannedVsActual', '2esttimeremaining')
  var actualColumnKey = new PivotKey.Generic('PlannedVsActual', '3timespent')
  var diffColumnKey = new PivotKey.Generic('PlannedVsActual', '4diff')
  var plannedRemainColumnKey = new PivotKey.Generic('PlannedVsActual', '5originalestimateremaining', 'originalEstimateRemainingLozenge:jiraConfig')
  var progressColumnKey = new PivotKey.Generic('PlannedVsActual', '6progress', 'timeTracking')
  var entries = []
  var issueIds = []
  var addEntry = function (issue) {
    if (issueIds.indexOf(issue.id) >= 0) {
      return
    }
    issueIds.push(issue.id)
    var original = issue.fields.timeoriginalestimate || 0
    rowKey = TimesheetUtils.clone(rowKey)
    rowKey._issue = issue
    var actual = issue.fields.timespent || 0
    var remaining = issue.fields.timeestimate || 0
    var estimate = actual + remaining
    var entry
    var addColumn = function (columnKey, value) {
      columnKey.order = self.options.timeTrackingColumns.indexOf(columnKey.keyValue)
      if (columnKey.order < 0) {
        return
      }
      if (!entry) {
        entry = new PivotEntry(rowKey, columnKey, value)
      } else {
        entry.addColumn(columnKey, value)
      }
    }
    addColumn(plannedColumnKey, original)
    addColumn(estimateColumnKey, estimate)
    addColumn(remainColumnKey, remaining)
    addColumn(actualColumnKey, actual)
    addColumn(diffColumnKey, original - estimate)
    addColumn(plannedRemainColumnKey, { issue: issue, value: original - actual })
    addColumn(progressColumnKey, { estimate: remaining, timespent: actual })
    entries.push(entry)
    if (issue._subTasks && issue._subTasks.length > 0) {
      issue._subTasks.forEach(addEntry)
    }
  }
  addEntry(issue)
  return entries
}

function TimeBalanceStrategy (rowKeyClass, columnKeyClass, options) {
  this.rowKeyClass = rowKeyClass
  this.columnKeyClass = columnKeyClass
  this.options = {
    timeBalanceColumns: options.timeBalanceColumns,
    sumSubTasks: options.sumSubTasks,
    groupByField: options.groupByField,
    groupByFieldObject: options.groupByFieldObject,
    categorizeByField: options.categorizeByField,
    categorizeByFieldObject: options.categorizeByFieldObject,
    orderByField: options.orderByField,
    userInfos: options.userInfos,
    loggedInUser: options.loggedInUser
  }
  if (options.startDate) {
    this.options.startDate = moment(options.startDate).toDate()
  }
  if (options.endDate) {
    this.options.endDate = TimesheetUtils.addDays(options.endDate, 1)
  }
  this.configOptions = options.configOptions
  if (options.changelogAuthors) {
    this.options.changelogAuthors = options.changelogAuthors
  } else if (options.user) {
    this.options.changelogAuthors = [options.user]
  }
}
TimeBalanceStrategy.prototype.getEntries = function (issue) {
  var self = this
  var rowKey = new this.rowKeyClass(issue, null, this.options)
  var plannedColumnKey = new PivotKey.Generic('PlannedVsActual', '1timeoriginalestimate')
  var estimateColumnKey = new PivotKey.Generic('PlannedVsActual', '12estimate')
  var remainColumnKey = new PivotKey.Generic('PlannedVsActual', '2esttimeremaining')
  var actualColumnKey = new PivotKey.Generic('PlannedVsActual', '3timespent')
  var diffColumnKey = new PivotKey.Generic('PlannedVsActual', '4diff')
  var plannedRemainColumnKey = new PivotKey.Generic('PlannedVsActual', '5originalestimateremaining', 'originalEstimateRemainingLozenge:jiraConfig')
  var progressColumnKey = new PivotKey.Generic('PlannedVsActual', '6progress', 'timeTracking')
  var entries = []
  var issueIds = []
  var deletedWorklogIds = []
  var worklogId, oldWorklogId
  var history, historyComment, historyCreated
  var lastFlushed = false
  var addEntries = function (issue/*, parent */) {
    var addEntry = function () {
      var entry
      var addColumn = function (columnKey, value) {
        columnKey.order = self.options.timeBalanceColumns.indexOf(columnKey.keyValue)
        if (columnKey.order < 0) {
          return
        }
        if (!entry) {
          rowKey = TimesheetUtils.clone(rowKey)
          if (self.options.sumSubTasks) {
            rowKey._issue = issue
          }
          entry = new PivotEntry(rowKey, columnKey, value)
          entry.isTotalLast = true
          if (!lastFlushed) {
            entry.isLast = true// for totals
            lastFlushed = true
          }
          entry.worklog = {
            author: history.author,
            comment: historyComment,
            started: historyCreated
          }
          if (worklogId && deletedWorklogIds.indexOf(worklogId) < 0) {
            entry.worklog.id = worklogId
            entry.worklog.ownWorklog = entry.worklog.author && entry.worklog.author.accountId === self.options.loggedInUser.accountId
          }
        } else {
          entry.addColumn(columnKey, value)
        }
      }
      addColumn(actualColumnKey, actual)// Time Spent
      addColumn(estimateColumnKey, estimate)// Estimate
      addColumn(diffColumnKey, original - estimate)// Variance
      addColumn(plannedColumnKey, original)// Original Estimate
      addColumn(remainColumnKey, remaining)// Est. Time Remaining
      addColumn(plannedRemainColumnKey, { issue: issue, value: original - actual })// Original Estimate Remaining
      addColumn(progressColumnKey, { estimate: remaining, timespent: actual })// Progress
      entries.unshift(entry)
      return entry
    }
    if (issueIds.indexOf(issue.id) >= 0) {
      return
    }
    issueIds.push(issue.id)
    var original, originalOld
    var actual, actualOld
    var remaining, remainingOld
    var estimate
    original = issue.fields.timeoriginalestimate || 0
    actual = issue.fields.timespent || 0
    remaining = issue.fields.timeestimate || 0
    estimate = actual + remaining
    var issueWorklog = issue.worklog || issue.fields.worklog
    var changelog = issue.changelog ? issue.changelog : {}
    var histories = changelog.histories ? changelog.histories : []
    for (var i = histories.length - 1; i >= 0; i--) {
      history = histories[i]
      var items = history.items
      historyCreated = TimesheetUtils.convertDate(history.created)
      if (!items || (self.options.startDate && historyCreated < self.options.startDate)) {
        break
      }
      var historyMatched = false
      historyComment = ''
      for (var j = 0; j < items.length; j++) {
        var item = items[j]
        worklogId = null
        oldWorklogId = null
        if (item.fieldtype === 'jira') {
          if (item.field === 'timeoriginalestimate') {
            originalOld = item.from
            historyMatched = true
            historyComment = historyComment || 'updated Original Estimate'
          } else if (item.field === 'timespent') {
            actualOld = item.from
            historyMatched = true
            historyComment = historyComment || 'updated Timespent'
          } else if (item.field === 'timeestimate') {
            remainingOld = item.from
            historyMatched = true
            historyComment = historyComment || 'updated Estimate'
          } else if (item.field === 'WorklogId') {
            worklogId = item.to // added or edited
            oldWorklogId = item.from // deleted or edited
            if (!worklogId && oldWorklogId) {
              deletedWorklogIds.push(oldWorklogId)
            }
          }
        }
      }
      var inGroups = (!self.options.changelogAuthors || // filtering by author
                (history.author && self.options.changelogAuthors.indexOf(history.author.accountId ? history.author.accountId : history.author.name.toLowerCase()) >= 0))
      if (inGroups && historyMatched && (!self.options.endDate || historyCreated < self.options.endDate)) { // last second is next day, do no use <=
        if (worklogId || oldWorklogId) {
          var logged = actual - actualOld
          if (logged >= 0) {
            historyComment = 'logged ' + parseFloat((logged / 3600).toFixed(2)).toLocaleString() + 'h'
          } else {
            historyComment = 'removed ' + parseFloat((-logged / 3600).toFixed(2)).toLocaleString() + 'h'
          }
        }
        if (!history.author) {
          history.author = { displayName: 'Jira automatically' }
        }
        var entry = addEntry()
        if (entry.worklog.id) {
          var worklog = TimesheetUtils.findByProperty('id', entry.worklog.id, issueWorklog.worklogs)
          if (worklog) {
            entry.worklog.comment += ' on ' + moment(TimesheetUtils.convertDate(worklog.started,
              !self.configOptions.preserveStartedTime && worklog.author ? worklog.author.timeZone : self.options.loggedInUser.timeZone)).format('DD/MMM')
          }
        }
      }
      original = originalOld === null ? 0 : originalOld ? parseInt(originalOld) : original
      actual = actualOld === null ? 0 : actualOld ? parseInt(actualOld) : actual
      remaining = remainingOld === null ? 0 : remainingOld ? parseInt(remainingOld) : remaining
      estimate = actual + remaining
    }
    var creator = issue.fields.creator
    var created = historyCreated = TimesheetUtils.convertDate(issue.fields.created)
    historyComment = 'created'
    worklogId = null
    inGroups = (!self.options.changelogAuthors || // filtering by author
            (creator && self.options.changelogAuthors.indexOf(creator.accountId ? creator.accountId : creator.name.toLowerCase()) >= 0))
    if (inGroups && (!self.options.startDate || created >= self.options.startDate) && (!self.options.endDate || created < self.options.endDate)) { // last second is next day, do no use <=
      history = {
        author: creator
      }
      addEntry()
    }
    if (issue._subTasks && issue._subTasks.length > 0) {
      issue._subTasks.forEach(function (child) {
        addEntries(child, issue)
      })
    }
  }
  addEntries(issue, issue)
  return entries
}

function TimespentStrategy (rowKeyClass, columnKeyClass, options) {
  this.rowKeyClass = rowKeyClass
  this.columnKeyClass = columnKeyClass
  this.options = TimespentStrategy.getStrategyOptions(options)
  this.configOptions = options.configOptions
}

TimespentStrategy.getStrategyOptions = function (options) {
  var result = {
    includeEmpty: options.includeEmpty
  }
  if (options.startDate) {
    result.startDate = moment(options.startDate).toDate()
  }
  if (options.endDate) {
    result.endDate = moment(options.endDate).toDate()
  }
  if (options.worklogAuthors) {
    result.worklogAuthors = options.worklogAuthors
  } else if (options.user) {
    result.worklogAuthors = [options.user]
  }
  if (options.restrictedProjects) {
    result.restrictedProjects = options.restrictedProjects
  }
  result.loggedInUser = options.loggedInUser
  result.groupByField = options.groupByField
  result.groupByFieldObject = options.groupByFieldObject
  result.categorizeByField = options.categorizeByField
  result.categorizeByFieldObject = options.categorizeByFieldObject
  result.orderByField = options.orderByField
  result.includeEmpty = options.includeEmpty
  result.userInfos = options.userInfos
  return result
}

TimespentStrategy.prototype.getEntries = function (issue) {
  var entries = []
  var nonEmptySubtasks = []
  var issueWorklog = issue.worklog || issue.fields.worklog
  var entry
  var columnKey = new PivotKey.Generic('Timespent', '3timespent')
  for (var i = 0; i < issueWorklog.worklogs.length; i++) {
    var worklog = issueWorklog.worklogs[i]
    var inRange = true
    var workDate = worklog.workDate = TimesheetUtils.convertDate(worklog.started,
      !this.configOptions.preserveStartedTime && worklog.author ? worklog.author.timeZone : this.options.loggedInUser.timeZone)
    if (this.options.startDate) {
      inRange = workDate >= this.options.startDate
    }
    if (this.options.endDate) {
      var endDateIncluded = TimesheetUtils.addDays(this.options.endDate, 1) // endDate should be in range
      inRange = inRange && (workDate < endDateIncluded)
    }
    var inGroups = (!this.options.worklogAuthors || // filtering by author
            (worklog.author && this.options.worklogAuthors.indexOf(worklog.author.accountId) >= 0))
    var restricted = this.options.restrictedProjects ? this.options.restrictedProjects.indexOf(worklog._issue ? worklog._issue.fields.project.key : issue.fields.project.key) >= 0 : false
    restricted = restricted && (!worklog.author || (worklog.author.accountId !== this.options.loggedInUser.accountId))
    if (inRange && inGroups && !restricted) {
      var rowKey = new this.rowKeyClass(issue, worklog, this.options)
      if (this.options.includeEmpty && /* sumSubTasks */ issue._subTasks) {
        rowKey._issue = issue // trigger summing numeric fields
        if (worklog._issue && nonEmptySubtasks.indexOf(worklog._issue.key) < 0) {
          nonEmptySubtasks.push(worklog._issue.key)
          // sum numeric fields child issues with worklogs
          for (var issueField in worklog._issue.fields) {
            var field = worklog._issue.fields[issueField]
            if (field && typeof field === 'number') {
              issue.fields[issueField] = (issue.fields[issueField] || 0) + field
            }
          }
        }
      }
      entry = new WorkedTimePivotEntry(rowKey, columnKey, worklog)
      entries.push(entry)
    }
  }
  if (this.options.includeEmpty && ['workeduser', 'workedusergroup', 'workeddate', 'workcreated', 'workedweek', 'commentfirstword', 'worklabel'].indexOf(this.options.groupByField) < 0) {
    var rowKey = new this.rowKeyClass(issue, null, this.options)
    var addEntry = function (issue, value) {
      rowKey = TimesheetUtils.clone(rowKey)
      rowKey._issue = issue
      entry = new WorkedTimePivotEntry(rowKey, columnKey, null)
      entry.value = value
      entries.push(entry)
    }
    if (!entries.length) {
      rowKey._issue = issue // trigger summing numeric fields
      addEntry(issue, this.options.groupByField ? {} : undefined)
    }
    if (issue._subTasks && issue._subTasks.length > 0) {
      issue._subTasks.forEach(function (issue) {
        if (nonEmptySubtasks.indexOf(issue.key) < 0) {
          addEntry(issue, {})
        }
      })
    }
  }
  return entries
}

function CostStrategy (rowKeyClass, columnKeyClass, options) {
  TimespentStrategy.call(this, rowKeyClass, columnKeyClass, options)
  this.options.hourlyRates = options.hourlyRates
}

CostStrategy.prototype.getEntries = function (issue) {
  var entries = TimespentStrategy.prototype.getEntries.call(this, issue)
  var columnCostKey = new PivotKey.Generic('Cost', '7cost', 'currency:$parent.currencySymbol')
  var entry
  var cost
  for (var i = 0; i < entries.length; i++) {
    entry = entries[i]
    cost = this.options.hourlyRates && entry.worklog && Math.round(entry.value / 36) / 100 *
      (this.options.hourlyRates[entry.worklog.author.accountId] || this.options.hourlyRates[''] || 0)
    entry.addColumn(columnCostKey, cost)
  }
  return entries
}

function CalendarStrategy (rowKeyClass, columnKeyClass, options) {
  this.rowKeyClass = rowKeyClass
  this.columnKeyClass = columnKeyClass
  this.options = {
    reportingDay: options.reportingDay == null ? moment.localeData().firstDayOfWeek() : options.reportingDay,
    groupByField: 'workedweek',
    groupByFieldObject: TimesheetUtils.FIELDS.WORKED_WEEK_FIELD
  }
  if (options.showWeekends) {
    this.options.showWeekends = options.showWeekends
  }
  if (options.startDate) {
    this.options.startDate = moment(options.startDate).toDate()
  }
  if (options.endDate) {
    this.options.endDate = moment(options.endDate).toDate()
  }
  if (options.worklogAuthors) {
    this.options.worklogAuthors = options.worklogAuthors
  } else if (options.user) {
    this.options.worklogAuthors = [options.user]
  }
  if (options.restrictedProjects) {
    this.options.restrictedProjects = options.restrictedProjects
  }
  this.options.loggedInUser = options.loggedInUser
  this.configOptions = options.configOptions
}

CalendarStrategy.prototype.getEntries = function (issue) {
  var entries = []
  var issueWorklog = issue.worklog || issue.fields.worklog
  var entry
  for (var i = 0; i < issueWorklog.worklogs.length; i++) {
    var worklog = issueWorklog.worklogs[i]
    var inRange = true
    var workDate = worklog.workDate = TimesheetUtils.convertDate(worklog.started,
      !this.configOptions.preserveStartedTime && worklog.author ? worklog.author.timeZone : this.options.loggedInUser.timeZone)
    if (this.options.startDate) {
      inRange = workDate >= this.options.startDate
    }
    if (this.options.endDate) {
      var endDateIncluded = TimesheetUtils.addDays(this.options.endDate, 1) // endDate should be in range
      inRange = inRange && (workDate < endDateIncluded)
    }
    var inGroups = (!this.options.worklogAuthors || // filtering by author
            (worklog.author && this.options.worklogAuthors.indexOf(worklog.author.accountId) >= 0))
    var restricted = this.options.restrictedProjects ? this.options.restrictedProjects.indexOf(worklog._issue ? worklog._issue.fields.project.key : issue.fields.project.key) >= 0 : false
    restricted = restricted && (!worklog.author || (worklog.author.accountId !== this.options.loggedInUser.accountId))
    if (inRange && inGroups && !restricted) {
      var rowKey = new this.rowKeyClass(issue, worklog, this.options)
      var dayDiff = moment(workDate).diff(moment(TimesheetUtils.getPrevMonday(workDate, this.options.reportingDay)), 'days')
      var columnKey = new PivotKey.Generic('Calendar', TimesheetUtils.normalizeNumber(dayDiff))
      columnKey.isToday = TimesheetUtils.sameDay(workDate, new Date())
      entry = new WorkedTimePivotEntry(rowKey, columnKey, worklog)
      entries.push(entry)
    }
  }
  return entries
}

CalendarStrategy.prototype.getColumnsFunction = function (date) {
  var result = {}
  var day = TimesheetUtils.getPrevMonday(date || new Date(), this.options.reportingDay)
  for (var i = 0; i < 7; i++) {
    var columnKey = new PivotKey.Generic('Calendar', TimesheetUtils.normalizeNumber(i))
    columnKey.isWeekend = TimesheetUtils.isWeekend(day, this.configOptions)
    if (date) {
      columnKey.isToday = TimesheetUtils.sameDay(day, new Date())
    }
    columnKey.day = TimesheetUtils.normalizeNumber(day.getDay())
    result[columnKey.keyValue] = new PivotColumn(columnKey)
    day = TimesheetUtils.addDays(day, 1)
  }
  return result
}

if (typeof module === 'object' && module.exports) {
  var TimesheetUtils = require('../../timesheetUtils')
  var moment = require('moment-timezone')
  var PivotEntryModule = require('./PivotEntry')
  var PivotEntry = PivotEntryModule.PivotEntry
  var WorkedTimePivotEntry = PivotEntryModule.WorkedTimePivotEntry
  var EmptyPivotEntry = PivotEntryModule.EmptyPivotEntry
  module.exports.WorkedTimePivotStrategy = WorkedTimePivotStrategy
  module.exports.TimeEntryStrategy = TimeEntryStrategy
  module.exports.IssueTimeStrategy = IssueTimeStrategy
  module.exports.PassedTimePivotStrategy = PassedTimePivotStrategy
  module.exports.ResolutionTimePivotStrategy = ResolutionTimePivotStrategy
  module.exports.TimeTrackingStrategy = TimeTrackingStrategy
  module.exports.TimeBalanceStrategy = TimeBalanceStrategy
  module.exports.TimespentStrategy = TimespentStrategy
  module.exports.LabelPivotStrategy = LabelPivotStrategy
  module.exports.CalendarStrategy = CalendarStrategy
}
