app.controller('BitbucketController', ['$scope', '$q', '$window', 'loggedInUser',
  'flightRecorder', 'pivottableService', 'configurationService', 'viewService',
  function ($scope, $q, $window, loggedInUser, flightRecorder, pivottableService, configurationService, viewService) {
    var AP = window.RA || window.AP

    init()

    function init () {
      $scope.loggedInUser = loggedInUser

      var closeDialogFunction = function () {
        $scope.$apply(function () {
          $scope.loading = true
        })
        var replyTo = loggedInUser.emailAddress || $scope.email
        if (replyTo) {
          replyTo = loggedInUser.displayName + ' <' + replyTo + '>'
        }
        flightRecorder.sendEmail($scope.includeSupportData.enabled, replyTo, $scope.title, $scope.content, $scope.includeSupportData.screenshot, $scope.includeSupportData.error).then(function () {
          AP.Dialog.close('submit')
        }).catch(function (response /* or error */) {
          AP.flag.create({
            title: 'Unexpected error',
            body: response.status ? response.status + ' ' + response.data : /* error */ response,
            type: 'error'
          })
        })
      }

      var paramsDeferred = $q.defer(); var $fields; var $restricted; var $filters; var $projects
      var $config; var $jiraConfig; var $isAdmin; var $permissions; var $statuses; var $user; var $statusCategories
      AP.dialog.getCustomData(function (params) {
        $scope.title = params.errorMessage
        if (params.stackTrace) {
          var content = ['', '', '---- Error Stack Trace ----']
          var stackTrace = params.stackTrace.map(function (frame) {
            return frame.source
          })
          content.push.apply(content, stackTrace)
          $scope.content = content.join('\n')
        }
        $scope.includeSupportData = { enabled: false, screenshot: params.screenshot, error: params.error }
        if (params.options) {
          $scope.includeSupportData.options = params.options
          flightRecorder.setEnabled(true)
          flightRecorder.routeParams = params.routeParams
          if (params.routeParams.viewId) {
            viewService.getViews(params.routeParams.viewId, loggedInUser).then(paramsDeferred.resolve)
          } else {
            paramsDeferred.resolve(params.routeParams)
          }
          $fields = pivottableService.getFields()
          $restricted = pivottableService.restrictedByAuditorsGroups(loggedInUser)
          $filters = pivottableService.getFilters()
          $projects = configurationService.getProjects()
          $config = configurationService.getUserConfiguration(loggedInUser)
          $jiraConfig = pivottableService.getJiraConfiguration()
          $isAdmin = pivottableService.isAdministrator()
          $permissions = pivottableService.havePermissions(['WORK_ON_ISSUES', 'EDIT_OWN_WORKLOGS', 'DELETE_OWN_WORKLOGS', 'EDIT_ALL_WORKLOGS', 'DELETE_ALL_WORKLOGS'])
          $statuses = configurationService.getAllStatuses()
          $statusCategories = configurationService.getAllStatusCategories()
          if (params.options.user && loggedInUser.accountId !== params.options.user) {
            $user = pivottableService.getUserInfo(params.options.user)
          }
        }
        $scope.$apply()
      })

      AP.events.on('dialog.submit', closeDialogFunction)
      AP.dialog.disableCloseOnSubmit()

      $scope.$watch('includeSupportData.enabled', function (value) {
        if (value) {
          var submitButton = AP.dialog.getButton('submit')
          submitButton.disable()
          $q.all([paramsDeferred.promise, $fields, $restricted, $filters, $projects, $config,
            $jiraConfig, $isAdmin, $permissions, $statuses, $statusCategories, $user]).then(function (values) {
            var /* routeParams = values[0], */ restricted = values[2]; var config = values[5]
            if (!$scope.includeSupportData.error) {
              $scope.includeSupportData.error = {}
            } else if (!angular.isObject($scope.includeSupportData.error)) {
              $scope.includeSupportData.error = { error: $scope.includeSupportData.error }
            }
            var options = $scope.includeSupportData.error.options = $scope.includeSupportData.options
            if (options.restrictable && restricted) {
              $scope.includeSupportData.error.restricted = true
            } else {
              if (!options.configOptions) {
                options.configOptions = {}
                if (options.restrictable) {
                  options.configOptions.auditorsRoles = config.auditorsRoles && config.auditorsRoles.val
                }
              }
              var $pivotTable = pivottableService.getPivotTable(loggedInUser, options)
            }
            $q.when($pivotTable).then(function () {
              if ($scope.SubmitIssueForm.$valid) {
                submitButton.enable()
              }
            })
          })
        }
      })
    }
  }
])
