'use strict'
app.service('jobService', ['$q', '$http', function ($q, $http) {
  // DO data in services: http://blog.nebithi.com/angularjs-dos-and-donts/
  this.data = {
  }

  var jobsKey = function (loggedInUser) {
    return 'jobs4' + loggedInUser.accountId
  }

  this.getJobs = function (loggedInUser) {
    var service = this
    var deferred = $q.defer()
    if (!Object.keys(service.data).length) {
      $http.get('/api/configuration/' + jobsKey(loggedInUser)).then(function (response) {
        var data = response.data
        service.data.config = data || {}
        deferred.resolve(service.data.config)
      })
    } else {
      deferred.resolve(service.data.jobs)
    }
    return deferred.promise
  }

  this.saveJob = function (job, loggedInUser) {
    var deferred = $q.defer()
    var service = this
    $http.get('/api/configuration/' + jobsKey(loggedInUser)).then(function (response) {
      var data = response.data
      var newJob = angular.copy(job)
      if (data) {
        var jobs = data.val
        if (job.jobId) {
          var i = service.indexById(job.jobId, jobs)
          if (i >= 0) {
            jobs.splice(i, 1, newJob)
          } else { // lost on reinstall probably
            jobs.push(newJob)
          }
        } else {
          var last = jobs[jobs.length - 1]
          newJob.jobId = last.jobId + 1
          jobs.push(newJob)
        }
        $http.put('/api/configuration/' + data.id, data).then(function (response) {
          var data = response.data
          angular.copy(data, service.data.config)
          deferred.resolve(newJob)
        })
      } else {
        newJob.jobId = 1
        data = {
          key: jobsKey(loggedInUser),
          val: [newJob]
        }
        $http.post('/api/configuration/', data).then(function (response) {
          var data = response.data
          angular.copy(data, service.data.config)
          deferred.resolve(newJob)
        })
      }
    })
    return deferred.promise
  }

  this.removeJob = function (jobId, loggedInUser) {
    var deferred = $q.defer()
    var service = this
    $http.get('/api/configuration/' + jobsKey(loggedInUser)).then(function (response) {
      var data = response.data
      if (data) {
        var jobs = data.val
        var i = service.indexById(jobId, jobs)
        jobs.splice(i, 1)
        if (jobs.length > 0) {
          data.val = jobs
          $http.put('/api/configuration/' + data.id, data).then(function (response) {
            var data = response.data
            angular.copy(data, service.data.config)
            deferred.resolve(service.data.config)
          })
        } else {
          $http.delete('/api/configuration/' + data.id).then(function () {
            angular.copy({}, service.data.config)
            deferred.resolve(service.data.config)
          })
        }
      } else {
        deferred.resolve(service.data.config)
      }
    })
    return deferred.promise
  }

  this.indexById = function (id, array) {
    return TimesheetUtils.indexByProperty('jobId', id, array)
  }

  this.resetState = function () {
    delete this.data.config
  }
}])
