var appBaseModule = angular.module('appBase', [
  'ngRoute', 'angular-i18n', 'aui',
  'talis.services.logging',
  'configuration', 'directives'])

appBaseModule.config(['$httpProvider', '$provide', '$qProvider', '$i18nProvider',
  function ($httpProvider, $provide, $qProvider, $i18nProvider) {
    $provide.factory('authHttpInterceptor', ['$window', '$q', function ($window, $q) {
      var location = '&' + $window.location.search.substring(1)
      var v = getUrlParam(location, '&v')
      var atlTokenTime
      return {
        request: function (config) {
          // token remains valid for 15 minutes by default - configured using maxTokenAge.
          if (v === '3' && (!atlTokenTime || Date.now() - atlTokenTime > 14 * 60 * 1000)) {
            atlTokenTime = Date.now()
            var deferred = $q.defer()
            AP.context.getToken(function (token) {
              $window.atlToken = token
              $httpProvider.defaults.headers.common['Authorization'] = 'JWT ' + token
              config.headers['Authorization'] = 'JWT ' + token
              deferred.resolve(config)
            })
            if (!config.url.match(/^\/api/)) { // do not wait and ACJIRA-1968 gadget refresh workaround
              deferred.resolve(config)
            }
            return deferred.promise
          } else {
            return config
          }
        },
        responseError: function (rejection) {
          if (rejection.status === 401) {
            // TODO: apMessages or apService.messsages()
            AP.flag.create({
              title: 'Authentication expired',
              body: 'Reload page to restore communication with backend',
              type: 'error'
            })
          }
          return $q.reject(rejection)
        }
      }
    }])
    $provide.factory('urlRewriteHttpInterceptor', ['$window', function ($window) {
      return {
        request: function (config) {
          var path = config.url.match(/^\/?(.+?)\/(.+?)\.json/)
          if (path && path[1] === 'i18n') {
            if (['ru_RU', 'fr_FR', 'de_DE', 'es_ES'].indexOf(path[2]) < 0) {
              config.url = $window.i18nDefault
            } else {
              config.url += $window.b
            }
          }
          return config
        }
      }
    }])
    $httpProvider.interceptors.push('authHttpInterceptor', 'urlRewriteHttpInterceptor')
    $i18nProvider.language = window.loggedInUserLocale
    if (window.deployedDate) { // production
      $qProvider.errorOnUnhandledRejections(false) // suppress 'Possibly unhandled rejection' errors
      //  set the debug mode to true, so we can use the onTranslationFailed
      $i18nProvider.debug = true
      //  return a string is we don't find the translationID
      $i18nProvider.onTranslationFailed = function (lang, translationID/*, section, placeholders */) {
        return translationID
      }
    }
  }])

appBaseModule.run(['$rootScope', '$window', '$http', '$injector', 'applicationLoggingService',
  function ($rootScope, $window, $http, $injector, logger) {
    logger.flush() // init

    // TODO: all context variables via html meta
    $rootScope.deployedDate = $window.deployedDate

    $rootScope.initOnErrorPopup = function (params) {
      var configurationService = $injector.get('configurationService') // lazy load for tests
      var loggedInUser = $injector.get('loggedInUser') // lazy load for tests
      loggedInUser.then(function (loggedInUser) {
        configurationService.getUserConfiguration(loggedInUser).then(function (config) {
          logger.trace('mailer url, ignore?', [$window.mailerUrl, config.onErrorIgnore.val])
          if ($window.mailerUrl && !config.onErrorIgnore.val) {
            $window.$onErrorPopup = params.dialog // see templates/main.html and modules/logging.exceptionLoggingService
            params.scope.onErrorDialog = function () { // see templates/popups/onError.html
              $onErrorPopup.hide()
              var customData = {
                errorMessage: $onErrorPopup.data('errorMessage'),
                stackTrace: $onErrorPopup.data('stackTrace'),
                options: $onErrorPopup.data('options'),
                routeParams: $onErrorPopup.data('routeParams')
              }
              TimesheetUtils.screenshot(function (blob, error) {
                customData.screenshot = blob
                customData.error = error
                AP.dialog.create({
                  key: 'bitbucket',
                  width: '700px',
                  height: '400px',
                  chrome: true,
                  header: 'Report Problem',
                  submitText: 'Submit',
                  customData: customData
                })
              })
            }
            params.scope.onErrorIgnore = function () { // see onError.html
              configurationService.saveUserPreferences({
                key: 'onErrorIgnore',
                val: true
              }, loggedInUser).then(function () {
                $window.$onErrorPopup.hide()
                delete $window.$onErrorPopup
              })
            }
          }
        })
      })
    }

    $rootScope.contactSupport = function () {
      var customData = angular.copy($rootScope.customData) || {}
      TimesheetUtils.screenshot(function (blob, error) {
        customData.screenshot = blob
        if (error) {
          customData.error = JSON.parse(JSON.stringify(error))
        }
        AP.dialog.create({
          key: 'bitbucket',
          width: '720px',
          height: '400px',
          chrome: true,
          header: 'Contact Support',
          submitText: 'Submit',
          customData: customData
        })
      })
    }
  }])

var getParam = function (param, $q, converter) {
  var deferred = $q.defer()
  window.AP.getLocation(function (location) {
    var result = getUrlParam(location, param)
    deferred.resolve(angular.isFunction(converter) ? converter(result) : result)
  })
  return deferred.promise
}
var getUrlParam = function (location, param) {
  var result
  var part = location.split(param + '=')[1]
  if (angular.isDefined(part)) {
    result = part.split(/&|#!/)[0] || ''
  }
  return result
}

appBaseModule.factory('projectKey', ['$q', function ($q) {
  return getParam('project.key', $q)
}])
appBaseModule.factory('externalFilter', ['$q', function ($q) {
  return getParam('filter', $q)
}])
appBaseModule.factory('jql', ['$q', function ($q) {
  return getParam('jql', $q, function (value) {
    if (angular.isDefined(value)) {
      return decodeURIComponent(TimesheetUtils.replaceAll(value, '+', ' '))
    } else {
      return value
    }
  })
}])

appBaseModule.factory('timesheetParams', ['$q', '$routeParams', function ($q, $routeParams) {
  var deferred = $q.defer()
  window.AP.getLocation(function (location) {
    var urlParts = location.split('#!')
    deferred.resolve(urlParts.length === 2
      ? TimesheetUtils.parseParams(urlParts[1])
      : $routeParams)
  })
  return deferred.promise
}])

appBaseModule.factory('loggedInUser', ['pivottableService',
  function (pivottableService) {
    return pivottableService.getMyself().then(function (json) {
      moment.locale(json.locale) // set global locale
      // TODO? moment.tz.setDefault() when moment everywhere
      return json
    })
  }
])

appBaseModule.service('apService', [function () {
  // TODO: messages, request
}])
