function PivotColumn (columnKey) {
  this.columnKey = columnKey
  this.sum = 0
  this.detailsNum = 0
  this.entries = []
  this.value = null
}
PivotColumn.prototype.add = function (pivotEntry, value) {
  this.entries.push(pivotEntry)
  this.detailsNum += 1
  if (typeof value === 'number') {
    if (pivotEntry.isTotalLast) {
      if (pivotEntry.isLast) {
        this.sum += value
      }
    } else {
      this.sum += value
    }
  } else if (value && typeof value === 'object' && Object.keys(value).length > 0) {
    this.value = this.value || {}
    for (var key in value) {
      if (typeof value[key] === 'number') {
        if (pivotEntry.isTotalLast) {
          if (pivotEntry.isLast) {
            this.value[key] = value[key] + (this.value[key] || 0)
          }
        } else {
          this.value[key] = value[key] + (this.value[key] || 0)
        }
      }
    }
  }
}

if (typeof module === 'object' && module.exports) {
  module.exports = PivotColumn
}
