app.controller('HourlyRatesController', ['$scope', '$modalInstance', '$q', 'configurationService', 'pivottableService',
  'loggedInUser', 'translations', '$timeout', 'currencySymbol', 'changed',
  function ($scope, $modalInstance, $q, configurationService, pivottableService,
    loggedInUser, translations, $timeout, currencySymbol, changed) {
    $scope.saveCurrencySymbol = function () {
      $scope.loading = true
      changed.changed = true
      if (!$scope.currencySymbol.val) {
        delete $scope.currencySymbol.val
      }
      configurationService.saveConfiguration($scope.currencySymbol).then(function (config) {
        delete $scope.loading
        $scope.currencySymbol = config
      })
    }

    var filterHourlyRates = accountId => $scope.hourlyRates.filter(hourlyRate => hourlyRate.accountId !== accountId)

    $scope.addHourlyRate = function (user, rate, edited) {
      $scope.loading = true
      changed.changed = true
      if (!user) user = { accountId: '' }
      configurationService.saveHourlyRate(user.accountId, rate).then(() => {
        delete $scope.loading
        user.hourlyRate = rate
        if (!edited) {
          $scope.hourlyRates = filterHourlyRates(user.accountId)
          if (rate) {
            $scope.hourlyRates.push(user)
          }
          $scope.paramUserInfo = null
        }
      })
    }

    $scope.removeHourlyRate = function (user) {
      $scope.loading = true
      changed.changed = true
      configurationService.deleteHourlyRate(user.accountId).then(function (accountId) {
        delete $scope.loading
        $scope.hourlyRates = filterHourlyRates(user.accountId)
      })
    }

    $scope.userChange = function (accountId) {
      pivottableService.getUserInfo(accountId).then(user => { $scope.paramUserInfo = user })
    }

    var $hourlyRates = configurationService.getHourlyRates().then(function (hourlyRates) {
      if (!hourlyRates) return {}
      var promises = []
      Object.keys(hourlyRates).forEach(hourlyRate => {
        promises.push(pivottableService.getUserInfo(hourlyRate))
      })
      return $q.all(promises).then(function (users) {
        users.forEach(user => { user.hourlyRate = hourlyRates[user.accountId] })
        return TimesheetUtils.sortByProperty(users, 'displayName')
      })
    })

    $hourlyRates.then(hourlyRates => {
      $scope.hourlyRate = 0
      $scope.hourlyRates = hourlyRates
    })

    $scope.cancel = function () {
      $modalInstance.dismiss()
    }

    $scope.currencySymbol = currencySymbol
  }
])
