app.controller('TimesheetController', ['$window', '$scope', '$rootScope', 'translations', '$timeout', '$q', '$route', '$routeParams',
  '$location', '$sce', '$interval', 'pivottableService', 'configurationService', 'loggedInUser', 'projectKey', 'jql', 'externalFilter', 'timesheetParams',
  'applicationLoggingService', '$modal', 'viewService', 'flightRecorder', 'validationService', 'jobService', '$injector', 'userConfiguration',
  function ($window, $scope, $rootScope, translations, $timeout, $q, $route, $routeParams,
    $location, $sce, $interval, pivottableService, configurationService, loggedInUser, projectKey, jql, externalFilter, timesheetParams,
    logger, $modal, viewService, flightRecorder, validationService, jobService, $injector, config) {
    var COOKIE_NAME_SUFFIX = $scope.dashboardId ? '-' + $scope.dashboardId + '-' + $scope.dashboardItemId : ''
    var COOKIE_NAME_LOCATION_SEARCH = ($window.isTimesheet ? 'jiratimesheet' : 'timereports') + '-location-search' + COOKIE_NAME_SUFFIX
    var TIMER_INTERVAL = 10

    var allReportParams = {
      timeTrackingColumns: null,
      timeBalanceColumns: null,
      timeInStatusCategories: null,
      offset: null,
      numOfWeeks: null,
      hideWeekends: null,
      filterOrProjectId: null,
      childrenFilterId: null,
      user: null,
      allUsers: null,
      group: null,
      excludeGroup: null,
      startDate: null,
      endDate: null,
      showWeekends: null,
      reportingDay: null,
      groupByField: null,
      categorizeByField: null,
      moreFields: null,
      sumSubTasks: null,
      includeEmpty: null,
      view: null,
      sum: null,
      showDetails: null
    }

    var showSaveButton = function (routeParams) {
      if ($scope.currentView && $scope.currentView.viewId) {
        delete $scope.showSaveButton
        for (var key in allReportParams) {
          if (!angular.equals($scope.currentView[key], routeParams[key])) {
            $scope.showSaveButton = true
            break
          }
        }
        if (!$scope.showSaveButton && $scope.currentView.name !== $scope.currentViewName) {
          $scope.showSaveButton = true
        }
      } else {
        $scope.showSaveButton = $scope.currentView && $scope.currentView.name
      }
    }

    var saveRouteParams = function (routeParams, noinit) {
      flightRecorder.setEnabled(false) // TODO: next iteration
      noinit || init(routeParams)
      showSaveButton(routeParams)
      delete routeParams._
      delete routeParams.loaded
      if (angular.isFunction(AP.cookie.save)) {
        var cookieParams = angular.copy(routeParams)
        if (cookieParams.jql) { // ACJS-890
          cookieParams.jql = encodeURIComponent(cookieParams.jql)
        }
        AP.cookie.save(COOKIE_NAME_LOCATION_SEARCH, JSON.stringify(cookieParams))
      }
      if (angular.isFunction(AP.history.pushState)) {
        var savedParams = angular.copy(routeParams)
        AP.history.pushState($.param(TimesheetUtils.normalizeObject(savedParams, true), /* traditional */ true))
      }
      routeParams._ = Date.now()
    }

    var $cookieDeferred = $q.defer()
    if (!Object.keys(timesheetParams).length && !$scope.issueKey && !$scope.issueTimeBalance && angular.isFunction(AP.cookie.read)) {
      AP.cookie.read(COOKIE_NAME_LOCATION_SEARCH, function (data) {
        flightRecorder.cookie(data)
        var savedParams
        try {
          savedParams = TimesheetUtils.getJson(data || '')
        } catch (err) {
          console.log('Cookie error', err)
          savedParams = timesheetParams
        }
        // angular.extend(savedParams, $routeParams);
        if (angular.isDefined(jql) || externalFilter) {
          delete savedParams.filterOrProjectId
          delete savedParams.jql
        } else if (savedParams.jql) { // ACJS-890
          savedParams.jql = decodeURIComponent(savedParams.jql)
        }
        $cookieDeferred.resolve(savedParams)
      })
    } else if (!Object.keys($routeParams).length) {
      $cookieDeferred.resolve(timesheetParams)
    } else {
      $cookieDeferred.resolve($routeParams)
    }
    $cookieDeferred.promise.then(function (routeParams) {
      var promise
      if ($scope.isGadget && $scope.dashboardId && $scope.dashboardItemId) {
        promise = pivottableService.getGadgetConfiguration($scope.dashboardId, $scope.dashboardItemId).then(function (config) {
          // issue#765: rewrite persisted config params in cookies
          var rewrite = angular.extend({
            timeTrackingColumns: null,
            timeBalanceColumns: null,
            timeInStatusCategories: null,
            title: null,
            numOfWeeks: null,
            hideWeekends: null,
            reportingDay: null,
            filterOrProjectId: null,
            childrenFilterId: null,
            user: null,
            allUsers: null,
            group: null,
            excludeGroup: null,
            groupByField: null,
            categorizeByField: null,
            moreFields: null,
            sumSubTasks: null,
            includeEmpty: null
          }, config)
          var optionalKeys = ['monthView', 'offset', 'sum', 'showDetails']
          optionalKeys.forEach(key => {
            // TIMES-1059: keep user selected view options
            if (!rewrite[key]) {
              delete rewrite[key]
            }
          })
          rewrite.showWeekends = !rewrite.hideWeekends
          return angular.extend(routeParams, rewrite)
        })
      } else {
        $scope.currentView = viewService.data.currentView
        if ($scope.issueKey && !$scope.issueTimeBalance) { // IssueTime
          try {
            var issueTimeView = window.localStorage.getItem('issueTimeView')
            routeParams.viewId = issueTimeView ? parseInt(issueTimeView.substr(5)) : null
          } catch (err) {
            console.log('timeentry cache error', err)
          }
        }
        promise = viewService.getViews(parseInt(routeParams.viewId), loggedInUser).then(function () {
          if ($scope.issueKey && !$scope.issueTimeBalance) { // IssueTime
            $scope.issueTimeOptions = new TimesheetSelectOptions(TimesheetGeneralOption, 'view_-1')
            $scope.issueTimeOptions.addOption(translations['TimeEntry'], 'view_-1')
            for (var i = 0; i < viewService.data.allViews.length; i++) {
              var view = viewService.data.allViews[i]
              $scope.issueTimeOptions.addOption(view.name, 'view_' + view.viewId)
              if (view.viewId === routeParams.viewId) {
                $scope.issueTimeOptions.selected = 'view_' + view.viewId
              }
            }
          }
          var currentViewIdDefined = angular.isDefined(viewService.data.currentView.viewId)
          $scope.showDeleteButton = currentViewIdDefined
          var sections = viewService.data.allViews.length > 0 ? { view: translations['Saved Report'], pivotTableType: translations['Default Report'] } : null
          $scope.viewOptions = new TimesheetSelectOptions(TimesheetGeneralOption, null, null, sections)
          $scope.viewOptions.selected = currentViewIdDefined ? 'view_' + viewService.data.currentView.viewId : 'pivotTableType_' + (routeParams.pivotTableType || 'Timesheet')
          for (var i = 0; i < viewService.data.allViews.length; i++) {
            var view = viewService.data.allViews[i]
            $scope.viewOptions.addOption(view.name, 'view_' + view.viewId)
          }
          var pivotTableTypeKeys = Object.keys(PivotTableType)
          for (var i = 0; i < pivotTableTypeKeys.length; i++) {
            var pivotTableType = pivotTableTypeKeys[i]
            if (pivotTableType === 'CostReport') continue
            $scope.viewOptions.addOption(translations[pivotTableType], 'pivotTableType_' + pivotTableType)
          }
          if (currentViewIdDefined) {
            var currentView = angular.copy($scope.currentView)
            delete routeParams._
            $scope.showSaveButton = currentView.viewId !== routeParams.viewId
            $scope.currentViewName = $scope.currentView.name // original
            if (Object.keys(routeParams) > 1) { // modified
              var rewrite = angular.extend({
                _allProjects: null,
                everyWeekOn: null,
                everyWeekAt: null,
                ifIncomplete: null,
                sendToCCUsers: null,
                addCsv: null,
                jobId: null
              }, allReportParams, routeParams)
              return TimesheetUtils.normalizeObject(angular.extend(currentView, rewrite))
            } else {
              return currentView
            }
          } else {
            delete routeParams.viewId
            return routeParams
          }
        })
      }
      promise.then(init)
    })

    $rootScope.onGadgetConfigSave = $rootScope.onGadgetConfigSave || $rootScope.$on('gadgetConfigSave', function () {
      $rootScope.showGadgetConfiguration = false
      $scope.refresh()
    })

    $rootScope.onGadgetConfigCancel = $rootScope.onGadgetConfigCancel || $rootScope.$on('gadgetConfigCancel', function () {
      $rootScope.showGadgetConfiguration = false
    })

    $scope.loading = 0
    AP.events.on('ISSUE_QUICK_ADD_CLICKED', function (event) {
      // event = { isNewToIssue: boolean (false, content panel is already on issue view) }
      $scope.$apply(_ => {
        $scope.addWorklogDialog()
      })
    })

    function init (routeParams) {
      $scope.isTimesheet = window.isTimesheet
      if (!routeParams.showDetails) {
        routeParams.showDetails = false // compress by default
      }
      logger.trace('routeParams', routeParams)
      $('.aui-inline-dialog').hide() // AP.inlineDialog.hide() does not work
      if ($scope.currentView && routeParams.name !== $scope.currentView.name) {
        $scope.currentView.name = routeParams.name
      }
      showSaveButton(routeParams)

      // utils
      $scope.TimesheetUtils = TimesheetUtils
      $scope.moment = moment
      $rootScope.showGadgetConfiguration = false
      $scope.gadgetDialog = false

      $scope.loggedInUser = loggedInUser
      $scope.reportingDayConfig = config.reportingDay && config.reportingDay.val

      $scope.pivotTableType = $scope.issueTimeBalance ? 'IssueTimeBalance' : $scope.createWorklog || ($scope.issueKey && !routeParams.viewId) ? 'IssueTime' : routeParams.pivotTableType || 'Timesheet'
      if ($scope.pivotTableType === 'PlannedVsActualTime') { // FIXME: remove
        $scope.pivotTableType = 'TimeTracking'
      }
      $scope.reportDialog = $scope.reportDialog || $scope.dialogType === 'timereports'
      $scope.contentTemplate = function () {
        if ($scope.reportDialog) {
          return $scope.templates['menu'] // furl
        } else if ($scope.issueTimeBalance) {
          return $scope.templates['issuetimebalance'] // furl
        } else if ($scope.issueKey && !routeParams.viewId) {
          return $scope.templates['issuetime'] // furl
        } else if ($scope.pivotTableType === 'Timesheet') {
          return $scope.templates['timesheet'] // furl
        } else if ($scope.pivotTableType === 'PieChart') {
          return $scope.templates['piechart'] // furl
        } else if ($scope.pivotTableType === 'Calendar') {
          return $scope.templates['calendar'] // furl
        } else { // TimeTracking, TimeEntry, etc
          return $scope.templates['pivottable'] // furl
        }
      }
      $scope.isDayView = ['TimeEntry', 'IssueTime'].indexOf($scope.pivotTableType) >= 0 || routeParams.view === 'day'

      $scope.dialogCloseFunction = function (data) {
        if (data == null) {
          return
        }
        if (data === 'submit' || $scope.pivotTableType === 'IssueTimeBalance') {
          $scope.refresh()
        } else if (angular.isObject(data)) {
          var execute = function () {
            delete $scope.pivotTable.showOnce
            options.queue = $scope.pivotTable.queue
            options.queueToAdd = $scope.pivotTable.queueToAdd
            options.matches = $scope.pivotTable.matches
            $scope.execute()
          }
          var updateWorklog = function (issue) {
            var issueWorklog = issue.worklog || issue.fields.worklog
            var worklogs = issueWorklog.worklogs

            var setOriginalValues = function (issueFields) {
              for (var issueField in issueFields) {
                var field = issueFields['original_' + issueField]
                if (field && typeof field === 'number') {
                  issueFields[issueField] = field
                }
              }
            }

            if (data.worklogOld) {
              for (var i = 0; i < worklogs.length; i++) {
                if (worklogs[i].id === data.worklogOld.id) {
                  worklogs.splice(i, 1)
                  if (options.includeEmpty && options.sumSubTasks) {
                    var tableRow = $scope.pivotTable.rows[issue.key]
                    var tableRowData = tableRow.data
                    for (var row in tableRowData) {
                      if (tableRowData[row].worklog) {
                        setOriginalValues(tableRowData[row].rowKey._issue.fields)
                        delete tableRowData[row].rowKey._issue.nonEmptySubtasks
                        break
                      }
                    }
                    setOriginalValues(tableRow.rowKey._issue.fields)
                    delete tableRow.rowKey._issue.nonEmptySubtasks
                  }
                  break
                }
              }
            }
            if (data.worklog) {
              Array.prototype.push.apply(worklogs, angular.isArray(data.worklog) ? data.worklog : [data.worklog])
            }
            execute()
          }
          var existingIssue = function (issueKey) {
            for (var i in pivottableService.allIssues) {
              if (pivottableService.allIssues[i].key === issueKey) {
                updateWorklog(pivottableService.allIssues[i])
                return true
              }
            }
            return false
          }

          if (!existingIssue(data.issueKey)) {
            delete $scope.pivotTable.queue[data.issueKey]
            return pivottableService.getIssueByKey($scope.pivotTable, data.issueKey, options).promise.then(function (issue) {
              var processIssue = function (parent) {
                pivottableService.processIssue($scope.pivotTable, options, issue).then(function (/* (nullable) issue */) {
                  if (options.sumSubTasks) {
                    pivottableService.checkIfMatches(parent || issue, options).then(function (matches) {
                      if (matches) {
                        pivottableService.allIssues.push(parent || issue)
                        execute()
                      } else {
                        AP.flag.create({
                          title: translations['Work logged successfully'],
                          body: translations['description.Work logged successfully'],
                          type: 'info'
                        })
                      }
                    })
                  } else {
                    execute()
                  }
                })
              }
              if (options.sumSubTasks) {
                var parent = pivottableService.getParentIssue($scope.pivotTable, issue, options)
                if (parent.hasOwnProperty('promise')) {
                  pivottableService.onParentIssue($scope.pivotTable, issue, options, parent, [], function (topParent) {
                    if (topParent) {
                      if (data.worklog) {
                        var copy = angular.copy(issue)
                        delete copy.worklog
                        delete copy.fields.worklog
                        if (angular.isArray(data.worklog)) {
                          data.worklog.forEach(function (worklog) {
                            worklog._issue = copy
                          })
                        } else {
                          data.worklog._issue = copy
                        }
                      }
                      if (!existingIssue(topParent.key)) {
                        processIssue(topParent)
                      }
                    } else { // cycle
                      processIssue()
                    }
                  })
                } else { // no parent
                  processIssue()
                }
              } else { // !sumSubTasks
                processIssue()
              }
            })
          }
        }
      }

      if ($scope.isGadget) {
        $scope.title = routeParams.title // for gadget config
        var title = $scope.title || translations[$scope.pivotTableType]
        if (!$scope.issueKey) {
          AP.jira.setDashboardItemTitle(title)
          AP.jira.DashboardItem.onDashboardItemEdit(function () {
            $rootScope.showGadgetConfiguration = true
            $scope.gadgetDialog = false
            $scope.$apply()
          })
        }
        var reportParams = angular.copy(routeParams)
        if (angular.isDefined(jql)) {
          var m = jql.match(/(\s+|^)order\s+by(\s+.+?)(\s+asc|\s+desc)?$/i)
          if (m) { // strip order by clause
            jql = jql.substring(0, jql.length - m[0].length)
          }
          reportParams.jql = jql
        } else if (externalFilter) {
          var externalFilterId = parseInt(externalFilter)
          if (externalFilterId < 0) {
            reportParams.jql = TimesheetUtils.getPredefinedJql(externalFilterId)
          } else {
            reportParams.filterOrProjectId = 'filter_' + externalFilter
          }
        }
        $scope.reportParamsObject = TimesheetUtils.normalizeObject(
          TimesheetUtils.unwrapBoolObjects(reportParams), true)
        if (jql === '') { // preserve empty JQL
          $scope.reportParamsObject.jql = jql
        }
        if ($scope.issueKey && !$scope.reportParamsObject.pivotTableType) {
          $scope.reportParamsObject.pivotTableType = 'TimeEntry'
        }
        routeParams.jql = $scope.reportParamsObject.jql
        $scope.reportParams = $.param($scope.reportParamsObject, /* traditional */ true)

        if ($scope.reportDialog) {
          AP.events.on('dialog.submit', function () {
            $window.parent.location.href = $scope.hostBaseUrl + '/plugins/servlet/ac/' + $scope.appKey + '/timereports#!' + $scope.reportParams
          })
        }
      }

      var openMoreOptionDialog = function () {
        var now = new Date()
        var worklogDate = this.worklog.date
        worklogDate.setHours(now.getHours())
        worklogDate.setMinutes(now.getMinutes())
        var worklog = {
          issueKey: this.worklog.issueKey,
          date: worklogDate,
          started: worklogDate.toJSON(),
          timeSpent: this.worklog.timeSpent,
          comment: this.worklog.comment,
          user: this.worklog.user,
          initialWorkLabels: pivottableService.workLabels
        }
        $scope.addWorklogDialog(worklog)
      }

      var addWorklogFunc = function (closePopup) {
        if (TimesheetUtils.getFirstLogWorkStarted(this.worklog.date, this.worklog.timeSpent, $scope.jiraConfig, options.configOptions) < $scope.allowToLogFromDate) {
          TimesheetUtils.showPeriodLocked($scope.allowToLogFromDate)
          return
        }
        var popupData = this
        var worklog = angular.copy(popupData.worklog)
        popupData.loading = true
        pivottableService.createWorklog(worklog, options.configOptions, $scope.jiraConfig).then(function (createdWorklog) {
          popupData.loading = false
          closePopup()
          $scope.dialogCloseFunction({ action: 'added', issueKey: popupData.worklog.issueKey, worklog: createdWorklog })
        }, function () {
          popupData.loading = false
        })
      }

      var getSuggestedTimeSpent = function (columnKey) {
        var secondsPassed = columnKey.isToday
          ? Math.round((TimesheetUtils.getCurrentTimeMs() - ($scope.workingTimeInStatus.from || 9) * 60 * 60 * 1000) / 1000 / 15 / 60) * 15 * 60
          : $scope.jiraConfig.workingHoursPerDay * 60 * 60
        var msSpent = Math.max(0, secondsPassed - $scope.pivotTable.totals[columnKey.keyValue].sum)
        return DrawHelper.getFormattedTime(msSpent, $scope.jiraConfig).replace(',', '.')
      }

      $scope.addWorklogPopup = {
        worklog: {
          estimateType: 'auto' // for createWorklog.getEstimateParams
        },
        init: function (columnKey, rowKey) {
          var self = this
          this.columnKey = columnKey
          this.rowKey = rowKey
          this.configOptions = $scope.configOptions
          this.loggedInUser = loggedInUser
          var milliseconds = columnKey.keyValue
          this.worklog.timeSpent = getSuggestedTimeSpent(columnKey)
          this.worklog.comment = ''
          this.worklog.date = new Date(milliseconds)
          this.worklog.issueKey = (rowKey.issue && rowKey.issue.key) ||
            (rowKey._issue && rowKey._issue.key) ||
            (rowKey.keyName === 'issue' && rowKey.keyValue)
          if ($scope.user !== loggedInUser.accountId) {
            this.worklog.user = $scope.paramUserInfo
          }
          // visibility
          this.worklog.visibilityGroup = null
          var currentUser = this.worklog.user || loggedInUser
          if ($scope.visibilityGroup) {
            var visibilityGroups = $scope.visibilityGroup.split(',')
            for (var i = 0; i < currentUser.groups.items.length; i++) {
              var group = currentUser.groups.items[i].name
              if (visibilityGroups.indexOf(group) >= 0) {
                this.worklog.visibilityGroup = group
                break
              }
            }
          }
          this.worklog.visibilityRole = null
          if (!this.worklog.user) {
            var projectKey = this.worklog.issueKey.split('-')[0]
            pivottableService.isProjectAdmin(projectKey).then(function (isAdmin) { // FIXME: ACJIRA-424
              if (isAdmin) { // avoid error in console
                configurationService.getCurrentMemberProjectRoles(projectKey).then(function (roles) {
                  if ($scope.visibilityRole) {
                    var visibilityRoles = $scope.visibilityRole.split(',')
                    for (var role in roles) {
                      if (visibilityRoles.indexOf(role) >= 0) {
                        self.worklog.visibilityRole = role
                        break
                      }
                    }
                  }
                })
              }
            })
          }
        },
        addWorklog: addWorklogFunc,
        openMoreOptionDialog: openMoreOptionDialog
      }

      $scope.editWorklogPopup = {
        worklog: {
          estimateType: 'auto' // for createWorklog.getEstimateParams
        },
        worklogs: [],
        init: function (columnKey, rowKey, rowData, addPossible) {
          this.columnKey = columnKey
          this.rowKey = rowKey
          var columnDate = new Date(columnKey.keyValue)
          this.worklogs = []
          this.colspan = 3
          this.pivotTableType = $scope.pivotTableType
          this.configOptions = $scope.configOptions
          this.loggedInUser = loggedInUser
          this.canLogWork = $scope.canLogWork
          this.canLogWorkForUser = $scope.canLogWorkForUser
          this.addWorklogDialog = $scope.addWorklogDialog
          if ($scope.pivotTableType === 'Calendar') {
            this.currentDay = moment(rowKey.field.startDate).add(parseInt(columnKey.keyValue), 'days')
            this.locked = this.currentDay.toDate() < $scope.allowToLogFromDate
          } else {
            this.currentDay = moment(columnDate)
          }
          if ($scope.pivotTableType !== 'Calendar') {
            this.worklog.timeSpent = getSuggestedTimeSpent(columnKey)
            this.worklog.comment = ''
            this.worklog.date = columnDate
            this.worklog.issueKey = (rowKey.issue && rowKey.issue.key) ||
              (addPossible && rowKey._issue && rowKey._issue.key)
            this.worklog.locked = columnDate < $scope.allowToLogFromDate
          }
          for (var i = 0; i < rowData.length; i++) {
            if ($scope.pivotTableType === 'Calendar' || $scope.groupByOptions.selected
              ? columnKey.keyValue === rowData[i].columnKey.keyValue
              : TimesheetUtils.sameDay(columnDate, new Date(rowData[i].columnKey.keyValue))) {
              var worklog = angular.copy(rowData[i].worklog)
              if (moment(worklog.started).toDate() < $scope.allowToLogFromDate) {
                worklog.locked = true
              }
              worklog.ownWorklog = worklog.author && worklog.author.accountId === loggedInUser.accountId
              if (!worklog._issue && ($scope.pivotTableType === 'Calendar' || $scope.groupByOptions.selected)) {
                worklog._issue = rowData[i].rowKey._issue ||
                                (rowKey.keyName === 'issue' && { key: rowData[i].rowKey._field, summary: rowData[i].rowKey.field.fields.summary })
              }
              this.worklogs.push(worklog)
              if (rowKey.field) { // issue/author infos for worklog popup
                if (worklog._issue) {
                  worklog._issueInfo = worklog._issue
                  this.colspan = 4
                } else if (rowKey.keyName !== 'issue') {
                  worklog._issueInfo = { key: rowData[i].rowKey._issue.key, fields: { summary: rowData[i].rowKey._issue.fields.summary } }
                  this.colspan = 4
                } // else worklog._issueInfo = rowData[i].rowKey.field;
                if ((!$scope.user || $scope.user !== loggedInUser.accountId) && routeParams.groupByField !== 'workeduser') {
                  worklog._authorInfo = worklog.author
                  this.colspan = 4
                }
              } else if (!$scope.user || $scope.user !== loggedInUser.accountId) {
                worklog._authorInfo = worklog.author
                this.colspan = 4
              }
            }
          }
        },
        openDeleteWorklogDialog: function (worklogId, parentIssue) {
          $scope.deleteWorklogDialog(
            worklogId,
            parentIssue ? parentIssue.key : this.worklog.issueKey)
        },
        openEditWorklogDialog: function (worklogId, parentIssue) {
          $scope.updateWorklogDialog(
            worklogId,
            parentIssue ? parentIssue.key : this.worklog.issueKey)
        },
        openMoreOptionDialog: openMoreOptionDialog,
        addWorklog: addWorklogFunc
      }

      var saveWorklog = function (worklog, noReDraw) {
        return pivottableService.saveWorklog(worklog, options.configOptions, $scope.jiraConfig).then(function (savedWorklog) {
          if (noReDraw || worklog.standaloneDialog) return savedWorklog
          var action = worklog.id ? 'updated' : 'added'
          var result = {
            action: action,
            worklog: savedWorklog,
            issueKey: worklog.issueKey
          }
          if (action === 'updated') {
            result.worklogOld = worklog
          }
          return $scope.dialogCloseFunction(result)
        })
      }
      var deleteWorklog = function (worklog) {
        return pivottableService.deleteWorklog(worklog).then(function () {
          return $scope.dialogCloseFunction({
            action: 'deleted',
            worklogOld: worklog,
            issueKey: worklog.issueKey
          })
        })
      }

      AP.events.onPublic('LoggedTime', function (savedWorklog) {
        $scope.dialogCloseFunction({
          action: 'added',
          worklog: savedWorklog,
          issueKey: $scope.issueKey
        })
      }, function (filter) {
        return filter.options.productContext['issue.key'] === $scope.issueKey
      })

      $scope.logSuggested = function () {
        // TODO: visibility, if needed by request (no visibility in native form)
        var next = $q.resolve()
        $scope.disableSuggested = true
        $scope.pivotTable.suggestedEntries.forEach(entry => {
          next = next.then(_ => {
            entry.worklog.timeSpent = DrawHelper.prettyDuration(entry.worklog.timeSpentSeconds, $scope.jiraConfig)
            return saveWorklog(entry.worklog)
          }).catch($scope.refresh)
        })
        next.then(_ => {
          delete $scope.pivotTable.suggestedEntries
          delete $scope.pivotTable.suggestedNum
          delete $scope.pivotTable.suggestedTotal
          delete $scope.disableSuggested
        }).catch($scope.refresh)
      }

      $scope.addWorklogDialog = function (worklog, issueKey, started) {
        var worklogToRemoveFromCache = angular.copy(worklog)
        worklog = worklog || {
          issueKey: issueKey || $scope.issueKey,
          started: new Date(),
          initialWorkLabels: pivottableService.workLabels,
          user: $scope.canLogWorkForUser ? $scope.paramUserInfo : null
        }
        if (started) { // Calendar moment day
          var startTime = TimesheetUtils.sameDay(started.toDate(), new Date())
            ? TimesheetUtils.getCurrentTimeMs()
            : (config.workingTimeInStatus.from || 9) * 60 * 60 * 1000
          worklog.started = started.add(startTime, 'ms').toDate()
        }
        if (!worklog.initialWorkLabels) { // via link or actions button
          worklog.initialWorkLabels = pivottableService.workLabels
        }
        if ($scope.isGadget) {
          $scope.openWorklogGadgetDialog('worklog-gadget-dialog-template', worklog, saveWorklog, worklogToRemoveFromCache)
        } else {
          var modalInstance = $modal.open({
            backdrop: 'static',
            templateUrl: 'worklog-dialog-template',
            controller: 'WorklogDialogController',
            windowSize: 'medium',
            resolve: {
              issueKey: function () {
                return $scope.issueKey
              },
              worklog: function () {
                return worklog
              },
              pivotTableType: function () {
                return $scope.pivotTableType
              },
              loggedInUser: function () {
                return $scope.paramUserInfo || loggedInUser
              },
              jiraConfig: function () {
                return $scope.jiraConfig
              }
            }
          })
          var retrySaveWorklog = function (worklog) {
            worklogToRemoveFromCache && cacheTimeEntryWorklog(worklogToRemoveFromCache, 'removeFromCache')
            saveWorklog(worklog).then(function () {
              worklog.deferred.resolve()
            }).catch(function (err) {
              worklog.deferred.reject(err)
              $timeout(function () { // resubscribe, see aui.js#$modalStack.close
                modalInstance.result.then(retrySaveWorklog).catch(function () {
                  // closed
                })
              })
            })
          }
          modalInstance.result.then(retrySaveWorklog).catch(function () {
            // closed
          })
        }
      }

      $scope.updateWorklogDialog = function (worklogId, issueKey) {
        var worklog = {
          issueKey: issueKey,
          id: worklogId,
          initialWorkLabels: pivottableService.workLabels
        }
        $scope.addWorklogDialog(worklog)
      }

      $scope.deleteWorklogDialog = function (worklogId, issueKey) {
        var worklog = {
          issueKey: issueKey,
          id: worklogId
        }
        if ($scope.isGadget) {
          $scope.openWorklogGadgetDialog('delete-worklog-gadget-dialog-template', worklog, deleteWorklog)
        } else {
          var modalInstance = $modal.open({
            backdrop: 'static',
            templateUrl: 'delete-worklog-dialog-template',
            controller: 'WorklogDialogController',
            windowSize: 'small',
            resolve: {
              issueKey: function () {
                return $scope.issueKey
              },
              worklog: function () {
                return worklog
              },
              pivotTableType: function () {
                return $scope.pivotTableType
              },
              loggedInUser: function () {
                return $scope.paramUserInfo || loggedInUser
              },
              jiraConfig: function () {
                return $scope.jiraConfig
              }
            }
          })
          modalInstance.result.then(function (worklog) {
            deleteWorklog(worklog).then(function () {
              worklog.deferred.resolve()
            })
          }).catch(function () {
            // closed
          })
        }
      }

      $scope.openWorklogGadgetDialog = function (template, worklog, callback, worklogToRemoveFromCache) {
        $scope.gadgetDialog = true
        $scope.worklogDialogData = {
          dialogTemplate: template,
          issueKey: $scope.issueKey,
          worklog: worklog,
          jiraConfig: $scope.jiraConfig,
          pivotTableType: $scope.pivotTableType,
          dismiss: function () {
            $scope.gadgetDialog = false
          },
          close: function (worklog) {
            worklogToRemoveFromCache && cacheTimeEntryWorklog(worklogToRemoveFromCache, 'removeFromCache')
            callback(worklog).then(function (savedWorklog) {
              $scope.gadgetDialog = false
              worklog.deferred.resolve(savedWorklog)
            }).catch(function (err) {
              worklog.deferred.reject(err)
            })
          }
        }
      }

      $scope.openUserPreferencesDialog = function () {
        var changed = {}
        var modalInstance = $modal.open({
          backdrop: 'static',
          templateUrl: $scope.templates['userPreferences'],
          controller: 'UserPreferencesController',
          windowSize: 'large',
          resolve: {
            loggedInUser: function () {
              return loggedInUser
            },
            translations: function () {
              return translations
            },
            changed: function () {
              return changed
            }
          }
        })
        modalInstance.result.then(function () {
          if (changed.changed) {
            $scope.refresh()
          }
        }).catch(function () {
          if (changed.changed) {
            $scope.refresh()
          }
        })
      }

      $scope.openHourlyRatesDialog = function () {
        var changed = {}
        var modalInstance = $modal.open({
          backdrop: 'static',
          templateUrl: $scope.templates['hourlyRates'],
          controller: 'HourlyRatesController',
          windowSize: 'small',
          resolve: {
            loggedInUser: function () {
              return loggedInUser
            },
            translations: function () {
              return translations
            },
            currencySymbol: function () {
              return config.currencySymbol
            },
            changed: function () {
              return changed
            }
          }
        })
        modalInstance.result.then(function () {
          if (changed.changed) {
            $scope.refresh()
          } else {
            modalInstance.dismiss()
          }
        }).catch(function () {
          if (changed.changed) {
            $scope.refresh()
          }
        })
      }

      var getCachedTimeEntriesWorklogs = function () {
        try {
          var worklogs = window.localStorage.getItem(timeentryCacheKey)
          return worklogs ? TimesheetUtils.getJson(LZString.decompressFromUTF16(worklogs)) : []
        } catch (err) {
          console.log(err)
          return []
        }
      }
      var isCurrentWorklogFromCacheStopped = function (worklog) {
        var cachedWorklogs = getCachedTimeEntriesWorklogs()
        var worklogStarted = typeof worklog.started === 'string' ? worklog.started : worklog.started.toJSON()
        return cachedWorklogs.some(function (w) {
          return w.started === worklogStarted && w.stopped
        })
      }
      var cacheTimeEntryWorklog = function (worklog, doNotSave) {
        if (!worklog.started) {
          return
        }
        var log = angular.copy(worklog)
        var cachedWorklogs = getCachedTimeEntriesWorklogs()
        try {
          var worklogStarted = typeof log.started === 'string' ? log.started : log.started.toJSON()
          cachedWorklogs = cachedWorklogs.filter(function (e) {
            return e.started !== worklogStarted
          })
          if (!doNotSave) {
            cachedWorklogs.push(log)
          }
          cachedWorklogs = JSON.stringify(cachedWorklogs)
          window.localStorage.setItem(timeentryCacheKey, LZString.compressToUTF16(cachedWorklogs))
        } catch (err) {
          console.log('timeentry cache error', err)
        }
      }
      var stopAllAnotherTimersInCache = function (worklog) {
        var cachedWorklogs = getCachedTimeEntriesWorklogs()
        var saveNeeded
        var worklogStarted = typeof worklog.started === 'string' ? worklog.started : worklog.started.toJSON()
        cachedWorklogs.forEach(function (worklog) {
          if (worklog.started !== worklogStarted && !worklog.stopped) {
            worklog.stopped = new Date()
            if (worklog.last) {
              worklog.timeSpentSeconds += (moment().unix() - worklog.last)
            }
            saveNeeded = true
          }
        })
        if (saveNeeded) {
          cachedWorklogs = JSON.stringify(cachedWorklogs)
          try {
            window.localStorage.setItem(timeentryCacheKey, LZString.compressToUTF16(cachedWorklogs))
          } catch (err) {
            console.log('timeentry cache error', err)
          }
        }
      }
      var stopTimer = function () {
        if ($rootScope.workTimer) {
          $interval.cancel($rootScope.workTimer)
          delete $rootScope.workTimer
        }
      }
      var startTimer = function (worklog, resumed) {
        var addTime = function (timeSpentSeconds) {
          if ($scope.workEntry) {
            worklog.timeSpentSeconds += timeSpentSeconds
            worklog.last = moment().unix()
            $scope.workEntry.values['2timespent'] = worklog.timeSpentSeconds
            cacheTimeEntryWorklog(worklog)
          } else {
            stopTimer()
          }
        }
        addTime(resumed && worklog.last ? moment().unix() - worklog.last : 0)
        stopAllAnotherTimersInCache(worklog)
        $rootScope.workTimer = $interval(function () {
          if (isCurrentWorklogFromCacheStopped(worklog)) {
            delete $scope.workEntry
            stopTimer()
          } else {
            addTime(TIMER_INTERVAL)
          }
        }, TIMER_INTERVAL * 1000)
      }

      stopTimer()

      $scope.createEntry = function (issue, worklog) {
        var rowKey = new PivotKey.Issue(issue, worklog, options.configOptions)
        var startedColumnKey = new PivotKey.Generic('TimeEntry', '1started', 'time')
        var timespentColumnKey = new PivotKey.Generic('TimeEntry', '2timespent')
        var actionColumnKey = new PivotKey.Generic('TimeEntry', '3action')
        actionColumnKey.showStart = true
        var entry = new PivotEntry(rowKey, startedColumnKey, worklog.started)
        worklog.workDate = moment(worklog.started).toDate()
        worklog.date = worklog.workDate
        entry.worklog = worklog
        entry.addColumn(timespentColumnKey, 0)
        if ($scope.pivotTableType === 'TimeEntry') {
          entry.addColumn(actionColumnKey, entry)
        }
        entry.color = 'gray'
        $scope.pivotTable.addEntry(entry)
        $scope.rowKeySize = TimesheetUtils.colspan($scope.pivotTable) // if first row added
        $scope.pivotTable._sortedColumns = null // reset

        entry.values[timespentColumnKey.keyValue] = worklog.timeSpentSeconds ? worklog.timeSpentSeconds : 1 // force 0h
        return entry
      }

      $scope.removeCachedWorklog = function () {
        cacheTimeEntryWorklog(this.worklog, 'removeFromCache')
        delete $scope.workEntry
        stopTimer()
        $scope.execute()
      }

      $scope.startWork = function (forceStart, stopWork, forceSave) {
        stopTimer()

        var startWork = !stopWork && (!$scope.workEntry || forceStart)
        if ($scope.workEntry) {
          $scope.workEntry.worklog.stopped = new Date()
          cacheTimeEntryWorklog($scope.workEntry.worklog)
          delete $scope.workEntry
        }
        var worklog
        if ($scope.pivotTable.rows[this.worklog.issueKey] && !this.worklog.started) {
          // from pivotCell directive (issue entry)
          worklog = angular.copy($scope.pivotTable.rows[this.worklog.issueKey].columns['3action'].worklog)
          delete worklog.stopped
        } else if (forceStart) { // from issue picker
          worklog = angular.copy(this.worklog)
        } else { // from pivotCell directive (worklog entry)
          worklog = this.worklog
          var resumeWork = true
        }
        if (startWork && !resumeWork) {
          worklog.started = new Date()
          worklog.timeSpentSeconds = 0
          pivottableService.havePermissions(['WORK_ON_ISSUES', 'EDIT_OWN_WORKLOGS', 'DELETE_OWN_WORKLOGS'], 'issuekey=' + worklog.issueKey).then((function (worklog) {
            return function (permissions) {
              if (!permissions[0]) {
                delete worklog.issueKey
                if ($scope.errors.indexOf('error.cannotLogWorkIssue') < 0) {
                  $scope.errors.push('error.cannotLogWorkIssue')
                }
                return
              } else {
                $scope.errors.splice(0, $scope.errors.length)
              }
              if (!permissions[1]) {
                $scope.cannotEditOwnWorklogIssue[worklog.issueKey] = true
              }
              if (!permissions[2]) {
                $scope.cannotDeleteOwnWorklogIssue[worklog.issueKey] = true
              }
              pivottableService.findIssueByKey(worklog.issueKey, options).then(function (issue) {
                $scope.workEntry = $scope.createEntry(issue, worklog)
                cacheTimeEntryWorklog(worklog)
                startTimer(worklog)
              })
            }
          })(worklog))
        } else if (startWork && resumeWork) {
          delete worklog.stopped
          $scope.workEntry = this.entry // from pivotCell directive (worklog entry)
          cacheTimeEntryWorklog(worklog)
          startTimer(worklog)
        } else if (stopWork) {
          worklog.timeSpent = DrawHelper.prettyDuration(worklog.timeSpentSeconds, $scope.jiraConfig)
          if (forceSave) {
            $scope.disableSuggested = true
            saveWorklog(worklog).then(_ => { delete $scope.disableSuggested }) // TODO: visibility, if needed
          } else {
            $scope.addWorklogDialog(worklog)
          }
        } else {
          cacheTimeEntryWorklog(worklog)
        }
      }
      $scope.editWorklog = function (worklog) {
        $scope.updateWorklogDialog(worklog.id, worklog.issueKey)
      }
      $scope.deleteWorklog = function (worklog) {
        $scope.deleteWorklogDialog(worklog.id, worklog.issueKey)
      }

      // STATIC and COOKIE DATA
      // FIXME: cookies are not allowed in iframe:
      // https://ecosystem.atlassian.net/browse/AC-900
      // rework to use backing db/plugin settings/user preferences
      var monthView = $scope.isMonthView = routeParams.view === 'month' || $scope.pivotTableType === 'Calendar'
      $scope.sum = $scope.pivotTableType !== 'PieChart' ? (routeParams.sum || ($scope.isMonthView ? 'week' : 'day')) : 'day'
      $scope.compressed = ['TimeEntry', 'IssueTimeBalance'].indexOf($scope.pivotTableType) >= 0 ? false : (routeParams.showDetails === false || routeParams.showDetails === 'false')
      var numOfWeeks = parseInt(routeParams.numOfWeeks) ||
            (['TimeTracking', 'IssueTimeBalance'].indexOf($scope.pivotTableType) >= 0 ||
                (['Timespent', 'CostReport'].indexOf($scope.pivotTableType) >= 0 && routeParams.numOfWeeks !== undefined)
              ? null : 1)
      $scope.numOfWeeks = { value: numOfWeeks }
      var offset = $scope.offset = parseInt(routeParams.offset) || 0
      var reportingDay = routeParams.reportingDay != null
        ? parseInt(routeParams.reportingDay)
        : $scope.reportingDayConfig || moment.localeData().firstDayOfWeek()
      if (reportingDay === -1) {
        if ($scope.pivotTableType === 'Calendar') {
          reportingDay = (moment().day() + 1) % 7
        } else if ($scope.sum !== 'day') {
          reportingDay = moment.localeData().firstDayOfWeek()
        }
      }
      $scope.reportingDay = reportingDay
      var calculatedStartDate = TimesheetUtils.getLastMondayDate(reportingDay)
      calculatedStartDate = TimesheetUtils.addWeeks(calculatedStartDate, 1 - numOfWeeks + offset)
      var firstDayOfTheMonth = TimesheetUtils.getFirstDayOfTheMonth()
      firstDayOfTheMonth = TimesheetUtils.addMonths(firstDayOfTheMonth, 1 - numOfWeeks + offset)
      $scope.startDate = {}
      if (routeParams.startDate) {
        $scope.startDate.value = moment(routeParams.startDate).toDate()
      } else if (routeParams.startDate === undefined) { // null for allDates
        $scope.startDate.calculated = true
        if (numOfWeeks) { // !TimeTracking
          $scope.startDate.value = $scope.isDayView
            ? moment().startOf('day').toDate()
            : monthView
              ? firstDayOfTheMonth
              : calculatedStartDate
        }
      }
      $scope.endDate = {}
      if ($scope.isDayView) {
        $scope.endDate.value = $scope.startDate.value
      } else if (routeParams.endDate) {
        var endDate = moment(routeParams.endDate)
        if (endDate.isAfter(moment().add(2, 'month'))) {
          endDate = moment().add(2, 'month')
        }
        $scope.endDate.value = endDate.toDate()
      } else if (routeParams.endDate === undefined) { // null for allDates
        $scope.endDate.calculated = true
        if (numOfWeeks) { // !TimeTracking
          var fromDate = $scope.startDate.value || calculatedStartDate
          $scope.endDate.value = monthView
            ? TimesheetUtils.getLastDayOfTheMonth(TimesheetUtils.addMonths(fromDate, numOfWeeks - 1))
            : TimesheetUtils.addDays(fromDate, (7 * (numOfWeeks - 1)) + 6)
        }
      }
      if ($scope.pivotTableType !== 'TimeTracking') { // gadgets and reports
        $scope.showWeekends = { enabled: !(routeParams.showWeekends === false || routeParams.showWeekends === 'false') }
        $scope.isStartDateToday = TimesheetUtils.sameDay($scope.startDate.value, new Date())
        if ($scope.pivotTableType === 'Timesheet' && !routeParams.sum && moment($scope.endDate.value).isAfter(moment($scope.startDate.value).add(2, 'month')) && $scope.sum === 'day') {
          // force sum: week
          $scope.sum = 'week'
        }
      } else {
        $scope.showWeekends = {}
        delete $scope.hideDetails
      }
      $scope.projectKey = (routeParams._allProjects || $scope.issueKey) ? null : (projectKey || $scope.projectKey)
      $scope.sumSubTasks = { enabled: $scope.pivotTableType !== 'Calendar' && (routeParams.sumSubTasks === true || routeParams.sumSubTasks === 'true') }
      if (!angular.isDefined(routeParams.jql)) { // report has no jql param
        $scope.filterOrProjectId = !angular.isDefined(jql) && externalFilter // report dialog has no jql injected
          ? 'filter_' + externalFilter
          : (routeParams.filterOrProjectId && !routeParams.filterOrProjectId.length ? null : routeParams.filterOrProjectId || // backward compatibility for gadget stored [] instead of null
                   ($scope.projectKey && !routeParams._allProjects ? 'project_' + $scope.projectKey : undefined))
      } else {
        $scope.filterOrProjectId = null
      }
      $scope.moreFields = routeParams.moreFields

      if (['Timesheet', 'TimeEntry', 'Timespent', 'CostReport', 'IssueWorkedTimeByUser'].indexOf($scope.pivotTableType) >= 0 && ($scope.filterOrProjectId || routeParams.jql /* not empty */)) {
        $scope.includeEmpty = { enabled: routeParams.includeEmpty === true || routeParams.includeEmpty === 'true', jql: routeParams.jql }
      } else {
        $scope.includeEmpty = { enabled: false }
      }
      $scope.instantView = routeParams.instantView
      $scope.hideMenu = routeParams.hideMenu
      $scope.canLogWork = $scope.canEditOwnWorklog = $scope.canDeleteOwnWorklog = $scope.canEditAllWorklog = $scope.canDeleteAllWorklog = false
      delete $scope.hideTotalColumn
      delete $scope.hideDetails
      delete $scope.hidePrevNext
      delete $scope.user

      var logWorkAbility = ['TimeEntry', 'IssueTime', 'IssueTimeBalance', 'Calendar', 'Timesheet'].indexOf($scope.pivotTableType) >= 0 &&
        !config.tempoToken.val

      if ($scope.pivotTableType === 'TimeBalance') {
        $scope.hideTotalColumn = true
      } else if ($scope.pivotTableType === 'TimeTracking') {
        $scope.hideTotalColumn = true
        $scope.hideDetails = !$scope.sumSubTasks.enabled && (routeParams.groupByField == null || routeParams.groupByField === 'workeduser')
        if ($scope.hideDetails) {
          $scope.compressed = true
        }
        $scope.hidePrevNext = routeParams.startDate === undefined && routeParams.endDate === undefined && !routeParams.numOfWeeks
      } else {
        $scope.allUsers = { enabled: routeParams.allUsers }
        if ($scope.pivotTableType === 'IssueWorkedTimeByUser' || $scope.pivotTableType === 'IssueWorkedTimeByStatus' || $scope.pivotTableType === 'PieChart') {
          $scope.hidePrevNext = routeParams.startDate === null && routeParams.endDate === null
        } else {
          $scope.hidePrevNext = false
        }
        if (['Timesheet', 'Timespent', 'CostReport', 'PieChart', 'TimeEntry', 'IssueTime', 'IssueTimeBalance', 'Calendar'].indexOf($scope.pivotTableType) >= 0) {
          $scope.user = !routeParams.allUsers && (!routeParams.group || !routeParams.group.length) && $scope.pivotTableType !== 'IssueTimeBalance'
            ? routeParams.user ? decodeURIComponent(routeParams.user) : loggedInUser.accountId : null
          if (logWorkAbility) {
            var $globalPermissions = pivottableService.havePermissions(['WORK_ON_ISSUES',
              'EDIT_OWN_WORKLOGS', 'DELETE_OWN_WORKLOGS', 'EDIT_ALL_WORKLOGS', 'DELETE_ALL_WORKLOGS']).then(function (permissions) {
              $scope.editWorklogPopup.canLogWork = $scope.canLogWork = $scope.user === loggedInUser.accountId && !config.disableLogWork.val && permissions[0]
              $scope.editWorklogPopup.cannotLogWorkIssue = $scope.cannotLogWorkIssue = {}
              $scope.editWorklogPopup.canEditOwnWorklog = $scope.canEditOwnWorklog = !config.disableLogWork.val && permissions[1]
              $scope.editWorklogPopup.cannotEditOwnWorklogIssue = $scope.cannotEditOwnWorklogIssue = {}
              $scope.editWorklogPopup.canDeleteOwnWorklog = $scope.canDeleteOwnWorklog = !config.disableLogWork.val && permissions[2]
              $scope.editWorklogPopup.cannotDeleteOwnWorklogIssue = $scope.cannotDeleteOwnWorklogIssue = {}
              $scope.editWorklogPopup.canEditAllWorklog = $scope.canEditAllWorklog = !config.disableLogWork.val && permissions[3]
              $scope.editWorklogPopup.cannotEditAllWorklogIssue = $scope.cannotEditAllWorklogIssue = {}
              $scope.editWorklogPopup.canDeleteAllWorklog = $scope.canDeleteAllWorklog = !config.disableLogWork.val && permissions[4]
              $scope.editWorklogPopup.cannotDeleteAllWorklogIssue = $scope.cannotDeleteAllWorklogIssue = {}

              if (routeParams.logWork && $scope.canLogWork) {
                $scope.addWorklogDialog()
              }
              delete routeParams.logWork // for saveRouteParams
            })
          }
          if ($scope.pivotTableType === 'TimeEntry') {
            $scope.hideTotalColumn = true
            delete $scope.group
            $scope.user = loggedInUser.accountId
          }
          if (['Timespent', 'CostReport'].indexOf($scope.pivotTableType) >= 0) {
            $scope.hideTotalColumn = true
          }
        }
        if ($scope.pivotTableType === 'PieChart') {
          $scope.hideDetails = true
        }
      }
      var $hourlyRates = $scope.pivotTableType === 'CostReport' ? configurationService.getHourlyRates() : null
      if (['TimeTracking', 'TimeBalance', 'IssueWorkedTimeByStatus', 'IssuePassedTimeByStatus', 'IssuePassedTimeByResolution'].indexOf($scope.pivotTableType) >= 0) {
        // optional user
        $scope.user = routeParams.user && !routeParams.group ? decodeURIComponent(routeParams.user) : undefined
        if (!$scope.user) {
          $scope.paramUserInfo = null
        }
        $scope.group = routeParams.group
      } else if (!routeParams.allUsers && $scope.pivotTableType !== 'TimeEntry') {
        $scope.group = routeParams.group
      }
      if ($scope.pivotTableType !== 'TimeEntry') {
        $scope.excludeGroup = routeParams.excludeGroup
      }
      $scope.errors = validationService.validate($scope)

      var tenant = $scope.hostBaseUrl ? $scope.hostBaseUrl.match(/https?:\/\/(.+?)[\.|:]/)[1] : ''
      var timeentryCacheKey = 'timeentry' + tenant + $scope.user

      $scope.changeWeek = function (value) {
        var monthView = $scope.isMonthView && !$scope.isDayView
        if (routeParams.startDate || $scope.isDayView) {
          var newStartDate = TimesheetUtils.changeDate($scope.startDate.value, value, $scope.pivotTableType !== 'Timesheet', monthView, $scope.reportingDay)
          if (newStartDate != null) {
            routeParams.startDate = newStartDate
          }
        } else {
          routeParams.offset = $scope.offset + ($scope.isMonthView ? value : value / 7)
        }
        if (routeParams.endDate) {
          var newEndDate = TimesheetUtils.changeDate($scope.endDate.value, value, true, monthView, $scope.reportingDay)
          if (newEndDate != null) {
            routeParams.endDate = newEndDate
          }
        }
        if (!$scope.loading) {
          routeParams.loaded = $scope.loaded
        }
        saveRouteParams(routeParams)
      }

      var isOutOfTodayGreater = function (date1, date2) {
        return !moment().isBetween(TimesheetUtils.addWeeks($scope.startDate.value, $scope.numOfWeeks.value - 1), TimesheetUtils.addDays($scope.endDate.value, 1)) && date1.getTime() > date2.getTime()
      }

      $scope.showPrevToday =
            $scope.startDate.value &&
            $scope.endDate.value &&
            $scope.isDayView
              ? TimesheetUtils.isAfterDate($scope.startDate.value, new Date())
              : isOutOfTodayGreater(TimesheetUtils.addWeeks($scope.startDate.value, $scope.numOfWeeks.value - 1), new Date())

      $scope.showNextToday =
            $scope.startDate.value &&
            $scope.endDate.value &&
            $scope.isDayView
              ? TimesheetUtils.isAfterDate(new Date(), TimesheetUtils.addDays($scope.endDate.value, 1))
              : isOutOfTodayGreater(new Date(), TimesheetUtils.addDays($scope.endDate.value, 1))

      $scope.moveToday = function () {
        delete routeParams.startDate
        delete routeParams.endDate
        delete routeParams.offset
        if (!$scope.loading) {
          routeParams.loaded = $scope.loaded
        }
        saveRouteParams(routeParams)
      }

      $scope.toggleMonthView = function () {
        if ($scope.isMonthView) {
          routeParams.view = 'day'
          routeParams.sum = 'day'
        } else if ($scope.isDayView) {
          routeParams.view = 'week'
          routeParams.sum = 'day'
        } else {
          routeParams.view = 'month'
          routeParams.sum = 'week'
        }
        if (!$scope.loading) {
          routeParams.loaded = $scope.loaded
        }
        saveRouteParams(routeParams)
      }

      $scope.toggleShowDetails = function () {
        if ($scope.compressed) {
          routeParams.showDetails = true // for gadget
        } else {
          routeParams.showDetails = false
        }
        saveRouteParams(routeParams, true)
        $scope.compressed = !$scope.compressed
      }

      $scope.groupByChanged = function () {
        if (!$scope.groupByOptions.selected) {
          delete routeParams.groupByField
        } else {
          routeParams.groupByField = $scope.groupByOptions.selected
        }
        saveRouteParams(routeParams)
      }

      $scope.categorizeByChanged = function () {
        if (!$scope.categorizeByOptions.selected) {
          delete routeParams.categorizeByField
        } else {
          routeParams.categorizeByField = $scope.categorizeByOptions.selected
        }
        saveRouteParams(routeParams)
      }

      $scope.moreFieldsChanged = function (values) {
        if (values != null && values !== '') {
          routeParams.moreFields = values.split(',')
        } else {
          delete routeParams.moreFields
        }
        saveRouteParams(routeParams)
      }

      $scope.orderByChanged = function () {
        if (!$scope.orderByOptions.selected) {
          delete routeParams.orderByField
        } else {
          routeParams.orderByField = $scope.orderByOptions.selected
        }
        saveRouteParams(routeParams)
      }

      $scope.filterByChanged = function (values) {
        if (values) {
          routeParams.filterOrProjectId = values.split(',')
          delete routeParams._allProjects
          delete routeParams.jql
        } else {
          delete routeParams.filterOrProjectId
          routeParams._allProjects = true
        }
        saveRouteParams(routeParams)
      }

      $scope.timeTrackingColumnsChanged = function (values) {
        if (values) {
          routeParams.timeTrackingColumns = values.split(',')
        } else {
          delete routeParams.timeTrackingColumns
        }
        saveRouteParams(routeParams)
      }

      $scope.timeBalanceColumnsChanged = function (values) {
        if (values) {
          routeParams.timeBalanceColumns = values.split(',')
        } else {
          delete routeParams.timeBalanceColumns
        }
        saveRouteParams(routeParams)
      }

      $scope.timeInStatusCategoriesChanged = function (values) {
        if (values) {
          routeParams.timeInStatusCategories = values.split(',')
        } else {
          delete routeParams.timeInStatusCategories
        }
        saveRouteParams(routeParams)
      }

      $scope.childrenFilterChanged = function () {
        if ($scope.childrenFilterOptions.selected == null) {
          delete routeParams.childrenFilterId
        } else {
          routeParams.childrenFilterId = $scope.childrenFilterOptions.selected
        }
        saveRouteParams(routeParams)
      }

      $scope.groupChange = function (groups) {
        if (groups) {
          routeParams.group = groups.split(',')
          delete routeParams.allUsers
        } else {
          delete routeParams.group
        }
        saveRouteParams(routeParams)
      }

      $scope.excludeGroupChange = function (groups) {
        if (groups) {
          routeParams.excludeGroup = groups.split(',')
        } else {
          delete routeParams.excludeGroup
        }
        saveRouteParams(routeParams)
      }

      $scope.numOfWeeksChange = function (numOfWeeks) {
        if (numOfWeeks) {
          routeParams.numOfWeeks = numOfWeeks
        } else if (['Timespent', 'CostReport'].indexOf($scope.pivotTableType) >= 0) {
          routeParams.numOfWeeks = 0 // all time
        } else {
          delete routeParams.numOfWeeks
        }
        saveRouteParams(routeParams)
      }

      $scope.startChangeDate = function (date) {
        changeDate('startDate', date)
      }

      $scope.endChangeDate = function (date) {
        changeDate('endDate', date)
      }

      var changeDate = function (prop, date) {
        if (date != null && date !== '') {
          routeParams[prop] = date
        } else {
          delete routeParams[prop]
        }
        saveRouteParams(routeParams)
      }

      $scope.reportingDayChanged = function () {
        if ($scope.reportingDayOptions.selected == null) {
          delete routeParams.reportingDay
        } else {
          routeParams.reportingDay = $scope.reportingDayOptions.selected
        }
        if (!$scope.loading) {
          routeParams.loaded = $scope.loaded
        }
        saveRouteParams(routeParams)
      }

      $scope.toggleShowWeekends = function () {
        if (routeParams.showWeekends === false || routeParams.showWeekends === 'false') {
          delete routeParams.showWeekends
        } else {
          routeParams.showWeekends = false
        }
        if (!$scope.loading) {
          routeParams.loaded = $scope.loaded
        }
        saveRouteParams(routeParams)
      }

      $scope.userChange = function (user) {
        if (user) {
          routeParams.user = encodeURIComponent(user)
          delete routeParams.allUsers
        } else {
          delete routeParams.user
          if (!routeParams.group || !routeParams.group.length) {
            routeParams.allUsers = true
          }
        }
        saveRouteParams(routeParams)
      }

      $scope.allUsersChange = function (allUsers) {
        if (allUsers) {
          routeParams.allUsers = true
        } else {
          delete routeParams.allUsers
        }
        saveRouteParams(routeParams)
      }

      $scope.sumSubTasksChange = function (sumSubTasks) {
        if (sumSubTasks) {
          routeParams.sumSubTasks = true
        } else {
          delete routeParams.sumSubTasks
        }
        saveRouteParams(routeParams)
      }

      $scope.includeEmptyChange = function (includeEmpty) {
        if (includeEmpty) {
          routeParams.includeEmpty = true
        } else {
          delete routeParams.includeEmpty
        }
        saveRouteParams(routeParams)
      }

      $scope.instantViewChange = function (instantView) {
        if (instantView) {
          routeParams.instantView = true
        } else {
          delete routeParams.instantView
        }
        // mark it as loaded when turning off instantView
        routeParams.loaded = !instantView
        saveRouteParams(routeParams)
      }

      $scope.setStartAt = function (startAt) {
        if (startAt) {
          routeParams.startAt = startAt
        } else {
          delete routeParams.startAt
        }
        if (!$scope.loading) {
          routeParams.loaded = $scope.loaded
        }
        saveRouteParams(routeParams)
      }

      $scope.toggleHideMenu = function () {
        $scope.hideMenu = !$scope.hideMenu
        if ($scope.hideMenu) {
          routeParams.hideMenu = $scope.hideMenu
        } else {
          delete routeParams.hideMenu
        }
        saveRouteParams(routeParams, true)
      }

      $scope.refresh = function () {
        pivottableService.resetState()
        configurationService.resetState()
        viewService.resetState()
        if (!$scope.loading) {
          routeParams.loaded = $scope.loaded
        }
        if (routeParams.viewId && !$scope.showSaveButton) {
          routeParams = { viewId: routeParams.viewId } // refresh view atleast if not modified
        }
        routeParams._ = Date.now()
        $location.search(routeParams)
      }

      $scope.removeMenuOption = function (key, value) {
        var item = routeParams[key]
        if (angular.isArray(item) && item.length > 1) {
          if (key === 'filterOrProjectId') {
            var option = $scope.filterByOptions.findOption(value, 'isLabel')
            if (option != null) {
              value = option.id
            }
          } else if (key === 'moreFields') {
            var option = $scope.moreFieldsOptions.findOption(value, 'isLabel')
            if (option != null) {
              value = option.id
            }
          }
          item.splice(item.indexOf(value), 1)
          // } else if (item === undefined) {
          //    routeParams[key] = null;
        } else {
          if (key === 'user' && $scope.user === loggedInUser.accountId &&
                    (!routeParams.group || !routeParams.group.length) &&
                    ['Timesheet', 'Timespent', 'CostReport', 'PieChart'].indexOf($scope.pivotTableType) >= 0) {
            routeParams.allUsers = true
          } else if (key === 'filterOrProjectId') {
            routeParams._allProjects = true
          }
          delete routeParams[key]
        }
        if (['filterOrProjectId', 'moreFields', 'group'].indexOf(key) >= 0) {
          $scope.$broadcast(key + 'Updated', routeParams[key])
        }
        saveRouteParams(routeParams)
      }

      $scope.getSelectedOptionLabels = function () {
        var result = []
        if ($scope.issueKey && $scope.issueTimeOptions && $scope.issueTimeOptions.selected === 'view_-1') {
          return result
        }
        var keys = Object.keys(routeParams)
        var addToResult = function (key, value, css) {
          var i18nParam = 'param.' + key
          if (key === 'filterOrProjectId') {
            var option = $scope.filterByOptions.findOption(value)
            if (option != null) {
              value = option.label
              if (option.id.indexOf('project_') === 0) {
                i18nParam += '.project'
              }
              if (option.id.indexOf('filter_') === 0 || option.id.indexOf('jql_') === 0) {
                i18nParam += '.filter'
              }
            }
          } else if (key === 'childrenFilterId') {
            var option = $scope.childrenFilterOptions.findOption(value)
            if (option != null) {
              value = option.label
            }
          } else if (key === 'groupByField') {
            var option = $scope.groupByOptions.findOption(value)
            if (option != null) {
              value = option.label
            }
          } else if (key === 'categorizeByField') {
            var option = $scope.categorizeByOptions.findOption(value)
            if (option != null) {
              value = option.label
            }
          } else if (key === 'moreFields') {
            var option = $scope.moreFieldsOptions.findOption(value)
            if (option != null) {
              value = option.label
            }
          } else if (key === 'orderByField') {
            var option = $scope.orderByOptions.findOption(value)
            if (option != null) {
              value = option.label
            }
          } else if (key === 'user') {
            value = $scope.paramUserInfo.displayName
          } else if (key === 'startDate' || key === 'endDate') {
            value = moment(value).format('YYYY-MM-DD') // TODO: format('l') along with date picker
          } else if (key === 'reportingDay') {
            value = value === -1 ? translations['Today']
              : moment.localeData().weekdays(moment().day(parseInt(value)))
          } else if (key === 'jql') {
            value = decodeURIComponent(TimesheetUtils.replaceAll(value, '+', ' '))
          }
          result.push({
            css: css,
            label: i18nParam,
            labelParam: value,
            key: key
          })
        }
        for (var i = 0; i < keys.length; i++) {
          var key = keys[i]
          if (['user', 'allUsers', 'startDate', 'endDate', 'groupByField', 'categorizeByField', 'moreFields',
            'orderByField', 'filterOrProjectId', 'childrenFilterId', 'group', 'excludeGroup',
            'reportingDay', 'jql', 'numOfWeeks'].indexOf(key) < 0) {
            continue
          } else if (key === 'childrenFilterId' && !$scope.showChildrenFilter) {
            continue
          } else if (key === 'endDate' && $scope.pivotTableType === 'TimeEntry') {
            continue
          } else if (key === 'allUsers' && ['Timesheet', 'Timespent', 'CostReport', 'PieChart', 'Calendar'].indexOf($scope.pivotTableType) < 0) {
            continue
          } else if (key === 'user' && !$scope.paramUserInfo) { // optional user
            continue
          } else if (key === 'moreFields' && $scope.pivotTableType === 'PieChart') {
            continue
          } else if (key === 'orderByField' && routeParams.groupByField) {
            continue
          } else if (key === 'reportingDay' && $scope.pivotTableType !== 'Timesheet') {
            continue
          } else if (['TimeTracking', 'IssuePassedTimeByStatus'].indexOf($scope.pivotTableType) >= 0 && key === 'groupByField' && ['workeduser', 'workeddate', 'workcreated'].indexOf(routeParams[key]) >= 0) {
            continue
          } else if (['Timespent', 'CostReport', 'IssueWorkedTimeByUser', 'IssueWorkedTimeByStatus'].indexOf(options.pivotTableType) < 0 && key === 'groupByField' && ['workedweek'].indexOf(routeParams[key]) >= 0) {
            continue
          } else if (((routeParams.group || routeParams.allUsers) && key === 'user') || (routeParams.allUsers && key === 'group')) {
            continue
          } else if (routeParams[key] == null && key !== 'numOfWeeks') { // ignore empty params
            continue
          } else if (['group', 'groupByField', 'categorizeByField', 'allUsers'].indexOf(key) >= 0 && !routeParams[key]) {
            continue
          } else if ($scope.pivotTableType === 'TimeEntry' && ['group', 'allUsers', 'groupByField'].indexOf(key) >= 0) {
            continue
          } else if ($scope.pivotTableType === 'Calendar' && ['groupByField'].indexOf(key) >= 0) {
            continue
          } else if (key === 'user' && $scope.pivotTableType === 'IssueWorkedTimeByUser') {
            continue
          } else if (key === 'numOfWeeks' && $scope.numOfWeeks.value) {
            continue
          }
          var value = routeParams[key]
          if (angular.isArray(value)) {
            for (var j = 0; j < value.length; j++) {
              addToResult(key, value[j])
            }
          } else if (value) {
            addToResult(key, value)
          } else if (key === 'jql') { // empty JQL
            addToResult(key, value)
          } else if (key === 'numOfWeeks' && !$scope.startDate.value && !$scope.endDate.value && ['Timespent', 'CostReport'].indexOf($scope.pivotTableType) >= 0) { // all time
            addToResult(key, value)
          }
        }
        if (!routeParams.user && !routeParams.allUsers && (!routeParams.group || !routeParams.group.length) && ['Timesheet', 'Timespent', 'CostReport', 'PieChart'].indexOf($scope.pivotTableType) >= 0) {
          addToResult('user', loggedInUser.accountId)
        }
        if (!routeParams.filterOrProjectId && $scope.projectKey && !routeParams._allProjects) {
          addToResult('filterOrProjectId', 'project_' + $scope.projectKey)
        }
        if (routeParams.startDate === undefined && $scope.numOfWeeks.value) {
          addToResult('startDate', $scope.startDate.value, 'inactive')
        } else if (routeParams.startDate === null) {
          addToResult('startDate', moment('1970-01-01'))
        }
        if ($scope.pivotTableType !== 'TimeEntry') {
          if (routeParams.endDate === undefined && $scope.numOfWeeks.value) {
            addToResult('endDate', $scope.endDate.value, 'inactive')
          } else if (routeParams.endDate === null) {
            addToResult('endDate', moment('2099-12-31'))
          }
        }
        return result
      }

      var getLicenseStatus = function () {
        var msg = ''
        if ($scope.license === 'expired') {
          msg = '<span class="red">LICENSE EXPIRED</span>'
        } else if ($scope.license === 'none') {
          msg = '<span class="red">NO LICENSE</span>'
        } else if ($scope.license !== 'active') {
          msg = '<span class="red">LICENSE ERROR</span>'
        }

        return {
          isActive: $scope.license === 'active',
          message: $sce.trustAsHtml(msg)
        }
      }
      $scope.licenseStatus = getLicenseStatus()

      var filterByUser = ['Timesheet', 'Timespent', 'CostReport', 'PieChart', 'TimeEntry', 'IssueTime', 'Calendar'].indexOf($scope.pivotTableType) >= 0 ||
             $scope.user

      // DYNAMIC DATA (uses request params)
      // e.g. http://localhost:5000/#!/?groupByField=status
      // http://localhost:5000/#!/?view=month&groupByField=assignee
      var options = {
        pivotTableType: $scope.pivotTableType,
        timeTrackingColumns: $scope.timeTrackingColumns || configurationService.getDefaultTimeTrackingColumns(),
        timeBalanceColumns: $scope.timeBalanceColumns || configurationService.getDefaultTimeBalanceColumns(),
        timeInStatusCategories: $scope.timeInStatusCategories,
        groupByField: routeParams.groupByField,
        categorizeByField: routeParams.groupByField && routeParams.categorizeByField,
        moreFields: TimesheetUtils.wrapIntoArray(routeParams.moreFields),
        orderByField: routeParams.orderByField,
        monthView: $scope.isMonthView,
        startDate: $scope.startDate.value,
        endDate: $scope.endDate.value,
        filterOrProjectId: $scope.filterOrProjectId,
        groups: !$scope.group || angular.isArray($scope.group)
          ? $scope.group : /* from $routeParams */ [$scope.group],
        excludeGroups: !$scope.excludeGroup || angular.isArray($scope.excludeGroup)
          ? $scope.excludeGroup : /* from $routeParams */ [$scope.excludeGroup],
        sumSubTasks: $scope.sumSubTasks.enabled,
        sum: $scope.sum,
        reportingDay: $scope.reportingDay,
        showWeekends: $scope.showWeekends.enabled,
        jql: routeParams.jql,
        issueKey: $scope.issueKey, // panel
        issueId: $scope.issueId, // panel
        dashboardId: $scope.dashboardId,
        dashboardItemId: $scope.dashboardItemId,
        includeEmpty: $scope.includeEmpty.enabled && ($scope.filterOrProjectId || routeParams.jql /* not empty! */),
        loggedInUser: loggedInUser,
        _fields: $scope._fields,
        queue: $scope.queue,
        jqlQuery: $scope.jqlQuery
      }
      if (filterByUser) {
        options.user = $scope.user
      }

      $scope.excelView = function (viewType) {
        var doExcelView = function (pivotTable) {
          var excelOptions = {
            hostBaseUrl: $scope.hostBaseUrl,
            pivotTableType: $scope.pivotTableType,
            timeTrackingColumns: $scope.timeTrackingColumns || configurationService.getDefaultTimeTrackingColumns(),
            timeBalanceColumns: $scope.timeBalanceColumns || configurationService.getDefaultTimeBalanceColumns(),
            timeInStatusCategories: $scope.timeInStatusCategories,
            moreFields: TimesheetUtils.moreFieldsForExport(routeParams, options.configOptions.exportColumns, $scope.allFields),
            _categorizeByField: routeParams.groupByField && routeParams.categorizeByField,
            categorizeByFieldObject: options.categorizeByFieldObject,
            categorizeByFieldOption: $scope.categorizeByOptions.findOption(routeParams.categorizeByField),
            _groupByField: routeParams.groupByField,
            groupByFieldObject: options.groupByFieldObject,
            groupByFieldOption: $scope.groupByOptions.findOption(routeParams.groupByField),
            orderByField: routeParams.orderByField,
            monthView: $scope.isMonthView,
            startDate: $scope.startDate.value,
            endDate: $scope.endDate.value,
            worklogAuthors: options.groups && options.groups.length > 0 ? pivottableService.worklogAuthors : null,
            sumSubTasks: $scope.sumSubTasks.enabled,
            configOptions: options.configOptions,
            includeEmpty: $scope.includeEmpty.enabled && ($scope.filterOrProjectId || routeParams.jql /* not empty! */),
            restrictedProjects: options.restrictedProjects,
            restrictedGroups: options.restrictedGroups,
            loggedInUser: loggedInUser,
            jiraConfig: $scope.jiraConfig,
            workingTimeInStatus: $scope.workingTimeInStatus,
            moreFieldsOptions: $scope.moreFieldsOptions,
            compressed: $scope.compressed,
            translations: translations,
            showDetailsCount: $scope.showDetailsCount,
            userInfos: options.userInfos
          }
          switch (viewType) {
            case 'html':
              excelOptions.fileName = 'exportView.xls'
              excelOptions.excelViewClass = HtmlView
              break
            case 'csv':
              excelOptions.excelViewClass = CsvView
              break
            default:
              excelOptions.excelViewClass = ExcelView
          }
          if (filterByUser) {
            excelOptions.user = $scope.user
          }
          excelOptions.pivotTable = pivotTable
          TimesheetUtils.sortByProperty(pivottableService.allIssues, 'key', false, PivotKey.Issue.comparator)
          var excelView = new excelOptions.excelViewClass(pivottableService.allIssues)
          excelView.download(excelOptions)
        }
        if (!$scope.loaded) {
          $scope.loading++
          pivottableService.getPivotTable(loggedInUser, options).then(function (pivotTable) {
            $scope.loading--
            doExcelView(pivotTable)
          })
        } else {
          doExcelView($scope.pivotTable)
        }
      }

      pivottableService.isAdministrator().then(function (isAdmin) {
        $scope.isAdmin = isAdmin
      })

      if ($scope.loading && $scope.loaded) {
        pivottableService.cancel()
      }
      $scope.loading++
      delete $scope.pivotTable

      var $paramUserInfo = $q.when($scope.user ? $scope.user === loggedInUser.accountId ? loggedInUser : pivottableService.getUserInfo($scope.user) : null).then(function (user) {
        $scope.paramUserInfo = user
        return user
      })

      var $fields = pivottableService.getFields()
      $fields.then(function () {
        var moreFields = TimesheetUtils.wrapIntoArray(routeParams.moreFields || [])
        if ((['TimeTracking', 'TimeBalance'].indexOf($scope.pivotTableType) >= 0 ||
                    TimesheetUtils.findOne(moreFields, pivottableService.allNumericFields) ||
                    pivottableService.isTimeTrackingColumnsInMoreFields(moreFields)) &&
                    $scope.sumSubTasks.enabled) {
          $scope.showChildrenFilter = true
          $scope.childrenFilterId = routeParams.childrenFilterId
          options.childrenFilterId = $scope.childrenFilterId
        } else {
          $scope.showChildrenFilter = false
          delete $scope.childrenFilterId
        }
      })

      var $restrictedByAuditorsGroups = pivottableService.restrictedByAuditorsGroups(loggedInUser)
      $restrictedByAuditorsGroups.then(restricted => {
        if (config.accountantsGroups.val) {
          for (var i = 0; i < loggedInUser.groups.items.length && !$scope.userIsAccountant; i++) {
            $scope.userIsAccountant = config.accountantsGroups.val.indexOf(loggedInUser.groups.items[i].name) >= 0
          }
        } else {
          $scope.userIsAccountant = config.auditorsGroups.val != null && !restricted
        }
        if ($scope.userIsAccountant) {
          $scope.viewOptions && $scope.viewOptions.addOption(translations['CostReport'], 'pivotTableType_CostReport')
          $scope.pivotTableTypeOptions.addOption(translations['CostReport'], 'CostReport')
        } else if ($scope.pivotTableType === 'CostReport') {
          $scope.pivotTableType = 'Timespent'
          options.pivotTableType = 'Timespent'
          $scope.viewOptions && ($scope.viewOptions.selected = 'pivotTableType_Timespent')
        }
      })

      $scope.loaded = $scope.isGadget || $scope.instantView || routeParams.loaded || ['TimeEntry', 'IssueTime'].indexOf($scope.pivotTableType) >= 0 ||
            ($scope.user === loggedInUser.accountId && !routeParams.startDate && !routeParams.endDate && numOfWeeks === 1)
      var restrictable = ['TimeTracking', 'TimeBalance', 'IssueTimeBalance'].indexOf($scope.pivotTableType) < 0 &&
            (routeParams.allUsers || options.groups || !$scope.user || $scope.user !== loggedInUser.accountId)
      restrictable = options.restrictable = restrictable || ['IssueWorkedTimeByUser', 'TimeBalance'].indexOf($scope.pivotTableType) >= 0
      var $restricted = restrictable ? $restrictedByAuditorsGroups : $q.resolve(false)
      $restricted.then(function (restricted) {
        if (restrictable && restricted) {
          $scope.loading--
          $scope.restrictionError = { msgKey: 'error.restriction.auditorsGroups' }
        } else if (restrictable && !$scope.licenseStatus.isActive) {
          $scope.loading--
          $scope.restrictionError = { msgKey: 'error.restriction.licenseError' }
        } else {
          delete $scope.restrictionError
          $scope.execute = function () {
            if ($scope.pivotTableType === 'PieChart') {
              $scope.pivotTable = {} // trigger pie chart loading
            }
            $scope.loading++
            $scope.loaded = true
            $scope.$loading = $q.all([pivottableService.getPivotTable(loggedInUser, options), $globalPermissions]).then(function (values) {
              var pivotTable = values[0]
              $scope.restrictedProjects = options.restrictedProjects && options.restrictedProjects.join(',')
              $scope.restrictedGroups = options.restrictedGroups && (options.restrictedGroups.join(',') || 'NONE')
              $scope.queue = pivotTable.queue
              $scope.jqlQuery = options.jqlQuery // for AUI Issue Picker
              $scope._fields = options._fields // for next run
              $scope.loading--

              if (['TimeEntry', 'IssueTime'].indexOf($scope.pivotTableType) >= 0 && $scope.isStartDateToday && $scope.canLogWork) {
                var unPersistentWorklogs = getCachedTimeEntriesWorklogs()
                angular.forEach(unPersistentWorklogs, function (worklog) {
                  if (!$scope.issueKey || $scope.issueKey === worklog.issueKey) {
                    pivottableService.findIssueByKey(worklog.issueKey, options).then(function (issue) {
                      var entry = $scope.createEntry(issue, worklog)
                      if (!entry.worklog.stopped && entry.worklog.started) {
                        $scope.workEntry = entry
                        startTimer(entry.worklog, 'resume')
                      }
                    })
                  }
                })
              }
              pivotTable.sortedColumns() // for sortedRows()
              var sortedRows = pivotTable.sortedRows()
              $scope.pivotTable = pivotTable
              $scope.rowKeySize = TimesheetUtils.colspan(pivotTable)
              var issuesCount = sortedRows.length
              var startAt = routeParams.startAt || 0
              $scope.issuesCount = {
                total: issuesCount,
                startAt: startAt < issuesCount ? startAt : 0,
                maxResults: 50
              }
              $scope.pages = []
              $scope.issuesCount.end = Math.min($scope.issuesCount.startAt + $scope.issuesCount.maxResults, $scope.issuesCount.total)
              var pagesNum = Math.ceil($scope.issuesCount.total / $scope.issuesCount.maxResults)
              for (var i = 0; i < pagesNum; i++) {
                $scope.pages.push(i)
              }
              var setPermissions = function (issueKey, allDisabled) {
                if (!allDisabled) {
                  pivottableService.havePermissions(['WORK_ON_ISSUES', 'EDIT_OWN_WORKLOGS', 'DELETE_OWN_WORKLOGS',
                    'EDIT_ALL_WORKLOGS', 'DELETE_ALL_WORKLOGS'], 'issuekey=' + issueKey).then(function (permissions) {
                    if (!permissions[0]) {
                      $scope.editWorklogPopup.cannotLogWorkIssue[issueKey] = true
                      $scope.cannotLogWorkIssue[issueKey] = true
                    }
                    if (!permissions[1]) {
                      $scope.editWorklogPopup.cannotEditOwnWorklogIssue[issueKey] = true
                      $scope.cannotEditOwnWorklogIssue[issueKey] = true
                    }
                    if (!permissions[2]) {
                      $scope.editWorklogPopup.cannotDeleteOwnWorklogIssue[issueKey] = true
                      $scope.cannotDeleteOwnWorklogIssue[issueKey] = true
                    }
                    if (!permissions[3]) {
                      $scope.editWorklogPopup.cannotEditAllWorklogIssue[issueKey] = true
                      $scope.cannotEditAllWorklogIssue[issueKey] = true
                    }
                    if (!permissions[4]) {
                      $scope.editWorklogPopup.cannotDeleteAllWorklogIssue[issueKey] = true
                      $scope.cannotDeleteAllWorklogIssue[issueKey] = true
                    }
                  })
                } else {
                  $scope.editWorklogPopup.cannotLogWorkIssue[issueKey] = true
                  $scope.cannotLogWorkIssue[issueKey] = true
                  $scope.editWorklogPopup.cannotEditOwnWorklogIssue[issueKey] = true
                  $scope.cannotEditOwnWorklogIssue[issueKey] = true
                  $scope.editWorklogPopup.cannotDeleteOwnWorklogIssue[issueKey] = true
                  $scope.cannotDeleteOwnWorklogIssue[issueKey] = true
                  $scope.editWorklogPopup.cannotEditAllWorklogIssue[issueKey] = true
                  $scope.cannotEditAllWorklogIssue[issueKey] = true
                  $scope.editWorklogPopup.cannotDeleteAllWorklogIssue[issueKey] = true
                  $scope.cannotDeleteAllWorklogIssue[issueKey] = true
                }
              }

              var canDoSomething = $scope.canLogWork || $scope.canEditOwnWorklog || $scope.canDeleteOwnWorklog || $scope.canEditAllWorklog || $scope.canDeleteAllWorklog
              if (canDoSomething || logWorkAbility) {
                for (var i = $scope.issuesCount.startAt; i < Math.min($scope.issuesCount.startAt + 50, issuesCount); i++) {
                  if ($scope.pivotTableType === 'Calendar') {
                    sortedRows[i].data.forEach(function (data) {
                      setPermissions(data.rowKey._issue.key, !canDoSomething && logWorkAbility)
                    })
                  } else {
                    setPermissions(sortedRows[i].rowKey.keyValue, !canDoSomething && logWorkAbility) // issueKey
                  }
                }
              }

              if (pivottableService.workLabels) {
                var labels = Object.keys(pivottableService.workLabels)
                $scope.workLabelsList =
                          $scope.workLabelsList
                            ? TimesheetUtils.uniqueArray($scope.workLabelsList.concat(labels))
                            : labels
              }
              $scope.epicLinkField = pivottableService.epicLinkField
              $scope.$broadcast('loadingReset')
              pivottableService.expireWorklogCache()
            }, function () {
              $scope.loading--
              if (!$scope.loading) {
                $scope.$broadcast('loadingReset')
                $scope.loaded = false
              }
            }, function (progress) {
              $scope.$broadcast('loadingProgress', progress)
            })
            if ($scope.pivotTableType === 'TimeEntry' &&
              !options.configOptions.inProgressIssuesJql &&
              options.startDate >= $scope.allowToLogFromDate) {
              $q.all([pivottableService.getUpdatedIssues(options), $scope.$loading]).then(values => {
                const startDate = new Date(options.startDate)
                const endDate = TimesheetUtils.addDays(options.endDate, 1)
                let timeSpentSeconds = ($scope.pivotTable.totals['2timespent'] ? $scope.pivotTable.totals['2timespent'].sum : 0)
                let started = new Date(startDate.getTime() + ((options.configOptions.workingTimeInStatus.from || 9) * 60 * 60 + timeSpentSeconds) * 1000)
                const issues = values[0].reduce((a, b) => a.concat(b)).filter(Boolean)
                let lastEntry
                let timeSpentTotal = $scope.jiraConfig.workingHoursPerDay * 60 * 60
                timeSpentTotal -= timeSpentSeconds
                if (timeSpentTotal <= 0 || !$scope.canLogWork) { // canLogWork is initialized on $loading
                  return
                }
                $scope.pivotTable.suggestedEntries = []
                $scope.pivotTable.suggestedNum = 0
                $scope.pivotTable.suggestedTotal = 0
                angular.forEach(issues, issue => {
                  const changelog = issue.changelog ? issue.changelog : {}
                  const issueWorklog = issue.worklog || issue.fields.worklog
                  let histories = changelog.histories ? changelog.histories : []
                  histories.unshift({ created: issue.fields.created, author: issue.fields.creator, items: [{ fieldtype: 'created' }] })
                  const todate = item => {
                    item.created = moment(item.created).toDate()
                    return item.author && item.author.accountId === loggedInUser.accountId &&
                      item.created >= startDate && item.created < endDate
                  }
                  histories = histories.filter(todate) // TODO: sort?
                  const issueComments = issue.fields.comment ? issue.fields.comment.comments : [] // last 20
                  const comments = issueComments.filter(todate) // TODO: sort?
                  if (!(histories.length + comments.length)) {
                    return
                  }
                  let historyComment
                  for (let i = 0; i < histories.length; i++) {
                    const items = histories[i].items
                    for (let j = 0; j < items.length; j++) {
                      if (items[j].fieldtype === 'jira' && items[j].field === 'WorklogId') {
                        var wLog = TimesheetUtils.findByProperty('id', items[j].toString, issueWorklog.worklogs)
                        if (wLog && new Date(wLog.started) >= startDate && new Date(wLog.started) < endDate) {
                          return // skip if work logged/updated
                        }
                      } else if (items[j].fieldtype === 'created') {
                        historyComment = translations['created']
                      } else if (items[j].fieldtype === 'jira' && items[j].field === 'status') {
                        historyComment = items[j].toString
                      }
                    }
                  }
                  const lastCreated = comments.length > 0 ? comments[0].created.getTime() : histories.length > 0 ? histories[0].created.getTime() : new Date.getTime()
                  timeSpentSeconds = Math.max(0, (lastCreated - started.getTime()) / 1000)
                  timeSpentSeconds = Math.round(timeSpentSeconds / 600) * 600 // round to 10 minutes
                  timeSpentSeconds = timeSpentTotal > timeSpentSeconds ? timeSpentSeconds : timeSpentTotal
                  timeSpentTotal -= timeSpentSeconds
                  const comment = comments.map(comment => comment.body).join('\n') || historyComment
                  const worklog = {
                    issueKey: issue.key,
                    started: new Date(lastCreated - timeSpentSeconds),
                    transient: true,
                    timeSpentSeconds: timeSpentSeconds,
                    comment: comment
                  }
                  lastEntry = $scope.createEntry(issue, worklog)
                  started = new Date(started.getTime() + timeSpentSeconds * 1000)
                  $scope.pivotTable.suggestedEntries.push(lastEntry)
                  $scope.pivotTable.suggestedNum++
                  $scope.pivotTable.suggestedTotal += timeSpentSeconds
                })
                if (timeSpentTotal > 0 && lastEntry) {
                  lastEntry.worklog.timeSpentSeconds += timeSpentTotal
                  lastEntry.values['2timespent'] += timeSpentTotal
                  $scope.pivotTable.suggestedTotal += timeSpentTotal
                }
              })
            }
          }
          var $statuses = ['IssueWorkedTimeByStatus', 'IssuePassedTimeByStatus'].indexOf($scope.pivotTableType) >= 0
            ? configurationService.getAllStatuses() : 0
          var $statusCategories = $scope.pivotTableType === 'IssuePassedTimeByStatus' || $scope.isGadget
            ? configurationService.getAllStatusCategories() : []
          $q.all([$paramUserInfo, $fields, $jiraConfig, $statuses, $statusCategories, $hourlyRates,
            /* wait for previous execution */ $scope.$loading]) // ACJIRA-300 // FIXME: move to pivottableService.getPivotTable, without getConfiguration
            .then(function (value) {
              var user = value[0]
              var fields = value[1]
              var statuses = value[3]
              var statusCategories = value[4]
              options.hourlyRates = value[5]
              if (filterByUser) {
                options.username = user ? user.accountId : $scope.user
              }
              options.configOptions = {}
              options.configOptions.statuses = statuses
              if (config.compositionIssueLink.val != null) {
                options.configOptions.compositionIssueLink = config.compositionIssueLink.val
              }
              if (config.parentIssueField.val != null) {
                options.configOptions.parentIssueField = config.parentIssueField.val
              }
              if (config.parentIssueType.val != null) {
                options.configOptions.parentIssueType = config.parentIssueType.val
              }
              options.configOptions.weekendType = config.weekendType.val
              options.configOptions.preserveStartedTime = config.preserveStartedTime.val
              $scope.workingTimeInStatus = options.configOptions.workingTimeInStatus = config.workingTimeInStatus.val
              $scope.isElapsedTime = $scope.pivotTableType === 'IssuePassedTimeByStatus' && !($scope.workingTimeInStatus.from && $scope.workingTimeInStatus.to)
              options.configOptions.startedTimeInStatus = config.startedTimeInStatus.val

              if (routeParams.timeInStatusCategories && !angular.isArray(routeParams.timeInStatusCategories)) {
                routeParams.timeInStatusCategories = [routeParams.timeInStatusCategories]
              }
              if (routeParams.timeTrackingColumns && !angular.isArray(routeParams.timeTrackingColumns)) {
                routeParams.timeTrackingColumns = [routeParams.timeTrackingColumns]
              }
              if (routeParams.timeBalanceColumns && !angular.isArray(routeParams.timeBalanceColumns)) {
                routeParams.timeBalanceColumns = [routeParams.timeBalanceColumns]
              }

              options.timeInStatusCategories = $scope.timeInStatusCategories = routeParams.timeInStatusCategories || config.timeInStatusCategories.val
              $scope.timeInStatusCategoriesData = new TimesheetSelectOptions(TimesheetGeneralOption, $scope.timeInStatusCategories)
              for (var i in statusCategories) {
                var statusCategory = statusCategories[i]
                $scope.timeInStatusCategoriesData.addOption(statusCategory.name, statusCategory.id.toString())
              }
              $scope.timeTrackingColumns = routeParams.timeTrackingColumns || config.timeTrackingColumns.val
              options.timeTrackingColumns = $scope.timeTrackingColumns || configurationService.getDefaultTimeTrackingColumns()
              $scope.timeTrackingColumnsData = new TimesheetSelectOptions(TimesheetGeneralOption, $scope.timeTrackingColumns)
              var timeTrackingColumns = configurationService.getAllTimeTrackingColumns()
              for (var i in timeTrackingColumns) {
                $scope.timeTrackingColumnsData.addOption(translations[timeTrackingColumns[i]], timeTrackingColumns[i])
              }
              $scope.timeBalanceColumns = routeParams.timeBalanceColumns || config.timeBalanceColumns.val
              options.timeBalanceColumns = $scope.timeBalanceColumns || configurationService.getDefaultTimeBalanceColumns()
              $scope.timeBalanceColumnsData = new TimesheetSelectOptions(TimesheetGeneralOption, $scope.timeBalanceColumns)
              var timeBalanceColumns = configurationService.getAllTimeBalanceColumns()
              for (var i in timeBalanceColumns) {
                $scope.timeBalanceColumnsData.addOption(translations[timeBalanceColumns[i]], timeBalanceColumns[i])
              }
              options.dateFields = config.dateFields.val || configurationService.getDefaultDateFields()

              options.configOptions.exportColumns = config.exportColumns.val ||
                            configurationService.getDefaultExportColumns()
              options.configOptions.durationTypeForExport = config.durationTypeForExport.val
              options.configOptions.inProgressIssuesJql = config.inProgressIssuesJql.val
              if (['TimeTracking', 'IssuePassedTimeByStatus'].indexOf(options.pivotTableType) >= 0 && ['workeduser', 'workeddate', 'workcreated'].indexOf(options.groupByField) >= 0) {
                options.groupByField = ''
              }
              if (['Timespent', 'CostReport', 'IssueWorkedTimeByUser', 'IssueWorkedTimeByStatus'].indexOf(options.pivotTableType) < 0 && ['workedweek'].indexOf(options.groupByField) >= 0) {
                options.groupByField = ''
              }
              if (restrictable) {
                $scope.auditorsRoles = options.configOptions.auditorsRoles = config.auditorsRoles && config.auditorsRoles.val
              } else {
                delete $scope.auditorsRoles
              }
              options.configOptions.restrictedGroups = config.restrictedGroups.val
              options.configOptions.workUnderLimitColor = config.workUnderLimitColor.val
              options.configOptions.workOverLimitColor = config.workOverLimitColor.val
              options.configOptions.showCategoryRow = config.showCategoryRow.val
              options.configOptions.tempoToken = config.tempoToken.val
              $scope.showTotalInFront = config.showTotalInFront.val
              options.configOptions.restrictWorkPerDay = config.restrictWorkPerDay.val
              $scope.workDescriptionRequired = config.workDescriptionRequired.val
              $scope.visibilityGroup = config.worklogVisibilityGroup.val
              $scope.visibilityRole = config.worklogVisibilityRole.val
              $scope.workLabelsList = config.workLabels.val
              $scope.currencySymbol = options.configOptions.currencySymbol = config.currencySymbol.val
              options.configOptions.showDetailsCount = $scope.showDetailsCount = config.showDetailsCount.val &&
                ['Timespent', 'IssueWorkedTimeByUser', 'IssueWorkedTimeByStatus', 'IssueWorkedTimeByLabel',
                  'IssuePassedTimeByStatus', 'IssuePassedTimeByResolution'].indexOf(options.pivotTableType) >= 0
              // TODO: field object for moreFields similarly to below
              if (options.groupByField) {
                for (var i = 0; i < fields.length; i++) {
                  if (fields[i].id === options.groupByField) {
                    options.groupByFieldObject = fields[i]
                    break
                  }
                }
              }
              if (options.categorizeByField) {
                for (var i = 0; i < fields.length; i++) {
                  if (fields[i].id === options.categorizeByField) {
                    options.categorizeByFieldObject = fields[i]
                    break
                  }
                }
              }
              if (options.groups) {
                options.username = ''
              }
              $scope.configOptions = options.configOptions
              $scope.loading-- // ready to View Report
              if ($scope.pivotTableType === 'PieChart') {
                $scope.pivotTable = null
              }
              var userInLogWorkForOthersGroups = function () {
                return config.logWorkForOthersGroups && config.logWorkForOthersGroups.val && loggedInUser.groups.items.length &&
                                loggedInUser.groups.items.filter(function (group) {
                                  return config.logWorkForOthersGroups.val.indexOf(group.name) >= 0
                                }).length > 0
              }
              var checkUserForRestrictedGroups = function () {
                return !options.configOptions.restrictedGroups || options.configOptions.restrictedGroups.filter(function (group) {
                  return TimesheetUtils.findByProperty('name', group, loggedInUser.groups.items) &&
                                    TimesheetUtils.findByProperty('name', group, $scope.paramUserInfo.groups.items)
                }).length > 0
              }
              var canLogWorkForOthers = logWorkAbility &&
                            ($scope.user && $scope.user !== loggedInUser.accountId) &&
                            !config.disableLogWork.val &&
                            ['Calendar', 'Timesheet'].indexOf($scope.pivotTableType) >= 0 &&
                            userInLogWorkForOthersGroups()
              canLogWorkForOthers = canLogWorkForOthers && checkUserForRestrictedGroups()
              delete $scope.canLogWorkForUser
              if (canLogWorkForOthers) {
                pivottableService.canLogWorkForUser($scope.user).then(can => { $scope.canLogWorkForUser = can })
              }
              $scope.allowToLogFromDate = config.allowToLogFromDate.val
              recordOptions()
              return pivottableService.clearWorklogCache(config) // promise or undefined
            })
            .then(!$scope.reportDialog && $scope.loaded ? $scope.execute : null)
        }
      })

      $fields.then(function (fields) {
        $scope.groupByOptions = new TimesheetSelectOptions(TimesheetGeneralOption, routeParams.groupByField)
        $scope.moreFieldsOptions = new TimesheetSelectOptions(TimesheetGeneralOption, TimesheetUtils.wrapIntoArray(routeParams.moreFields))
        $scope.orderByOptions = new TimesheetSelectOptions(TimesheetGeneralOption, routeParams.orderByField)
        var filteredFields = TimesheetUtils.filterFields(fields)
        for (var i = 0; i < filteredFields.length; i++) {
          var filteredField = filteredFields[i]
          $scope.groupByOptions.addOption(filteredField.name, filteredField.id)
          $scope.moreFieldsOptions.addOption(filteredField.name, filteredField.id)
          $scope.orderByOptions.addOption(filteredField.name, filteredField.id)
        }
        $scope.moreFieldsOptions.addOption(TimesheetUtils.FIELDS.PROJECT_CATEGORY_FIELD.name, TimesheetUtils.FIELDS.PROJECT_CATEGORY_FIELD.id)
        $scope.moreFieldsOptions.addOption(TimesheetUtils.FIELDS.PARENT_ISSUE_FIELD.name, TimesheetUtils.FIELDS.PARENT_ISSUE_FIELD.id)
        $scope.moreFieldsOptions.addOption(TimesheetUtils.FIELDS.TIME_TRACKING_PROGRESS_FIELD.name, TimesheetUtils.FIELDS.TIME_TRACKING_PROGRESS_FIELD.id)
        $scope.moreFieldsOptions.addOption(TimesheetUtils.FIELDS.TIME_TRACKING_ESTIMATE_FIELD.name, TimesheetUtils.FIELDS.TIME_TRACKING_ESTIMATE_FIELD.id)
        $scope.moreFieldsOptions.addOption(TimesheetUtils.FIELDS.TIME_TRACKING_VARIANCE_FIELD.name, TimesheetUtils.FIELDS.TIME_TRACKING_VARIANCE_FIELD.id)
        $scope.moreFieldsOptions.addOption(TimesheetUtils.FIELDS.TIME_TRACKING_ORIGINAL_ESTIMATE_REMAINING_FIELD.name, TimesheetUtils.FIELDS.TIME_TRACKING_ORIGINAL_ESTIMATE_REMAINING_FIELD.id)
        $scope.moreFieldsOptions.sort()
        $scope.orderByOptions.addOption(TimesheetUtils.FIELDS.TIME_TRACKING_PROGRESS_FIELD.name, TimesheetUtils.FIELDS.TIME_TRACKING_PROGRESS_FIELD.id)
        $scope.orderByOptions.addOption(TimesheetUtils.FIELDS.TIME_TRACKING_ESTIMATE_FIELD.name, TimesheetUtils.FIELDS.TIME_TRACKING_ESTIMATE_FIELD.id)
        $scope.orderByOptions.addOption(TimesheetUtils.FIELDS.TIME_TRACKING_VARIANCE_FIELD.name, TimesheetUtils.FIELDS.TIME_TRACKING_VARIANCE_FIELD.id)
        $scope.orderByOptions.addOption(TimesheetUtils.FIELDS.TIME_TRACKING_ORIGINAL_ESTIMATE_REMAINING_FIELD.name, TimesheetUtils.FIELDS.TIME_TRACKING_ORIGINAL_ESTIMATE_REMAINING_FIELD.id)
        if (['TimeTracking', 'IssuePassedTimeByStatus', 'IssuePassedTimeByResolution', 'TimeBalance'].indexOf($scope.pivotTableType) < 0) {
          $scope.groupByOptions.addOption(TimesheetUtils.FIELDS.WORKED_USER_FIELD.name, TimesheetUtils.FIELDS.WORKED_USER_FIELD.id)
          $scope.groupByOptions.addOption(TimesheetUtils.FIELDS.WORKED_USER_GROUP_FIELD.name, TimesheetUtils.FIELDS.WORKED_USER_GROUP_FIELD.id)
          $scope.groupByOptions.addOption(TimesheetUtils.FIELDS.WORKED_DATE_FIELD.name, TimesheetUtils.FIELDS.WORKED_DATE_FIELD.id)
          $scope.groupByOptions.addOption(TimesheetUtils.FIELDS.WORK_CREATED_FIELD.name, TimesheetUtils.FIELDS.WORK_CREATED_FIELD.id)
          $scope.groupByOptions.addOption(TimesheetUtils.FIELDS.COMMENT_FIRSTWORD_FIELD.name, TimesheetUtils.FIELDS.COMMENT_FIRSTWORD_FIELD.id)
          $scope.groupByOptions.addOption(TimesheetUtils.FIELDS.WORK_LABEL_FIELD.name, TimesheetUtils.FIELDS.WORK_LABEL_FIELD.id)
        }
        $scope.groupByOptions.addOption(TimesheetUtils.FIELDS.ISSUE_FIELD.name, TimesheetUtils.FIELDS.ISSUE_FIELD.id)
        $scope.groupByOptions.addOption(TimesheetUtils.FIELDS.PARENT_ISSUE_FIELD.name, TimesheetUtils.FIELDS.PARENT_ISSUE_FIELD.id)
        $scope.groupByOptions.addOption(TimesheetUtils.FIELDS.PROJECT_CATEGORY_FIELD.name, TimesheetUtils.FIELDS.PROJECT_CATEGORY_FIELD.id)
        if (['Timespent', 'CostReport', 'IssueWorkedTimeByUser', 'IssueWorkedTimeByStatus'].indexOf($scope.pivotTableType) >= 0) {
          $scope.groupByOptions.addOption(TimesheetUtils.FIELDS.WORKED_WEEK_FIELD.name, TimesheetUtils.FIELDS.WORKED_WEEK_FIELD.id)
        }
        $scope.groupByOptions.sort()
        $scope.groupByOptions.addOption(translations['No grouping'], '', true)
        $scope.categorizeByOptions = angular.copy($scope.groupByOptions)
        $scope.categorizeByOptions.selected = routeParams.categorizeByField || ''
        $scope.orderByOptions.sort()
        // for excelView
        fields.push(TimesheetUtils.FIELDS.WORKED_USER_FIELD, TimesheetUtils.FIELDS.WORKED_USER_GROUP_FIELD, TimesheetUtils.FIELDS.WORKED_DATE_FIELD,
          TimesheetUtils.FIELDS.WORK_CREATED_FIELD, TimesheetUtils.FIELDS.COMMENT_FIRSTWORD_FIELD,
          TimesheetUtils.FIELDS.PARENT_ISSUE_FIELD, TimesheetUtils.FIELDS.WORK_LABEL_FIELD,
          TimesheetUtils.FIELDS.PROJECT_CATEGORY_FIELD, TimesheetUtils.FIELDS.TIME_TRACKING_PROGRESS_FIELD,
          TimesheetUtils.FIELDS.TIME_TRACKING_ESTIMATE_FIELD, TimesheetUtils.FIELDS.TIME_TRACKING_VARIANCE_FIELD,
          TimesheetUtils.FIELDS.TIME_TRACKING_ORIGINAL_ESTIMATE_REMAINING_FIELD, TimesheetUtils.FIELDS.WORKED_WEEK_FIELD)
        $scope.allFields = fields
        if (!$scope.hasOwnProperty('epicNameField')) {
          $scope.epicNameField = null
          for (var i = 0; i < fields.length; i++) {
            var field = fields[i]
            if (field.custom && field.schema != null) {
              if (field.schema.custom === 'com.pyxis.greenhopper.jira:gh-epic-label') {
                $scope.epicNameField = field
                break
              }
            }
          }
        }
      })

      var $filtersAndProjects = $q.all([pivottableService.getFilters(), configurationService.getProjects()]).then(function (value) {
        var filters = value[0]; var projects = value[1]
        $scope.filterByOptions = new TimesheetSelectOptions(TimesheetGeneralOption, routeParams.filterOrProjectId, null, { filter: 'Filter', project: 'Project', jql: 'Predefined' })
        $scope.childrenFilterOptions = new TimesheetSelectOptions(TimesheetGeneralOption, '' + (routeParams.childrenFilterId || ''), null)
        $scope.childrenFilterOptions.addOption('No filtering', '', true)
        for (var i = 0; i < filters.length; i++) {
          $scope.filterByOptions.addOption(filters[i].name, 'filter_' + filters[i].id)
          $scope.childrenFilterOptions.addOption(filters[i].name, filters[i].id)
        }
        for (var i = 0; i < projects.length; i++) {
          $scope.filterByOptions.addOption(projects[i].name + ' (' + projects[i].key + ')', 'project_' + projects[i].key)
        }
        var predefinedFilters = ['Active sprints issues', 'My open issues', 'Reported by me',
          'Viewed recently', 'All issues', 'Open issues', 'Created recently',
          'Resolved recently', 'Updated recently', 'Done issues']
        for (var i = 0; i < predefinedFilters.length; i++) {
          if (i || $scope.epicNameField) { // no Active sprints issues for Jira Core
            $scope.filterByOptions.addOption(predefinedFilters[i], 'jql_' + i)
            $scope.childrenFilterOptions.addOption(predefinedFilters[i], 'jql_' + i)
          }
        }
      })

      $q.all([$fields, $filtersAndProjects, $paramUserInfo]).then(function () {
        var optionLabels = $scope.getSelectedOptionLabels()
        $scope.gadgetSummary = new GadgetSummary(optionLabels, $scope.paramUserInfo, $scope.pivotTableType, translations).getSummaryItems()
        $scope.selectedOptionLabels = optionLabels
      })

      $scope.pivotTableTypeOptions = new TimesheetSelectOptions(TimesheetGeneralOption, $scope.pivotTableType)
      var pivotTableTypeKeys = Object.keys(PivotTableType)
      for (var i = 0; i < pivotTableTypeKeys.length; i++) {
        if (pivotTableTypeKeys[i] === 'CostReport') continue
        $scope.pivotTableTypeOptions.addOption(translations[pivotTableTypeKeys[i]], pivotTableTypeKeys[i])
      }

      $scope.reportingDayOptions = new TimesheetSelectOptions(TimesheetGeneralOption, routeParams.reportingDay != null ? parseInt(routeParams.reportingDay) : null)
      if (routeParams.reportingDay != null) {
        $scope.reportingDayOptions.addOption('', null)
      }
      $scope.reportingDayOptions.addOption(translations['Today'], -1)
      var getUsers = userIds => userIds ? $q.all(userIds.split(',').map(pivottableService.getUserInfo.bind(pivottableService))) : $q.resolve()
      $scope.everyWeekOptions = new TimesheetSelectOptions(TimesheetGeneralOption, routeParams.everyWeekOn || $scope.reportingDay || -1)
      $scope.everyWeekOptions.addOption(translations['Daily'], -1)
      var day = moment()
      for (var i = 0; i < 7; i++) {
        var d = (moment.localeData().firstDayOfWeek() + i) % 7 // in the order of locale week
        day.day(d)
        $scope.reportingDayOptions.addOption(moment.localeData().weekdays(day), d)
        $scope.everyWeekOptions.addOption(translations['Every'] + ' ' + moment.localeData().weekdays(day), d)
      }
      var m = /^(\d\d?):(\d\d?)(am|pm)$/.exec(routeParams.everyWeekAt)
      if (m) {
        $scope.everyWeekOptions.hour = parseInt(m[1])
        $scope.everyWeekOptions.minute = parseInt(m[2])
        $scope.everyWeekOptions.period = m[3]
      } else {
        $scope.everyWeekOptions.hour = 6
        $scope.everyWeekOptions.minute = 0
        $scope.everyWeekOptions.period = 'am'
      }
      $scope.everyWeekOptions.ifIncomplete = routeParams.ifIncomplete
      getUsers(routeParams.sendToCCUsers).then(users => { $scope.everyWeekOptions.sendToCCUsers = users })
      $scope.everyWeekOptions.addCsv = routeParams.addCsv
      $scope.sumOptions = new TimesheetSelectOptions(TimesheetGeneralOption, routeParams.sum)
      $scope.sumOptions.addOption('', '')
      $scope.sumOptions.addOption(translations['Day'], 'day')
      $scope.sumOptions.addOption(translations['Week'], 'week')
      $scope.sumOptions.addOption(translations['Month'], 'month')
      $scope.viewByOptions = new TimesheetSelectOptions(TimesheetGeneralOption, routeParams.view)
      $scope.viewByOptions.addOption('', '')
      $scope.viewByOptions.addOption(translations['Day'], 'day')
      $scope.viewByOptions.addOption(translations['Week'], 'week')
      $scope.viewByOptions.addOption(translations['Month'], 'month')

      var $jiraConfig = pivottableService.getJiraConfiguration()
      $jiraConfig.then(function (jiraConfig) {
        if (jiraConfig && !jiraConfig.timeTrackingConfiguration) {
          AP.flag.create({
            title: 'Time Tracking is currently OFF',
            body: 'Please enable Time Tracking in Administration - Issues - Time Tracking',
            type: 'error'
          })
        }
        options.jiraConfig = $scope.jiraConfig = jiraConfig
        $scope.jiraConfig.maxFractionDigits = config.maxFractionDigits.val
        $scope.jiraConfig.timeFormat = config.durationType.val || (config.prettyDuration.val ? 'pretty' : 'h') // FIXME: rework directive to use addon config
        // Time Reports TimeTracking Provider
        if ($scope.createWorklog) {
          var worklog = {
            issueKey: $scope.issueKey,
            initialWorkLabels: pivottableService.workLabels,
            standaloneDialog: true
          }
          $scope.addWorklogDialog(worklog)
        }
      })

      var sumMonth = moment($scope.startDate.value).month() !== moment($scope.endDate.value).month() ||
                (TimesheetUtils.sameDay($scope.startDate.value, TimesheetUtils.getFirstDayOfTheMonth($scope.startDate.value)) &&
                   TimesheetUtils.sameDay($scope.endDate.value, TimesheetUtils.getLastDayOfTheMonth($scope.endDate.value)))

      var setSumWeek = function () {
        var setSumWeekValues = function (styleClass, title) {
          $scope.sumTitle = title
          $scope.sumClass = styleClass
        }
        if ($scope.sum === 'month' || $scope.isDayView) {
          setSumWeekValues('day', 'Show daily worked hours')
        } else if ($scope.sum === 'week') {
          if (sumMonth) {
            setSumWeekValues('month', 'Sum worked hours by month')
          } else {
            setSumWeekValues('day', 'Show daily worked hours')
          }
        } else {
          setSumWeekValues('week', 'Sum worked hours by week')
        }
      }

      $scope.weekSumChange = function () {
        routeParams.sum = $scope.sum === 'week' ? (sumMonth ? 'month' : 'day') : $scope.sum === 'month' ? 'day' : $scope.isDayView ? 'day' : 'week'
        if (!$scope.loading) {
          routeParams.loaded = $scope.loaded
        }
        saveRouteParams(routeParams)
      }

      setSumWeek()

      $scope.example = function (params) {
        angular.copy(params, routeParams)
        saveRouteParams(routeParams)
      }

      $scope.issueTimeViewChanged = function (view) {
        try {
          if (view === 'view_-1') {
            window.localStorage.removeItem('issueTimeView')
          } else {
            window.localStorage.setItem('issueTimeView', view)
          }
        } catch (err) {
          console.log('issueTimeView cache error', err)
        }
        $scope.issueTimeOptions.selected = view
        pivottableService.resetState()
        configurationService.resetState()
        if (view === 'view_-1') {
          init({})
        } else {
          $scope.viewOptions.selected = view
          $scope.viewChanged()
        }
      }

      $scope.viewChanged = function () {
        delete routeParams.timeTrackingColumns
        delete routeParams.timeBalanceColumns
        delete routeParams.timeInStatusCategories
        delete routeParams.name

        var view = $scope.viewOptions.selected
        var split = view.split('_')
        if (split[0] === 'view') { // view changed - reset routeParams
          angular.copy({ viewId: split[1] }, routeParams)
          viewService.updateCurrentView(routeParams.viewId)
          angular.extend(routeParams, $scope.currentView)
          $scope.currentViewName = $scope.currentView.name // original
          $scope.showDeleteButton = true
        } else if (routeParams.viewId) {
          // view changed to pivotTableType - reset routeParams
          angular.copy({ pivotTableType: split[1] }, routeParams)
          angular.copy({}, $scope.currentView) // clear
          $scope.showDeleteButton = false
        } else { // pivotTableType changed - keep routeParams
          routeParams.pivotTableType = split[1]
          delete routeParams.everyWeekOn
          delete routeParams.everyWeekAt
          delete routeParams.ifIncomplete
          delete routeParams.sendToCCUsers
          delete routeParams.addCsv
        }
        saveRouteParams(routeParams)
      }

      $scope.saveView = function () {
        angular.copy(routeParams, $scope.currentView)
        delete $scope.currentView._
        delete $scope.currentView.hideMenu
        var result
        $scope.showSaveButton = false
        if ($scope.currentView.viewId) {
          result = viewService.updateView($scope.currentView, loggedInUser)
          var view = $scope.viewOptions.findOption($scope.viewOptions.selected)
          if (view && view.label !== $scope.currentView.name) {
            view.label = $scope.currentView.name
            $scope.$broadcast('viewUpdated', $scope.viewOptions.selected)
          }
        } else {
          result = viewService.addView($scope.currentView, loggedInUser).then(function (view) {
            routeParams.viewId = view.viewId
            saveRouteParams(routeParams, 'noinit')
            $location.search(routeParams)
          })
        }
        return result
      }

      $scope.deleteView = function () {
        viewService.removeView($scope.currentView, loggedInUser).then(function () {
          delete routeParams.viewId
          delete routeParams.name
          delete routeParams.jobId
          delete routeParams.everyWeekOn
          delete routeParams.everyWeekAt
          delete routeParams.ifIncomplete
          delete routeParams.sendToCCUsers
          delete routeParams.addCsv
          saveRouteParams(routeParams, 'noinit')
          $location.search(routeParams)
        })
        if ($scope.currentView.jobId) {
          jobService.removeJob($scope.currentView.jobId, loggedInUser)
        }
      }

      $scope.viewNameChanged = function () {
        routeParams.name = $scope.currentView.name
        if (!$scope.loading) {
          routeParams.loaded = $scope.loaded
        }
        saveRouteParams(routeParams)
      }

      $scope.commentChanged = function (worklog) {
        if (worklog.id) {
          saveWorklog(worklog, 'noReDraw')
        } else {
          cacheTimeEntryWorklog(worklog)
        }
      }

      $scope.subscribePopup = {
        everyWeekOptions: $scope.everyWeekOptions,
        init: function () {
          this.currentView = angular.copy($scope.currentView) // to avoid blinking controls
          this.currentUser = $scope.user === loggedInUser.accountId
          this.pivotTableType = $scope.pivotTableType
          if (!this.currentUser) {
            delete this.currentView.ifIncomplete
          }
          this.loading = false
        },
        subscribe: function (closePopup) {
          this.loading = true
          routeParams.ifIncomplete = $scope.everyWeekOptions.ifIncomplete
          if ($scope.everyWeekOptions.sendToCCUsers) {
            routeParams.sendToCCUsers = $scope.everyWeekOptions.sendToCCUsers.map(user => user.accountId).join(',')
          } else {
            delete routeParams.sendToCCUsers
          }
          routeParams.addCsv = $scope.everyWeekOptions.addCsv
          routeParams.everyWeekOn = $scope.everyWeekOptions.selected
          routeParams.everyWeekAt = $scope.everyWeekOptions.hour + ':' + $scope.everyWeekOptions.minute + $scope.everyWeekOptions.period
          if (!routeParams.name) {
            routeParams.name = translations[$scope.pivotTableType] + ' ' + $scope.everyWeekOptions.findOption(routeParams.everyWeekOn).label
          }
          if (!routeParams.filterOrProjectId && $scope.projectKey && !routeParams._allProjects) {
            routeParams.filterOrProjectId = 'project_' + $scope.projectKey
          }
          var nextRun = moment(routeParams.everyWeekAt, 'h:ma')
          if (routeParams.everyWeekOn !== -1) {
            nextRun.day(routeParams.everyWeekOn)
          }
          nextRun = nextRun.toDate()
          if (nextRun < new Date()) {
            // next week/day
            nextRun.setDate(nextRun.getDate() + (routeParams.everyWeekOn === -1 ? 1 : 7))
          }
          var job = {
            jobId: routeParams.jobId, // nullable
            schedule: 'weekly',
            on: routeParams.everyWeekOn,
            at: routeParams.everyWeekAt,
            ifIncomplete: routeParams.ifIncomplete,
            sendToCCUsers: routeParams.sendToCCUsers,
            addCsv: routeParams.addCsv,
            nextRun: nextRun.getTime() // current timezone
          }
          jobService.saveJob(job, loggedInUser).then(function (newJob) {
            routeParams.jobId = newJob.jobId
            saveRouteParams(routeParams, 'noinit')
            $scope.saveView().then(closePopup)
          })
        },
        unsubscribe: function (closePopup) {
          this.loading = true
          jobService.removeJob(routeParams.jobId, loggedInUser).then(function () {
            delete routeParams.jobId
            delete routeParams.everyWeekOn
            delete routeParams.everyWeekAt
            delete routeParams.ifIncomplete
            delete routeParams.sendToCCUsers
            delete routeParams.addCsv
            $scope.saveView().then(closePopup)
          })
        },
        send: function (closePopup) {
          if (this.loading) return
          var self = this
          this.loading = true
          pivottableService.sendNow(routeParams).then(function () {
            self.loading = false
            AP.flag.create({
              title: 'Send Now',
              body: 'Report is being generated and will be emailed as soon as possible',
              type: 'success',
              close: 'auto'
            })
            closePopup()
          }, function (err) {
            self.loading = false
            AP.flag.create({
              title: 'Send Now',
              body: err.data || err || 'Something went wrong',
              type: 'error',
              close: 'auto'
            })
          })
        },
        ccChange: function (userIds) {
          if (userIds) {
            getUsers(userIds).then(users => { $scope.everyWeekOptions.sendToCCUsers = users })
          } else {
            delete $scope.everyWeekOptions.sendToCCUsers
          }
        }
      }

      $scope.shareViewPopup = {
        init: function () {
          this.loading = false
        },
        shareView: function (user, closePopup) {
          this.loading = true
          var view = angular.copy($scope.currentView)
          delete view.jobId
          delete view.everyWeekOn
          delete view.everyWeekAt
          delete view.ifIncomplete
          delete view.sendToCCUsers
          delete view.addCsv
          if (user.accountId === loggedInUser.accountId) {
            view.name += ' ' + translations['Copy']
          }
          viewService.addView(view, user).then(function () {
            if (user.accountId === loggedInUser.accountId) {
              routeParams._ = Date.now()
              $location.search(routeParams)
            } else {
              closePopup()
            }
          })
        },
        shareToUserChange: function (accountId) {
          pivottableService.getUserInfo(accountId).then(user => { $scope.shareViewPopup.shareToUser = user })
        }
      }
      var $onErrorPopup = $window.$onErrorPopup

      var recordOptions = function () {
        var optionsCopy = angular.copy(options)
        delete optionsCopy.queue
        delete optionsCopy.queueToAdd
        delete optionsCopy.matches
        if ($onErrorPopup) {
          $onErrorPopup.data('options', optionsCopy)
          $onErrorPopup.data('routeParams', routeParams)
        }
        $rootScope.customData = {
          options: optionsCopy,
          routeParams: routeParams
        }
      }

      $scope.userHoverPopup = {
        init: function (user) {
          this.user = user
        }
      }
    }
  }
])
