app.directive('auiGroupPicker', ['translationsSync', function (translations) {
  return {
    restrict: 'A',
    link: function (scope, elem, attrs) {
      $(elem).auiSelect2({
        hasAvatar: false,
        placeholder: ' ', // must not be blank // using placeholder for selected value, because $(elem).auiSelect2({...}).select2('val', '1'); works ONLY for {multiple: true} @see: http://jsfiddle.net/arunpjohny/VpnCe/3/
        multiple: true,
        ajax: {
          url: '/rest/api/2/groups/picker', // JIRA-relative URL to the REST end-point
          type: 'GET',
          dataType: 'json',
          cache: true,
          // query parameters for the remote ajax call
          data: function data (term) {
            return {
              query: term,
              maxResults: 1000,
              showAvatar: true
            }
          },
          // parse data from the server into form select2 expects
          results: function results (data) {
            data = TimesheetUtils.getJson(data)
            return {
              results: data.groups
            }
          },
          // select2 uses $.ajax as a default transport function so we have to override it
          // to use AP.request for cross-origin communication
          transport: function transport (params) {
            AP.request({
              url: params.url + '?' + $.param(params.data),
              headers: {
                Accept: 'application/json'
              },
              success: params.success,
              error: params.error
            })
          }
        },
        // specify id parameter of each group entity
        id: function id (group) {
          return group.name
        },
        // define how selected element should look like
        formatSelection: function formatSelection (group) {
          return Select2.util.escapeMarkup(group.name)
        },
        // define how single option should look like
        formatResult: function formatResult (group, container, query, escapeMarkup) {
          // format result string
          var resultText = group.name
          var higlightedMatch = []
          // we need this to disable html escaping by select2 as we are doing it on our own
          var noopEscapeMarkup = function noopEscapeMarkup (s) {
            return s
          }

          // highlight matches of the query term using matcher provided by the select2 library
          Select2.util.markMatch(escapeMarkup(resultText), escapeMarkup(query.term), higlightedMatch, noopEscapeMarkup)

          // convert array to string
          return higlightedMatch.join('')
        },
        formatNoMatches: function formatNoMatches (/* query */) {
          return translations['No matches found']
        }
      })
      var input = elem.select2('container').find('input')
      input.attr('name', input.attr('id') + Date.now())

      var update = function (newValue) {
        if (newValue != null && newValue !== '') {
          if (!angular.isArray(newValue)) {
            newValue = newValue.split(',')
          }
          var data = []
          for (var i = 0; i < newValue.length; i++) {
            data.push({ name: newValue[i], html: newValue[i] })
          }
          $(elem).auiSelect2('data', data)
        } else {
          $(elem).auiSelect2('data', null)
        }
      }
      scope.$watch(attrs.ngModel, update)
      if (attrs.updateEvent) {
        scope.$on(attrs.updateEvent, function (event, value) {
          update(value)
        })
      }

      $(elem).change(function () {
        if (attrs.ngGroupChangedFunc != null) {
          scope[attrs.ngGroupChangedFunc]($(elem).val())
        }
      })
    }
  }
}])

app.directive('auiIssuePicker', ['pivottableService', 'translationsSync', function (pivottableService, translations) {
  return {
    scope: {
      ngModel: '=',
      ngDisabled: '='
    },
    restrict: 'A',
    link: function (scope, elem) {
      $(elem).auiSelect2({
        placeholder: '', // using placeholder for selected value, because $(elem).auiSelect2({...}).select2('val', '1'); works ONLY for {multiple: true} @see: http://jsfiddle.net/arunpjohny/VpnCe/3/
        multiple: false,
        ajax: {
          url: '/rest/api/2/issue/picker',
          type: 'GET',
          dataType: 'json',
          cache: true,
          // query parameters for the remote ajax call
          data: function data (term) {
            return {
              query: term,
              currentJQL: scope.$parent.jqlQuery || '',
              showSubTasks: true
            }
          },
          // parse data from the server into form select2 expects
          results: function results (data) {
            data = TimesheetUtils.getJson(data)
            var results = []
            if (data.sections.length > 0) {
              results.push({
                select2Section: data.sections[0].label,
                children: data.sections[0].issues
              })
              if (data.sections.length > 1) {
                results.push({
                  select2Section: data.sections[1].label,
                  children: data.sections[1].issues
                })
              }
            }
            return {
              results: results
            }
          },
          // select2 uses $.ajax as a default transport function so we have to override it
          // to use AP.request for cross-origin communication
          transport: function transport (params) {
            AP.request({
              url: params.url + '?' + $.param(params.data),
              headers: {
                Accept: 'application/json'
              },
              success: params.success,
              error: params.error
            })
          }
        },
        // specify id parameter of each issue entity
        id: function id (issue) {
          return issue.key
        },
        // define how selected element should look like
        formatSelection: function formatSelection (issue) {
          return Select2.util.escapeMarkup(issue.key + ': ' + issue.summaryText)
        },
        // define how single option should look like
        formatResult: function formatResult (issue, container, query, escapeMarkup) {
          if (issue.hasOwnProperty('select2Section')) {
            return issue.select2Section
          }
          // format result string
          var resultText = issue.key + ': ' + issue.summaryText
          var higlightedMatch = []
          // we need this to disable html escaping by select2 as we are doing it on our own
          var noopEscapeMarkup = function noopEscapeMarkup (s) {
            return s
          }

          // highlight matches of the query term using matcher provided by the select2 library
          Select2.util.markMatch(escapeMarkup(resultText), escapeMarkup(query.term), higlightedMatch, noopEscapeMarkup)

          // convert array to string
          return higlightedMatch.join('')
        },
        formatNoMatches: function formatNoMatches (/* query */) {
          return translations['No matches found']
        },
        initSelection: function (element, callback) {
          // assert(element.val() == scope.ngValue.key)
          if (scope.ngModel) {
            pivottableService.findIssueByKey(scope.ngModel, { _fields: 'summary' }).then(function (issue) {
              callback({
                key: issue.key,
                summaryText: issue.fields.summary
              })
            })
          }
        }
      })
      scope.$watch('ngModel', function (issueKey) {
        if (issueKey) {
          elem.select2('val', issueKey) // trigger initSelection
        }
      })
      elem.on('focus', function () {
        elem.select2('open')
      })
    }
  }
}])

app.directive('auiMultiSelectPicker', ['translationsSync', function (translations) {
  return {
    restrict: 'A',
    link: function (scope, elem, attrs) {
      scope.$watch(attrs.ngOptionsData, function () {
        if (scope[attrs.ngOptionsData] != null) {
          $(elem).auiSelect2({
            hasAvatar: false,
            placeholder: ' ', // must not be blank
            multiple: attrs.auiMultiSelectPicker !== 'false',
            data: { results: scope[attrs.ngOptionsData].getOptions(), text: 'label' },
            // specify id parameter of each item
            id: function id (item) {
              return item.id
            },
            // define how selected element should look like
            formatSelection: function formatSelection (item) {
              var label = item.label
              if (label && label.length > 22) {
                label = label.substring(0, 22)
                return '<div title="' + item.label + '">' + Select2.util.escapeMarkup(label) + '</div>'
              } else {
                return Select2.util.escapeMarkup(label)
              }
            },
            // define how single option should look like
            formatResult: function formatResult (item, container, query, escapeMarkup) {
              // format result string
              var resultText = item.label
              var higlightedMatch = []
              // we need this to disable html escaping by select2 as we are doing it on our own
              var noopEscapeMarkup = function noopEscapeMarkup (s) {
                return s
              }

              // highlight matches of the query term using matcher provided by the select2 library
              Select2.util.markMatch(escapeMarkup(resultText), escapeMarkup(query.term), higlightedMatch, noopEscapeMarkup)

              // convert array to string
              return higlightedMatch.join('')
            },
            formatNoMatches: function formatNoMatches (/* query */) {
              return translations['No matches found']
            }
          })
          elem.on('select2-loaded', function () {
            elem.select2('dropdown')
              .find('.select2-result-label')
              .each(function (i, div) {
                var $div = $(div)
                var label = $div.text()
                if (label && label.length > 25) {
                  $div.attr('title', label)
                }
              })
          })
          var input = elem.select2('container').find('input')
          input.attr('name', input.attr('id') + Date.now())
        }
      })

      var update = function (newValue) {
        if (newValue != null) {
          if (!angular.isArray(newValue)) {
            newValue = newValue.split(',')
          }
          var data = []
          if (scope[attrs.ngOptionsData] != null) {
            for (var i = 0; i < newValue.length; i++) {
              var item = scope[attrs.ngOptionsData].findOption(newValue[i])
              if (item != null) {
                data.push({
                  id: item.id,
                  label: item.label
                })
              }
            }
          }
          // multi or single
          $(elem).auiSelect2('data', attrs.auiMultiSelectPicker !== 'false' || !data.length ? data : data[0])
        } else {
          $(elem).auiSelect2('data', null)
        }
      }
      scope.$watch(attrs.ngModel, update)
      if (attrs.updateEvent) {
        scope.$on(attrs.updateEvent, function (event, value) {
          update(value)
        })
      }

      $(elem).change(function () {
        if (attrs.ngValueChangedFunc != null) {
          scope[attrs.ngValueChangedFunc]($(elem).val())
        }
      })
    }
  }
}])

app.directive('pieChart', ['pivottableService', '$interpolate', '$compile', function (pivottableService, $interpolate, $compile) {
  return {
    restrict: 'A',
    link: function (scope, element, attrs) {
      var chart = null
      var baseUrl = scope.$parent.hostBaseUrl

      var buildLinkObject = function (label, issue) {
        if (issue == null) {
          return { label: label }
        } else {
          var resolution = issue.fields.resolution
          if (resolution != null) {
            resolution = { name: resolution.name, description: resolution.description }
          }
          return {
            label: issue.key,
            url: baseUrl + '/browse/' + issue.key,
            hint: issue.fields.summary,
            resolution: resolution
          }
        }
      }

      var buildParentLinkObject = function (issue) {
        if (issue != null) {
          var parentIssueKey = pivottableService.getParentIssueKey(issue, { configOptions: scope.configOptions })
          if (parentIssueKey != null) {
            return {
              label: parentIssueKey,
              url: baseUrl + '/browse/' + parentIssueKey,
              hint: '',
              resolution: null // TODO get hint and resolution for parent issue, try to reuse #buildLinkObject(issue)
            }
          }
        }
        return null
      }

      var convertDataToPie = function (pivotTable) {
        var result = null
        if (pivotTable != null) {
          result = []
          var rowKeys = Object.keys(pivotTable.rows)
          for (var i in rowKeys) {
            var rowKey = rowKeys[i]
            var row = pivotTable.rows[rowKey]
            var icons = []
            var issue = row.rowKey.issue
            if (issue != null) {
              var priority = issue.fields.priority
              var issueType = issue.fields.issuetype
              icons = []
              if (priority) {
                icons.push({ url: priority.iconUrl, title: priority.name })
              }
              if (issueType) {
                icons.push({ url: issueType.iconUrl, title: issueType.name + ' - ' + issueType.description })
              }
            }
            var label = DrawHelper.buildLabel(rowKey, row)
            result.push({
              label: label,
              data: row.sum,
              link: buildLinkObject(label, issue),
              icons: icons,
              masterLink: buildParentLinkObject(issue)
            })
          }
        }
        return result
      }

      var convertDataToBar = function (pivotTable) {
        var result = null
        if (pivotTable != null) {
          result = []
          var groups = {}
          for (var rowKey in pivotTable.rows) {
            var row = pivotTable.rows[rowKey]
            var group = row.rowKey._field
            if (!groups[group]) {
              groups[group] = 1
            }
          }
          var groupKeys = Object.keys(groups)
          for (var rowKey in pivotTable.rows) {
            var row = pivotTable.rows[rowKey]
            var data = []
            for (var i in groupKeys) {
              data.push([groupKeys[i], row.rowKey._field === groupKeys[i] ? row.sum : 0])
            }
            var label = row.rowKey._project
            result.push({
              label: label,
              data: data,
              link: buildLinkObject(label)
            })
          }
        }
        return result
      }

      var formatConfig

      var sliderHtml = '<span><pivottable-slider class="chart" /></span>'

      var wrapLegend = function () {
        var $legend = $(element).find('.legend')
        $legend.find('table').css('font-size', '')
        $legend
          .find('table tr:first')
          .first()
          .before(
            '<tr>' +
                        '<th colspan="2">' +
                            '<span id="sliderPlaceholder"></span>' +
                        '</th>' +
                    '</tr>')
        $legend
          .find('#sliderPlaceholder')
          .before($compile(sliderHtml)(scope))
        $legend
          .find('table tbody')
          .first()
          .append($compile(
            '<tr>' +
                        '<td colspan="2">' +
                            "<span>{{'Total' | i18n}}</span>" + ' <b>' + DrawHelper.getFormattedTime(scope[attrs.ngModel].sum, formatConfig) + '</b>' +
                            '&nbsp;<a ng-click="loading || !loaded || refresh()" ng-disabled="loading || !loaded" ng-class="{disabled: loading || !loaded}" title="{{\'Refresh\' | i18n}}">' +
                            '<span class="aui-icon aui-icon-small aui-iconfont-refresh-small">{{\'Refresh\' | i18n}}</span>' +
                            '</a>' +
                        '</td>' +
                    '</tr>')(scope))
      }

      scope.$watch(attrs.ngModel, function (pivotTable) {
        chart = null
        if (pivotTable && scope.loaded && !scope.loading) {
          formatConfig = scope.jiraConfig || {}
          var stackChart = !!(scope.categorizeByOptions.selected && scope.groupByOptions.selected)
          var data = stackChart ? convertDataToBar(pivotTable) : convertDataToPie(pivotTable)
          if (data != null && data.length > 0) {
            if (chart == null) {
              chart = $.plot(element, data, {
                series: {
                  stack: true,
                  bars: {
                    show: stackChart,
                    barWidth: 0.75,
                    align: 'center'
                  },
                  pie: {
                    show: !stackChart,
                    radius: 0.9,
                    label: {
                      show: true,
                      radius: 0.8,
                      formatter: function (label, series) {
                        return DrawHelper.composeTooltip(series.label, series, data, scope.isGadget)
                      },
                      background: { opacity: 0.8 },
                      threshold: 0.05
                    },
                    offset: {
                      // left: -Math.round(height / 2)
                    }
                  }
                },
                grid: {
                  color: '#000',
                  borderWidth: 0,
                  hoverable: true
                },
                tooltip: {
                  show: true,
                  content: function (label, x, y, item) {
                    return label + ' (' + DrawHelper.getFormattedTime(y, formatConfig) + ') ' + (!stackChart && item.series.percent < 5 ? ' ' + Math.round(item.series.percent) + '%' : '')
                  }
                },
                xaxis: {
                  mode: 'categories'
                },
                yaxis: {
                  tickFormatter: function (val/*, axis */) {
                    return DrawHelper.getFormattedTime(val, formatConfig)
                  },
                  minTickSize: 3600
                },
                legend: {
                  show: true,
                  margin: [-270, 23],
                  labelFormatter: function (label, series) {
                    return DrawHelper.composeLegendTooltip(label, series, data, formatConfig, scope.isGadget)
                  },
                  backgroundOpacity: 0
                }
              })
              // element.show();
            } else {
              chart.setData(data)
              chart.setupGrid()
              chart.draw()
            }
            wrapLegend()
            $(element).resize(wrapLegend)
          } else if (scope.startDate != null ||
                        scope.endDate != null) { // if start/end date specified - show slider
            var noDataHtml =
                            '<div style="float:left; margin-top:5px; margin-right:150px">' +
                                '<b>NO DATA</b>' +
                            '</div>' +
                            '<div class="legend">' +
                                '<table style="color: rgb(0, 0, 0); width: 215px">' +
                                    '<tr>' +
                                        '<th colspan="2">' +
                                            sliderHtml +
                                        '</th>' +
                                    '</tr>' +
                                '</table>' +
                            '</div>'
            element.html($compile(noDataHtml)(scope))
          }
        }
      })
    }
  }
}])

app.directive('auiInlineDialog', ['$compile', '$timeout', function ($compile, $timeout) {
  return {
    restrict: 'E',
    scope: {
      template: '=',
      container: '=',
      popupData: '=',
      workDescriptionRequired: '=',
      workLabelsList: '=',
      jiraConfig: '=',
      onHover: '=',
      persistent: '=',
      initFn: '&'
    },
    link: function (scope) {
      scope.hostBaseUrl = scope.$parent.hostBaseUrl
      var dialog = AJS.InlineDialog(scope.container, scope.$id, function (content, trigger, showPopup) {
        var includeString = '<ng-include src="\'' + scope.template + '\'"></ng-include>'
        $timeout(function () {
          content.css('padding', '5px').html($compile(includeString)(scope))
        }, 0)
        showPopup()
        return false
      }, {
        useLiveEvents: true,
        onHover: scope.onHover,
        persistent: scope.persistent,
        noBind: !scope.container,
        initCallback: function (/* popup */) {
          this.popup.css('right', 'auto') // issue#945
          this.popup.refresh()
          $('.contents', this.popup).off('mouseleave')
          if (scope.onHover) {
            $('.contents', this.popup).on('mouseleave', function () {
              dialog.hide()
            })
          }
        },
        preHideCallback: function () {
          return !$('ul.is-autocomplete').size()
        }
      })

      scope.closePopup = function () {
        dialog.hide()
      }
      // http://weblogs.asp.net/dwahlin/creating-custom-angularjs-directives-part-3-isolate-scope-and-function-parameters
      // Referencing DOM nodes in Angular expressions is disallowed
      scope.initFn({ params: { dialog: dialog, scope: scope } })
    }
  }
}])

app.directive('modalLikeDialog', ['$controller', '$compile', '$document', 'loggedInUser',
  function ($controller, $compile, $document, loggedInUser) {
    return {
      restrict: 'E',
      scope: {
        controller: '=',
        data: '=',
        modalInstance: '='
      },
      link: function (scope, element) {
        var html = '<ng-include src="data.dialogTemplate"></ng-include>'
        var ctrlLocals = {
          $scope: scope,
          issueKey: scope.data.issueKey,
          worklog: scope.data.worklog,
          jiraConfig: scope.data.jiraConfig,
          pivotTableType: scope.data.pivotTableType,
          $modalInstance: {
            dismiss: scope.data.dismiss,
            close: scope.data.close
          }
        }
        loggedInUser.then(function (loggedInUser) {
          ctrlLocals.loggedInUser = loggedInUser
          $controller(scope.controller, ctrlLocals)
          var value = $compile(html)(scope)
          $(element).html(value)
          var dismiss = function (evt) {
            if (evt.which === 27) {
              scope.data.dismiss()
              $document.unbind('keydown', dismiss)
            }
          }
          $document.bind('keydown', dismiss)
        })
      }
    }
  }
])

app.directive('enterPressed', [function () {
  return {
    require: '^form',
    link: function (scope, element, attrs, ctrl) {
      element.bind('keydown keypress', function (event) {
        if (event.which === 13 && ctrl.$valid && !ctrl.$pending && !$('ul.is-autocomplete').size()) {
          scope.$eval(attrs.enterPressed)
          event.preventDefault()
        }
      })
    }
  }
}])

app.directive('validationErrors', [function () {
  return {
    restrict: 'E',
    scope: {
      errors: '=',
      errorMessages: '='
    },
    replace: true,
    template: '<ul class="errors"><li ng-repeat="error in errors" i18n="error" i18n-parameters="{placeholders:errorMessages[+$index]}"></li></ul>'
  }
}])

app.directive('jql', ['$q', function ($q) {
  return {
    require: 'ngModel',
    link: function (scope, elm, attrs, ctrl) {
      ctrl.$asyncValidators.jql = function (modelValue/*, viewValue */) {
        var deferred = $q.defer()

        if (ctrl.$isEmpty(modelValue)) {
          deferred.resolve(true)
        } else {
          AP.request({
            type: 'POST',
            contentType: 'application/json',
            url: '/rest/api/2/jql/parse',
            headers: {
              Accept: 'application/json'
            },
            data: JSON.stringify({ queries: [modelValue] }),
            success: function (data) {
              data = TimesheetUtils.getJson(data)
              if (data.queries[0].errors) {
                ctrl.$error.jqlError = data.queries[0].errors.join('\n')
                deferred.reject()
              } else {
                delete ctrl.$error.jqlError
                deferred.resolve(true)
              }
            },
            error: function (jqXHR, type, error) {
              ctrl.$error.jqlError = 'Unexpected Error'
              deferred.reject()
            }
          })
        }

        return deferred.promise
      }
    }
  }
}])

app.directive('workLabels', ['$timeout', function ($timeout) {
  return {
    restrict: 'A',
    link: function (scope, element, attrs) {
      var $body = $(document.body)
      var height
      var $completer = $(element).textcomplete([
        {
          match: /#(\w*)$/,
          search: function (term, callback) {
            var labels = scope[attrs.workLabels] || []
            callback($.map(labels, function (element) {
              return element.indexOf(term) === 0 ? DrawHelper.escapeHtml(element) : null
            }))
          },
          index: 1,
          replace: function (value) {
            return ' #' + value + ' '
          }
        }
      ], {
        className: 'is-autocomplete', // DEPRECATED, addAttr manually if updating textcomplete.js
        zIndex: '10000',
        maxCount: 20
      }).live('textComplete:show', function () {
        if ($body.hasClass('is-gadget')) {
          $timeout(() => {
            height = $body.height()
            var $dropdown = $completer.data('textComplete').dropdown.$el
            if (height < $dropdown.height()) {
              $body.height($dropdown.offset({ top: 0 }).height())
            }
          }, 100)
        }
      }).live('textComplete:hide', function () {
        if ($body.hasClass('is-gadget')) {
          $timeout(() => {
            $body.height(height)
          }, 100)
        }
      })
    }
  }
}])

app.directive('loading', [function () {
  return {
    restrict: 'E',
    link: function (scope, element) {
      var html = '<div id="loading" class="aui-progress-indicator">' +
                '<span class="aui-progress-indicator-value"></span>' +
                '</div>'
      $(element).html(html)
      scope.$on('loadingProgress', function (event, progress) {
        if ($('#loading').length) {
          AJS.progressBars.update('#loading', progress)
        }
      })
      scope.$on('loadingReset', function () {
        if ($('#loading').length) {
          AJS.progressBars.setIndeterminate('#loading')
        }
      })
    }
  }
}])
