'use strict'
var directives = angular.module('directives', ['talis.services.logging'])

directives.directive('context', ['applicationLoggingService', function (logger) {
  return {
    restrict: 'E',
    scope: {
      items: '='
    },
    link: function (scope) {
      logger.trace('context', scope.items)
      var context = TimesheetUtils.getJson(scope.items)
      logger.baseUrl = context.hostBaseUrl
      angular.extend(scope.$parent, context)
    }
  }
}])

directives.directive('autoFocus', ['$timeout', function ($timeout) {
  return function (scope, element) {
    $timeout(function () {
      element.focus().select()
    }, 0)
  }
}])

directives.directive('formInvalid', [function () {
  return {
    restrict: 'E',
    require: '^form',
    link: function (scope, element, attrs, form) {
      var submitButton = AP.dialog.getButton('submit')
      scope.$watch(form.$name + '.$invalid', function (invalid) {
        if (invalid) {
          submitButton.disable()
        } else {
          submitButton.enable()
        }
      })
      AP.events.on('dialog.submit', function () {
        submitButton.disable()
      })
    }
  }
}])

directives.directive('logWorkPermission', ['pivottableService', function (pivottableService) {
  return {
    restrict: 'A',
    require: 'ngModel',
    link: function (scope, element, attrs, controller) {
      scope.$watch('worklog.issueKey', function (newValue) {
        if (newValue && !scope.worklog.user) {
          pivottableService.havePermissions(['WORK_ON_ISSUES'], 'issueKey=' + newValue).then(function (permissions) {
            controller.$setValidity(element[0].name, permissions[0])
          })
        }
      })
    }
  }
}])

// validate timepsent string, e.g. 1w 2d 3h 4m, or 1 (default time unit)
// see also timesheetUtils.getTimeSpentMs()
directives.directive('timespent', [function () {
  return {
    require: 'ngModel',
    scope: {
      zeroable: '='
    },
    link: function (scope, elm, attrs, ctrl) {
      ctrl.$validators.timespent = function (modelValue, viewValue) {
        if (!viewValue || /^\d*\.?\d+$/.test(viewValue)) {
          return !viewValue || parseFloat(viewValue) > 0
        }
        var blocks = TimesheetUtils.convertTimeSpent(viewValue).split(' ')
        var units = ['w', 'd', 'h', 'm']
        var result = blocks.length > 0
        for (var i = 0; i < blocks.length && result && units.length > 0; i++) {
          do {
            var unit = units.shift()
            var r = RegExp('^(\\d*\\.?\\d+)' + unit + '$')
            var m = blocks[i].match(r)
            result = m && (parseFloat(m[1]) > 0 || (scope.zeroable && parseFloat(m[1]) === 0))
          } while (units.length > 0 && !result)
        }
        return result
      }
    }
  }
}])

directives.directive('workPerDay', ['$q', 'pivottableService', function ($q, pivottableService) {
  return {
    require: 'ngModel',
    /* FIXME: https://docs.angularjs.org/error/$compile/multidir with timespent
    scope: {
      workPerDay: '='
    },
    */
    link: function (scope, elm, attrs, ctrl) {
      ctrl.$validators.workPerDay = function (/* modelValue, viewValue */) {
        delete ctrl.$error.workPerDayError
        return true
      }
      ctrl.$asyncValidators.workPerDay = function (modelValue/*, viewValue */) {
        const params = scope.$eval(attrs.workPerDay)
        if (params.configOptions && params.configOptions.restrictWorkPerDay) {
          let startDate = params.started
          let timeSpentsPerDay = {}
          const endDate = TimesheetUtils.addDays(startDate, 1)
          if (scope.$parent.worklog && scope.$parent.worklog.id) {
            timeSpentsPerDay[moment(startDate).startOf('day').toDate()] =
              TimesheetUtils.getTimeSpentMs(modelValue, scope.$parent.jiraConfig) / 1000 - scope.$parent.worklogTimeSpentSeconds
          } else {
            timeSpentsPerDay = TimesheetUtils.mapLogWorkToStarted(startDate, modelValue, scope.$parent.jiraConfig, params.configOptions)
            startDate = TimesheetUtils.getFirstLogWorkStarted(startDate, modelValue, scope.$parent.jiraConfig, params.configOptions)
          }
          const timespentSecondsAllowed = scope.$parent.jiraConfig.workingHoursPerDay * 3600
          const options = {
            startDate: startDate,
            endDate: endDate,
            username: params.user.accountId,
            configOptions: params.configOptions
          }
          return pivottableService.getWorkedIssues(options).then(data => {
            const issues = data.reduce((a, b) => a.concat(b))
            angular.forEach(issues, issue => {
              issue.fields.worklog.worklogs.forEach(worklog => {
                const started = moment(worklog.started).toDate()
                const startedStartOfDay = moment(started).startOf('day').toDate()
                if (worklog.author && worklog.author.accountId === params.user.accountId &&
                  started >= startDate && started < endDate) {
                  timeSpentsPerDay[startedStartOfDay] = (timeSpentsPerDay[startedStartOfDay] || 0) + worklog.timeSpentSeconds
                }
              })
            })
            for (var spent in timeSpentsPerDay) {
              if (timeSpentsPerDay[spent] > timespentSecondsAllowed) {
                ctrl.$error.workPerDayError = '>1d!'
                throw new Error() // TODO: unless configured to warn only? (in future?)
              }
            }
          }).catch(e => {
            if (!ctrl.$error.workPerDayError) {
              ctrl.$error.workPerDayError = 'Error'
              console.log(e)
            }
            throw e
          })
        } else {
          return $q.resolve(true)
        }
      }

      scope.$watch('worklogDatetime.date', ctrl.$validate.bind(ctrl), true)
    }
  }
}])

directives.directive('longerOneDayWarning', [function () {
  return {
    restrict: 'E',
    scope: true,
    controller: ['$scope', function ($scope) {
      $scope.warning = {
        show: false
      }
      $scope.$watch(($scope.popupData ? 'popupData.' : '') + 'worklog.timeSpent', function (timespent) {
        $scope.warning.show = TimesheetUtils.isLongerOneDay(timespent, $scope.jiraConfig)
      })
    }],
    transclude: true,
    template: '<span class="yellow" data-ng-show="warning.show" ng-transclude></span>'
  }
}])

directives.directive('lockPeriodError', [function () {
  return {
    restrict: 'E',
    scope: true,
    controller: ['$scope', function ($scope) {
      $scope.lockPeriodError = {
        show: false
      }
      function showError () {
        var err = TimesheetUtils.getFirstLogWorkStarted(moment($scope.worklogDatetime.date).toDate(), $scope.worklog.timeSpent, $scope.jiraConfig, $scope.configOptions) < $scope.allowToLogFromDate
        $scope.lockPeriodError.show = err
        $scope.logWorkForm['log-work-date-logged'].$setValidity('allowToLogFromDate', !err)
      }
      $scope.$watch('worklog.timeSpent', showError)
      $scope.$watch('worklogDatetime.date', showError)
    }],
    transclude: true,
    template: '<span class="red" title="{{\'earlier than Log Work Retrospectively\' | i18n}}" data-ng-show="lockPeriodError.show" ng-transclude></span>'
  }
}])

directives.directive('error', [function () {
  return {
    restrict: 'E',
    link: function (scope, element) {
      $(element).click(function () {
        scope.$apply(function () {
          throw new Error('Test')
        })
      })
    }
  }
}])
